<template>
  <div class="tests-element">
    <p class="number">{{ index + 1 }} вопрос</p>
    <p class="title">{{ question.type === 'conformity' ? 'Сопоставление' : question.body }}</p>
    <p class="text" v-if="question.type==='open'||question.type==='multi-open'">{{ openAnswer() }}</p>
    <div class="checkbox-wrapper" v-if="question.type==='multi-open'">
      <a-checkbox
          :checked="item.body===openAnswer()"
          class="checkbox" style="pointer-events: none" v-for="item in question.answers" :key="item.id">{{ item.body }}
        <a-typography-text :type="item.body===openAnswer()?'success':''" class="status">
          <check-circle-outlined/>
          Правильный ответ
        </a-typography-text>
      </a-checkbox>
    </div>
    <div class="checkbox-wrapper" v-if="question.type==='single'||question.type==='multi'">
      <a-checkbox
          :checked="checkAnswer(item.id)"
          class="checkbox" style="pointer-events: none" v-for="item in question.answers" :key="item.id">{{ item.body }}
        <a-typography-text :type="item.correct?'success':'danger'" class="status">
          <check-circle-outlined v-if="item.correct"/>
          <close-circle-outlined v-else/>
          {{ item.correct ? 'Правильный ответ' : 'Неправильный ответ' }}
        </a-typography-text>
      </a-checkbox>
    </div>
    <MatchingAnswer style="margin-top: 20px" v-if="question.type==='conformity'" :answers="question.answers"
                    :question-id="question.id"
                    :passing-test="passingTest"
                    :rater="true"></MatchingAnswer>
    <SeparateTestEstimationSelects v-if="false" :question="question" :passing-test="passingTest">
    </SeparateTestEstimationSelects>
    <div class="evaluation" v-if="score==='null'">
      <p class="evaluation__text text">Ваша оценка</p>
      <a-input-number
          :value="testingScore.map(item=>item.questionId).indexOf(question.id)!==-1?testingScore [testingScore.map(item=>item.questionId).indexOf(question.id)].estimate:null"
          class="evaluation__input" :class="{'blocked':score!=='null'}" :max="question.maxScore?question.maxScore:1"
          :min="0"
          @change="val=>$store.commit('setScore', {questionId:question.id,estimate:val})" placeholder="Оцените анкету"/>
      <p class="evaluation__max"><span>Максимальный балл:</span> {{ question.maxScore ? question.maxScore : 1 }}</p>
    </div>
  </div>
</template>

<script>
import {CheckCircleOutlined, CloseCircleOutlined} from "@ant-design/icons-vue";
import SeparateTestEstimationSelects from "@/components/testing/SeparateTestEstimationSelects";
import MatchingAnswer from "@/components/events/testing/MatchingAnswer";

export default {
  name: "SeparateTestEstimation",
  components: {
    MatchingAnswer,
    CheckCircleOutlined, CloseCircleOutlined, SeparateTestEstimationSelects

  },
  props: ['question', 'index', 'passingTest', 'score', 'testingScore'],
  methods: {
    checkAnswer(id) {
      if (!this.passingTest) {
        return false;
      }
      let qIndex = this.passingTest.testingPassing.answers.map(item => item.questionId).indexOf(this.question.id);
      if (qIndex === -1) {
        return false;
      }
      return this.passingTest.testingPassing.answers[qIndex].answers.includes(id);
    },
    openAnswer() {
      if (!this.passingTest) {
        return false;
      }
      let qIndex = this.passingTest.testingPassing.answers.map(item => item.questionId).indexOf(this.question.id);
      if (qIndex === -1) {
        return false;
      }
      return this.passingTest.testingPassing.answers[qIndex].openAnswer
    }
  },
}
</script>

