import {
    approveInterview,
    fetchAllEvents,
    fetchCurrentEvents,
    fetchEventPassing,
    fetchEventsHistory,
    fetchEventsTesting,
    fetchRaterEvents,
    fetchTimeByInterviewDate,
    fetchUserEvents,
    fetchUsersByStageId,
    saveBookedTime,
    savePassingBlank,
    savePassingTesting,
    fetchCurrentPassingBlank,
    savePassingForCVS, fetchPassingForCVS, fetchEventPassValid
} from "@/store/events/serverInterationMethods";
import {errorHandler} from "@/lib/errorHandler";
import {fetchCurrentBlank} from "@/store/testing/serverInterationMethods";
import router from "@/router";
import dayjs from "dayjs";

export const events = {
    actions: {
        async fetchCurrentBlank({getters, commit}, {id, edit}) {
            const {data} = await fetchCurrentBlank(getters.token, id);
            commit('setCurrentBlank', {data, testingId: id, edit});
        },
        async fetchEventsHistory({commit, getters}) {
            const {data} = await fetchEventsHistory(getters.token);
            commit('parseEventsHistory', data);
        },
        async fetchUserEvents({getters, commit}, {name, status}) {
            const {data} = await fetchUserEvents(getters.token, name, status);
            commit('setUserEvents', data);
            return data;
        },
        async fetchAllEvents({getters, commit}, {name, type}) {
            const {data} = await fetchAllEvents(getters.token, name, type);
            commit('setAllEvents', data);
        },
        async fetchCurrentEvents({getters, commit}, id) {
            let data;
            if (id === 'new') {
                data = {
                    title: null,
                    description: null,
                    previewUrl: null,
                    backgroundColor: null,
                    startDate: new Date(),
                    endDate: null,
                    stages: []
                }
            } else {
                let response = await fetchCurrentEvents(getters.token, id);
                data = response.data;
            }
            commit('setCurrentEvents', data);
            return data;
        },
        // async fetchEventsInterview({getters, commit}, id) {
        //     const {data} = await fetchEventsInterview(getters.token, id);
        //     commit('setCurrentStage', data);
        // },

        async fetchEventPassing({getters, commit}, passingId) {
            const {data} = await fetchEventPassing(getters.token, passingId);
            commit('setEventPassing', data);
        },
        async fetchEventsTesting({getters, commit}, {testingId, eventId, role, passingId, isPassTesting}) {
            await this.dispatch('fetchCurrentEvents', eventId);
            const {data} = await fetchEventsTesting(getters.token, testingId);

            if (new Date() > new Date(data.endDate)) {
                errorHandler({
                    name: "appointment",
                    status: 300,
                    meta: {
                        body: "Этап закончился",
                    },
                });
                router.push({name: "separateEvent", params: {eventId}});
                return;
            }
            if (new Date(data.startDate) > new Date()) {
                errorHandler({
                    name: "appointment",
                    status: 300,
                    meta: {
                        body: "Этап не начался",
                    },
                });
                router.push({name: "separateEvent", params: {eventId}});
                return;
            }


            const eventRes = await fetchCurrentEvents(getters.token, eventId);


            if (role !== 'rater' && (passingId !== 'null' || eventRes?.data?.eventPassings[0]?.id)) {
                const eventPassValid = await fetchEventPassValid(getters.token, {
                    eventPassingId: (passingId !== 'null' ? passingId : eventRes.data.eventPassings[0]?.id)
                })
                if (!eventPassValid?.data) {
                    router.push({name: "separateEvent", params: {eventId}});
                    return false;
                }
            }


            const passingRes = await fetchEventPassing(getters.token, passingId !== 'null' ? passingId : eventRes.data.eventPassings.length ? eventRes.data.eventPassings[0].id : null);
            if (isPassTesting && role !== 'rater') {
                const index = passingRes?.data?.testingPassing?.map(item => item.testingId).indexOf(testingId)
                if (index !== -1 && index != null) {
                    if (passingRes.data.testingPassing[index].testing.type !== 'form') {
                        router.push({name: "separateEvent", params: {eventId}});

                        return false;
                    } else {
                        if (passingRes.data.testingPassing[index].finalScore != null) {
                            router.push({name: "separateEvent", params: {eventId}});
                            return false;
                        }
                    }
                }
            }
            const stageIndex = getters.currentEvents.stages.map(item => item.testId).indexOf(testingId);
            if (stageIndex !== -1) {
                const stageId = getters.currentEvents.stages[stageIndex].id;
                commit('setStageId', stageId);
                if (passingId !== 'null' ? passingId : eventRes.data.eventPassings.length ? eventRes.data.eventPassings[0].id : null) {
                    const {data} = await fetchPassingForCVS(getters.token, stageId, passingId);
                    commit('setPassingForCVS', data.length ? data[0] : null);
                }
            }
            await commit('setCurrentStage', {
                stage: data,
                event: eventRes.data,
                passing: passingRes.data,
                role,
            });
        },
        async fetchCurrentPassingBlank({getters, commit}, id) {
            const {data} = await fetchCurrentPassingBlank(getters.token, id);
            commit('setCurrentPassingBlank', data);

        },
        async fetchTimeByInterviewDate({getters}) {
            const {data} = await fetchTimeByInterviewDate(getters.token, getters.currentStage.id);
            return data;
        },
        async saveBookedTime({getters}, whenDate) {
            const {status} = await saveBookedTime(getters.token, {
                eventPassingId: getters.currentEvents.id,
                whenDate,
                stageId: getters.currentStage.id
            })
            errorHandler({name: 'appointment', status})
            return status;
        },
        async savePassingTesting({getters, commit}, {role, finish, eventId}) {
            await commit('checkPassingAnswers', role);
            const {status, data} = await savePassingTesting(getters.token, getters.passingTest);
            if (role === 'rater' && getters.passingBlank) {
                await this.dispatch('savePassingBlank');
            }
            await commit('checkPassingForCVS', {stageId: getters.stageId, eventPassingId: data, role})
            await savePassingForCVS(getters.token, getters.passingStageForCVSData);
            commit('setPassingForCVS', null);

            errorHandler({name: 'passing', status, meta: {finish, eventId}});
        },
        async savePassingBlank({getters}) {
            await savePassingBlank(getters.token, getters.passingBlank);
        },
        async fetchRaterEvents({getters, commit}) {
            const {data} = await fetchRaterEvents(getters.token);
            commit('setAllEvents', data);
        },
        async approveInterview({getters}, data) {
            const {status} = await approveInterview(getters.token, data);
            errorHandler({name: 'appointment', status});
            return status;
        },
        async fetchUsersByStageId({getters}, {stageId, name, city, sex}) {
            const {data} = await fetchUsersByStageId(getters.token, stageId, name ? name : '', city ? city : '', sex ? sex : '');
            return data;
        },

        async createInterviewPassing({getters}) {
            await savePassingTesting(getters.token, {
                currentStageNumber: getters.eventPassing ? getters.eventPassing.currentStageNumber + 1 : 1,
                eventId: getters.currentEvents.id
            })
        },


    },
    mutations: {
        setPassingForCVS(state, data) {
            state.passingStageForCVSData = data;
            if (data) {
                state.passingStageForCVSData.body = JSON.parse(state.passingStageForCVSData.body);
                state.passingStageForCVSData.eventId = state.currentEvents.id;
            }
        },
        setStageId(state, data) {
            state.stageId = data;
        },
        setPassingId(state, data) {
            state.passingId = data;
        },
        clearEventsList(state) {
            state.allEvents = null;
        },
        setUserEvents(state, data) {
            state.userEvents = data;
        },
        setAllEvents(state, data) {
            state.allEvents = data;
        },
        addUserEvents(state, data) {
            state.userEvents = state.userEvents.concat(data);
        },
        addAllEvents(state, data) {
            state.allEvents = state.allEvents.concat(data);
        },
        setCurrentEvents(state, data) {
            state.currentEvents = data;

        },
        setEventPassing(state, data) {
            state.eventPassing = data;
            if (state.eventPassing) {
                for (let i = 0; i < state.currentEvents.stages.length; i++) {
                    if (state.currentEvents.stages.type !== 'interview') {
                        state.currentEvents.stages[i]['maxScore'] = 0;
                        let index = state.eventPassing.testingPassing.map((pass) => pass.testingId).indexOf(state.currentEvents.stages[i].testId)
                        if (index !== -1) {
                            for (let j = 0; j < state.eventPassing.testingPassing[index]?.testing?.questions.length; j++) {
                                state.currentEvents.stages[i].maxScore += (state.eventPassing.testingPassing[index].testing.questions[j].maxScore ? state.eventPassing.testingPassing[index].testing.questions[j].maxScore : 1);
                            }
                        }
                    }
                }
            }
        },
        decrementTimer(state) {
            state.currentStage.limitSeconds--;

        },
        setCurrentStage(state, {stage, event, passing, role}) {
            state.currentStage = stage;
            state.currentStage['startDate'] = new Date(event.stages[event.stages.map(item => item.testId).indexOf(stage.id)].startDate);
            state.currentStage['endDate'] = new Date(event.stages[event.stages.map(item => item.testId).indexOf(stage.id)].endDate);

            const isNewPassing = stage.type === 'interview' ? false : passing?.testingPassing.map(item => item.testingId).includes(stage.id);

            state.passingTest = {
                eventId: event.id,
                currentStageNumber: role === 'rater' ? passing.currentStageNumber : (passing ? (!isNewPassing ? (passing.currentStageNumber + 1) : passing.currentStageNumber) : 1),
                testingPassing: {
                    testingId: stage.id,
                    answers: []
                }
            }
            let newPassing = passing ? passing.testingPassing[passing.testingPassing.map(item => (role === 'rater' ? item.id : item.testingId)).indexOf(state.testingPassingId ? state.testingPassingId : stage.id)] : null;
            if (newPassing && (role === 'rater' || stage.type === 'form')) {
                if (role === 'rater' && stage.type !== 'test') {
                    state.testingScore = [];
                    this.dispatch('fetchCurrentBlank', {id: stage.id})
                    this.dispatch('fetchCurrentPassingBlank', newPassing.id);

                }
                state.passingTest.testingPassing['finalScore'] = newPassing.finalScore != null ? newPassing.finalScore : null;
                state.passingTest.testingPassing['id'] = newPassing.id;
                state.passingTest.testingPassing.answers = newPassing.answers;

                if (stage.type === 'exercise') {
                    for (let i = 0; i < state.passingTest.testingPassing.answers.length; i++) {
                        state.passingTest.testingPassing.answers[i].attachmentAnswerFile = state.passingTest.testingPassing.answers[i].attachmentAnswerFile.split(',');
                    }
                }
                if (stage.type === 'form') {
                    //let result = state.currentStage.questions.length - state.passingTest.testingPassing.answers.length
                    let result = [];
                    for (let i = 0; i < state.currentStage.questions.length; i++) {
                        let add = true;
                        for (let j = 0; j < state.passingTest.testingPassing.answers.length; j++) {
                            if (state.currentStage.questions[i].id === state.passingTest.testingPassing.answers[j].questionId) {
                                add = false;
                                break;
                            }
                        }
                        if (add) {
                            result.push(state.currentStage.questions[i].id);
                        }
                    }
                    for (let i = 0; i < result.length; i++) {
                        state.passingTest.testingPassing.answers.push({
                            questionId: result[i],
                            answers: state.currentStage.questions[i].type === 'select' || state.currentStage.questions[i].type === 'single' ? '' : [],
                            matchingAnswers: [],
                            openAnswer: state.currentStage.questions[i].type === 'open' || state.currentStage.questions[i].type === 'text' || state.currentStage.questions[i].type === 'cities' || state.currentStage.questions[i].type === 'multi-open' ? '' : 'null', // nullable
                            attachmentAnswerFile: 'null'  // nullable
                        })
                    }
                }

            } else {
                for (let i = 0; i < state.currentStage.questions.length; i++) {
                    state.passingTest.testingPassing.answers.push({
                        questionId: state.currentStage.questions[i].id,
                        answers: state.currentStage.questions[i].type === 'select' || state.currentStage.questions[i].type === 'single' ? "" : [],
                        matchingAnswers: [],
                        openAnswer: state.currentStage.questions[i].type === 'open' || state.currentStage.questions[i].type === 'text' || state.currentStage.questions[i].type === 'cities' || state.currentStage.questions[i].type === 'multi-open' ? '' : 'null', // nullable
                        attachmentAnswerFile: stage.type === 'exercise' ? [] : 'null'  // nullable
                    });
                }
            }
        },
        parseEventsHistory(state, data) {
            let history = []
            for (let i = 0; i < data.length; i++) {
                let event = data[i].event;
                for (let j = 0; j < data[i].testingPassing.length; j++) {
                    let passing = data[i].testingPassing[j];
                    history.push({
                        title: event.title,
                        startDate: event.startDate,
                        createDate: passing.createDate,
                        type: passing.testing?.type,
                        score: passing.finalScore != null ? `${passing.finalScore} / ${passing.maxScore}` : 'На оценивании'
                    });
                }
            }
            this.commit('setEventsHistory', history);
        },
        setEventsHistory(state, data) {
            state.eventsHistory = data;
        },

        setCurrentInterview(state, id) {
            state.currentStage = state.currentEvents.stages[state.currentEvents.stages.map(item => item.id).indexOf(id)];
        },

        //PASSING TEST

        pickAnswer(state, {questionId, answerId, type, meta}) {
            let qIndex = state.passingTest.testingPassing.answers.map(item => item.questionId).indexOf(questionId);
            if (type === 'single' || type === 'select') {
                state.passingTest.testingPassing.answers[qIndex].answers = answerId;
            }
            if (type === 'multi') {
                if (typeof state.passingTest.testingPassing.answers[qIndex].answers === 'string') {
                    state.passingTest.testingPassing.answers[qIndex].answers = state.passingTest.testingPassing.answers[qIndex].answers.split(',');
                }
                let aIndex = state.passingTest.testingPassing.answers[qIndex].answers.indexOf(answerId);
                if (aIndex !== -1) {
                    state.passingTest.testingPassing.answers[qIndex].answers.splice(aIndex, 1);
                } else {
                    state.passingTest.testingPassing.answers[qIndex].answers.push(answerId);
                }
            }
            if (type === 'conformity') {
                let aIndex = state.passingTest.testingPassing.answers[qIndex].matchingAnswers.map(answer => answer.body).indexOf(meta.body);
                if (aIndex !== -1) {
                    state.passingTest.testingPassing.answers[qIndex].matchingAnswers[aIndex].answerId = answerId;
                } else {
                    state.passingTest.testingPassing.answers[qIndex].matchingAnswers.push({
                        body: meta.body,
                        answerId
                    });
                }
            }
            if (type === 'open' || type === 'text' || type === 'cities' || type === 'date') {
                state.passingTest.testingPassing.answers[qIndex].openAnswer = answerId;
            }
            if (type === 'file') {
                state.passingTest.testingPassing.answers[qIndex].attachmentAnswerFile = answerId;
            }
            if (type === 'files') {
                let answer = state.passingTest.testingPassing.answers[qIndex].attachmentAnswerFile[meta.index];
                if (answer != null) {
                    state.passingTest.testingPassing.answers[qIndex].attachmentAnswerFile[meta.index] = answerId;
                } else {
                    state.passingTest.testingPassing.answers[qIndex].attachmentAnswerFile.push(answerId);
                }
            }
        },
        deleteMatchingAnswer(state, {questionId, answerId}) {
            let qIndex = state.passingTest.testingPassing.answers.map(item => item.questionId).indexOf(questionId);
            let aIndex = state.passingTest.testingPassing.answers[qIndex].matchingAnswers.map(answer => answer.answerId).indexOf(answerId);
            state.passingTest.testingPassing.answers[qIndex].matchingAnswers.splice(aIndex, 1);
        },
        checkPassingForCVS(state, {role, eventPassingId, stageId}) {
            if (!state.passingStageForCVSData) {
                state.passingStageForCVSData = {
                    body: {},
                    eventPassingId,
                    stageId,
                    eventId: state.currentEvents.id,
                    createDate: new Date(),
                };
                state.passingStageForCVSData.body[state.currentStage.title] = '';
            }


            for (let i = 0; i < state.currentStage.questions.length; i++) {
                let answer = '';
                const answerIndex = state.passingTest.testingPassing.answers.map(item => item.questionId).indexOf(state.currentStage.questions[i].id);
                if (answerIndex !== -1) {
                    if (state.currentStage.questions[i].type === 'single' || state.currentStage.questions[i].type === 'select') {
                        let bodyIndex = state.currentStage.questions[i].answers.map(item => item.id).indexOf(state.passingTest.testingPassing.answers[answerIndex].answers);
                        if (bodyIndex !== -1) {
                            answer = state.currentStage.questions[i].answers[bodyIndex].body;
                        }
                    }


                    if (state.currentStage.questions[i].type === 'multi') {
                        const parsingAnswer = typeof state.passingTest.testingPassing.answers[answerIndex].answers === 'string' ? state.passingTest.testingPassing.answers[answerIndex].answers.split(',') : state.passingTest.testingPassing.answers[answerIndex].answers;
                        answer = parsingAnswer.map(answerItem => {
                            let bodyIndex = state.currentStage.questions[i].answers.map(item => item.id).indexOf(answerItem);
                            if (bodyIndex !== -1) {
                                return state.currentStage.questions[i].answers[bodyIndex].body;
                            }
                        }).join(',');
                    }
                    if (state.currentStage.questions[i].type === 'open' || state.currentStage.questions[i].type === 'multi-open' || state.currentStage.questions[i].type === 'text' || state.currentStage.questions[i].type === 'cities') {
                        answer = state.passingTest.testingPassing.answers[answerIndex].openAnswer;
                    }

                    if (state.currentStage.questions[i].type === 'date') {
                        answer = dayjs(state.passingTest.testingPassing.answers[answerIndex].openAnswer).format('DD-MM-YYYY');
                    }

                    if (state.currentStage.questions[i].type === 'file') {
                        answer = state.passingTest.testingPassing.answers[answerIndex].attachmentAnswerFile;
                    }

                    if (state.currentStage.questions[i].type === 'conformity') {
                        answer = state.passingTest.testingPassing.answers[answerIndex].matchingAnswers.map(answerItem => {
                            let bodyIndex = state.currentStage.questions[i].answers.map(item => item.id).indexOf(answerItem.answerId);
                            if (bodyIndex !== -1) {
                                return `${answerItem.body}-${state.currentStage.questions[i].answers[bodyIndex].matchingCorrectBody}`
                            }
                        }).join(',');
                    }
                }
                state.passingStageForCVSData.body[state.currentStage.questions[i].type === 'conformity' ? 'Сопоставление' : state.currentStage.questions[i].body] = answer;
            }

            if (role === 'rater') {
                state.passingStageForCVSData.body[state.currentStage.title] = state.passingTest.testingPassing.finalScore;
            }

            state.passingStageForCVSData.body['Дата создания'] = dayjs(state.passingStageForCVSData.createDate).format('DD.MM.YYYY HH:mm:ss');

            state.passingStageForCVSData.body = JSON.stringify(state.passingStageForCVSData.body);

        },
        checkPassingAnswers(state, role) {

            for (let i = 0; i < state.passingTest.testingPassing.answers.length; i++) {
                try {
                    if (typeof state.passingTest.testingPassing.answers[i].answers !== 'string')
                        state.passingTest.testingPassing.answers[i].answers = state.passingTest.testingPassing.answers[i].answers.join(',');
                } catch (e) {
                    console.log(e)
                }
                try {
                    if (typeof state.passingTest.testingPassing.answers[i].attachmentAnswerFile !== 'string')
                        state.passingTest.testingPassing.answers[i].attachmentAnswerFile = state.passingTest.testingPassing.answers[i].attachmentAnswerFile.join(',');
                } catch (e) {
                    console.log(e)
                }
            }

            if (role === 'rater') {
                if (state.passingTest.testingPassing.finalScore == null) {
                    state.passingTest.testingPassing.finalScore = 0;
                }
                for (let i = 0; i < state.testingScore.length; i++) {
                    if (state.testingScore[i].estimate) {
                        state.passingTest.testingPassing.finalScore = state.passingTest.testingPassing.finalScore + state.testingScore[i].estimate;
                    }
                }
            }
        },
        finishTesting(state) {
            for (let i = 0; i < state.passingTest.testingPassing.answers.length; i++) {
                if (state.currentStage.questions[i].type === 'multi') {
                    if (!state.passingTest.testingPassing.answers[i].answers.length) {
                        state.passingTest.testingPassing.answers[i]['delete'] = true;
                    }
                }

                if (state.currentStage.questions[i].type === 'single') {
                    if (!state.passingTest.testingPassing.answers[i].answers) {
                        state.passingTest.testingPassing.answers[i]['delete'] = true;
                    }
                }

                if (state.currentStage.questions[i].type === 'open') {
                    if (!state.passingTest.testingPassing.answers[i].openAnswer) {
                        state.passingTest.testingPassing.answers[i]['delete'] = true;
                    }
                }

                if (state.currentStage.questions[i].type === 'conformity') {
                    if (state.passingTest.testingPassing.answers[i].matchingAnswers.length !== state.currentStage.questions[i].answers.length) {
                        state.passingTest.testingPassing.answers[i]['delete'] = true;
                    }
                }
            }
            state.passingTest.testingPassing.answers = state.passingTest.testingPassing.answers.filter(item => !item.delete);
        },
        clearPassing(state) {
            state.currentStage = null;
            state.interview = null;
            state.passingTest = null;
            state.testingPassingId = null;
            state.currentBlank = null;
            state.passingBlank = null;
            state.passingBlankAllScore = 0;
            state.passingStageForCVSData = null;
            state.testingScore = [];
            state.stageId = null;
            state.currentEvents = null;
            state.eventPassing = null;
        },
        setScore(state, data) {
            let index = state.testingScore.map(item => item.questionId).indexOf(data.questionId);
            if (index === -1) {
                state.testingScore.push(data);
            } else {
                state.testingScore[index].estimate = data.estimate;
            }
        },
        setTestingPassingId(state, data) {
            state.testingPassingId = data;
        },
        setBlankComment(state, data) {
            state.passingBlank.comment = data;
        },
        setBlankColor(state, data) {
            state.passingBlank.finalColor = data;
        },
        setPassingBlankScore(state, {val, index}) {
            state.passingBlank.blankCriteriaPassings[index].score = val;
            state.passingBlankAllScore = state.passingBlank.blankCriteriaPassings.reduce((a, b) => a.score + b.score);

        },
        setCurrentPassingBlank(state, data) {
            if (data) {
                state.passingBlank = data;
                for (let i = 0; i < state.passingBlank.blankCriteriaPassings.length; i++) {
                    state.passingBlankAllScore += state.passingBlank.blankCriteriaPassings[i].score;
                }
            } else if (state.currentBlank) {
                state.passingBlank = {
                    blankId: state.currentBlank.id,
                    finalColor: null,
                    comment: null,
                    testingPassingId: state.passingTest.testingPassing.id,
                    blankCriteriaPassings: []
                }
                for (let i = 0; i < state.currentBlank.blankCriterias.length; i++) {
                    state.passingBlank.blankCriteriaPassings.push(
                        {
                            blankCriteriaId: state.currentBlank.blankCriterias[i].id,
                            score: 0
                        }
                    )
                }
            }

        },
        setCurrentBlank(state, {data, testingId, edit}) {
            if (!data && edit) {
                data = {
                    testingId,
                    blankCriterias: []
                }
            }
            state.currentBlank = data;
        },
        addBlank(state) {
            state.currentBlank.blankCriterias.push({
                title: null,
                description: null,
                maxScore: 0
            });
        },

        deleteBlank(state, index) {
            if (state.currentBlank?.blankCriterias?.[index]) {
                if (state.currentBlank?.blankCriterias[index]?.id) {
                    state.currentBlank.blankCriterias[index]['delete'] = true;
                } else {
                    state.currentBlank.blankCriterias.splice(index, 1);
                }
            }
        },
        setBlankDescription(state, {val, index}) {
            state.currentBlank.blankCriterias[index].description = val;
        },
        setBlankTitle(state, {val, index}) {
            state.currentBlank.blankCriterias[index]['title'] = val;
        },
        setBlankScore(state, {val, index}) {
            state.currentBlank.blankCriterias[index].maxScore = val;
        },
        setBlankEstimationText(state, {val, key}) {
            state.currentBlank[key] = val;
        },


    },
    state: {
        userEvents: null,
        currentEvents: null,
        currentStage: null,
        eventsHistory: null,
        allEvents: null,
        eventPassing: null,
        //passing test
        passingTest: null,
        testingPassingId: null,
        testingScore: [],
        passingBlank: null,
        currentBlank: null,
        passingBlankAllScore: 0,
        passingStageForCVSData: null,
        stageId: null,
        passingId: null


    },
    getters: {
        userEvents: state => state.userEvents,
        currentEvents: state => state.currentEvents,
        currentStage: state => state.currentStage,
        eventsHistory: state => state.eventsHistory,
        allEvents: state => state.allEvents,
        passingTest: state => state.passingTest,
        eventPassing: state => state.eventPassing,
        testingScore: state => state.testingScore,
        passingBlank: state => state.passingBlank,
        currentBlank: state => state.currentBlank,
        passingBlankAllScore: state => state.passingBlankAllScore,
        passingStageForCVSData: state => state.passingStageForCVSData,
        stageId: state => state.stageId,
        passingId: state => state.passingId,

    }
}