import {
    approvedRegistration,
    changePassword,
    fetchUserData,
    login,
    register, restorePassword, restorePasswordSendEmail, updateUserData,
} from "./serverInterationMethods";
import {errorHandler} from "@/lib/errorHandler";


// store with all authenticated user data
export const user = {
    state: {
        // authorization user token for api
        token: null,
        user: null,
        role: null
    },
    mutations: {
        // setting token state
        setToken(state, data) {
            state.token = data;
        },
        setUserData(state, data) {
            state.user = data;

            if (data) {
                state.role = data.role;
            } else {
                state.role = null;
            }
        },
        setRole(state, data) {
            state.role = data;
        },
        setUserName(state, data) {
            state.user.name = data;
        }
    },
    actions: {
        async restorePasswordSendEmail(_, data) {
            const {status} = await restorePasswordSendEmail(data);
            if (status === 200) {
                errorHandler({name: 'restorePasswordSendEmail'});
            }
        },

        async restorePassword(_, data) {
            const {status} = await restorePassword(data);
            errorHandler({name: 'restorePassword', status});
        },

        async approvedRegistration(_, data) {
            const {status} = await approvedRegistration(data);
            errorHandler({name: 'approvedRegistration', status});
        },

        async fetchUserData({getters, commit}) {
            const {data} = await fetchUserData(getters.token);
            commit('setUserData', data);
            if (!data) {
                this.dispatch('logout');
            }
        },
        // gets user auth token from cookies if needed
        async loadUserToken({getters, commit}) {
            if (getters.token) {
                await this.dispatch('fetchUserData');
                return
            }
            let token = localStorage.getItem('token');
            if (token) {
                commit('setToken', token);
                await this.dispatch('fetchUserData');
            }
        },
        // default user authorization method. Saves token in cookies if success
        async login({getters, commit}, user) {
            if (getters.token) return;
            const response = await login(user)
            if (response?.status === 200) {
                const {data} = response;
                localStorage.setItem('token', data.token);
                commit('setToken', data.token);
                errorHandler({name: 'login', status: response?.status});
            }
        },
        // default new user register method
        async register({getters}, data) {
            if (getters.token) return;
            const {status} = await register(data);
            return status;
        },
        // default logout method. Clear auth token from Storage (cookies by default)
        logout({commit}) {
            localStorage.removeItem('token');
            sessionStorage.removeItem('approveRegistration');
            commit('setToken', null);
            commit('setUserData', null);
            errorHandler({name: 'logout'})
        },
        async updateUserData({getters, commit}, user) {
            //user.birthDate = dayjs(user.birthDate).toDate();
            const {status} = await updateUserData(getters.token, user);
            if (status === 200)
                commit('setUserName', user.name)
            errorHandler({name: 'updateUser', status});
        },
        async changePassword({getters}, data) {
            const {status} = await changePassword(getters.token, data);
            errorHandler({name: 'updateUser', status});
        }
    },
    getters: {
        // get default user auth token
        token: (state) => state.token,
        user: state => state.user,
        role: state => state.role
    }
};