<template>
  <div class="stream-editor">
    <div class="editor-content">

    </div>

    <div class="editor-el">
      <a-typography-title :level="5">Название:</a-typography-title>
      <a-input :value="stream?.title" @change="event=>changeStreamData(event.target.value,'title')"></a-input>
    </div>

    <div class="editor-el">
      <a-typography-title :level="5">Описание:</a-typography-title>
      <a-textarea :rows="4" :value="stream?.description"
                  @change="event=>changeStreamData(event.target.value,'description')"></a-textarea>
    </div>


    <!--    <div class="editor-el">-->
    <!--      <a-typography-title :level="5">Даты проведения:</a-typography-title>-->
    <!--      <a-config-provider :locale="locale">-->
    <!--        <a-range-picker-->
    <!--            v-model:value="streamDates"-->
    <!--            :locale="locale"-->
    <!--            :show-time="{ format: 'HH:mm' }"-->
    <!--            format="YYYY-MM-DD HH:mm"-->
    <!--            :placeholder="['Начало', 'Конец']"-->
    <!--        />-->
    <!--      </a-config-provider>-->
    <!--    </div>-->


    <div class="editor-el">
      <a-typography-title :level="5">
        <a-tooltip>
          <template #title>
            Необходимо вставить html код трансляции. Например:
            <code>{{ iframeExample }}</code>
          </template>
          <info-circle-outlined/>
        </a-tooltip>
        Iframe:
      </a-typography-title>
      <a-textarea :rows="4" :value="stream?.body"
                  @change="event=>changeStreamData(event.target.value,'body')"></a-textarea>
    </div>

    <div class="editor-el">
      <div class="" style="display: flex; justify-content: center">
        <a-button type="primary" @click="saveStream">Сохранить трансляцию</a-button>
      </div>
    </div>


  </div>
</template>


<script>
import {defineComponent} from 'vue'
import dayjs from "dayjs";
import {InfoCircleOutlined} from "@ant-design/icons-vue";
import locale from "ant-design-vue/es/date-picker/locale/ru_RU";


export default defineComponent({
  name: "StreamEditorPage",
  props: ['streamId'],
  components: {
    InfoCircleOutlined
  },
  computed: {
    stream() {
      return this.$store.getters.stream
    },
    streamDates: {
      get() {
        const startDate = this.stream?.startDate ? dayjs(this.stream?.startDate) : null
        const endDate = this.stream?.endDate ? dayjs(this.stream?.endDate) : null
        return [startDate, endDate];
      },
      set(val) {
        this.$store.commit('changeStreamData', {val: val[0], field: 'startDate'});
        this.$store.commit('changeStreamData', {val: val[1], field: 'endDate'});
      }
    }
  },
  data() {
    return {
      dayjs,
      iframeExample: '<iframe src="..." width="..."\n' +
          '                    height="..." allow="autoplay; encrypted-media; fullscreen; picture-in-picture;" frameborder="0"\n' +
          '                    allowfullscreen></iframe>',
      locale
    }
  },
  methods: {
    changeStreamData(val, field) {
      this.$store.commit('changeStreamData', {val, field});
    },
    saveStream() {
      if (!this.stream?.title || !this.stream?.description || !this.stream?.body) return;
      this.$store.dispatch('saveStream', this.streamId);
    }
  },
  beforeMount() {
    this.$store.dispatch('fetchStreamById', this.streamId);
  }
})
</script>


