import {
    calculateCharts, calculateSoloParamCharts,
    createEvent, createMember, deleteUser, fetchEventsResults,
    fetchEventsStatistic, fetchEventXLSByStageId, fetchFromQuestions,
    fetchOwnEvents, fetchPostMessages, fetchRaters, fetchUserById, fetchUsersByEventId,
    fetchUsersList,
    saveRater, saveRaterData, saveStatistics, sendPostMessage, updateMember
} from "@/store/admin/serverInterantionMethods";
import {errorHandler} from "@/lib/errorHandler";
import {ageCalculation} from "@/lib/convertTime";

export const admin = {
    actions: {

        async deleteUser({getters, commit}, userId) {
            const {status} = await deleteUser(getters.token, userId);
            if (status === 200) {
                commit('deleteUser', userId);
            }
            errorHandler({name: 'deleteUser', status});
        },

        async fetchEventXLS({getters}, event) {


            const zip = new JSZip();

            for (let i = 0; i < event?.stages?.length; i++) {
                const {data} = await fetchEventXLSByStageId(getters.token, event?.id, event?.stages[i].id)
                zip.file(`${event?.stages[i].testing ? event?.stages[i].testing.title.replace(/[.]/g, ',') : event?.stages[i].id}.xls`, data);
            }

            zip.generateAsync({type: "blob"})
                .then(function (content) {
                    const el = document.createElement('a');
                    el.download = event.title.replace(/[.]/g, ',');
                    el.href = URL.createObjectURL(content);
                    el.click();
                });
        },

        async saveRaterPass({getters}, {pass, clientId}) {
            await savePassingTesting(getters.token, pass, clientId);
        },

        async fetchUsersByEventId({getters}, id) {
            const {data} = await fetchUsersByEventId(getters.token, id);
            return data;
        },

        async saveRaterData({getters}, data) {
            const {status} = await saveRaterData(getters.token, data);
            errorHandler({name: 'changeRater', status});
        },

        async fetchRaters({commit, getters}, {page, limit}) {
            const {data} = await fetchRaters(getters.token, page, limit);
            commit('setRaters', data);
        },

        async fetchUserById({getters, commit}, id) {
            let data;
            if (id === 'new') {
                data = {
                    name: [null, null, null],
                    phone: null,
                    password: null,
                    enterPassword: null,
                    city: null,
                    birthDate: null,
                    sex: null,
                    registrationDate: new Date()
                }
            } else {
                const response = await fetchUserById(getters.token, id);
                data = response.data;
            }
            commit('setCurrentUser', data);
        },

        async saveMemberData({getters}, {id, data}) {
            let response;
            if (id === 'new') {
                response = await createMember(getters.token, data);
            } else {
                response = await updateMember(getters.token, data);
            }
            errorHandler({name: 'member', status: response.status});
            return response.status
        },

        async fetchPostMessages({getters, commit}, {page, limit}) {
            const {data} = await fetchPostMessages(getters.token, page, limit);
            commit('setPostMessages', {data, page});
            return data;
        },

        async sendPostMessage({getters,}, post) {
            const {status} = await sendPostMessage(getters.token, post);
            errorHandler({name: 'postMessage', status});
            return status;
        },

        async calculateSoloParamCharts({getters, commit}, {funcType, questionId, index, type}) {
            const {data} = await calculateSoloParamCharts(getters.token, funcType, questionId);
            commit('setSoloChartsData', {data, index, funcType, type, questionId})
        },

        async fetchFromQuestions({getters}, id) {
            const {data} = await fetchFromQuestions(getters.token, id);
            return data;
        },
        async fetchUsersList({commit, getters}, {
            name,
            city,
            sex,
            minBirthDate,
            maxBirthDate,
            sortRegDate,
            externalId,
            phone,
            role
        }) {
            const {data} = await fetchUsersList(getters.token, name ? name : '', city ? city : '', sex ? sex : '', minBirthDate ? dayjs(minBirthDate).endOf('day').toDate().toUTCString() : '', maxBirthDate ? dayjs(maxBirthDate).endOf('day').toDate().toUTCString() : '', sortRegDate ? sortRegDate : 'desc', externalId, phone, role);
            commit('setUsersList', data);
        },
        async createEvent({getters}) {
            const data = {
                title: "My first event",
                description: "My first description",
                previewUrl: "https://lh3.googleusercontent.com/pVwHZ9RIYePQ_6EpW-gNrKb3Po17SwIIx5LT7RPgrPcpUsXMIeeWVzCKj5ByRNhFOPU4eZseKv2mLh1yIHH_=w1200",
                backgroundColor: "green",
                createDate: new Date(),
                startDate: new Date(),
                endDate: new Date()
            }
            const {status} = await createEvent(getters.token, data)
            errorHandler({name: 'createEvent', status})
        },
        async fetchOwnEvents({getters, commit}, {name, minStartDate, maxStartDate, minEndDate, maxEndDate}) {
            const {data} = await fetchOwnEvents(getters.token, name, minStartDate ? minStartDate : '', maxStartDate ? maxStartDate : '', minEndDate ? minEndDate : '', maxEndDate ? maxEndDate : '');
            commit('setOwnEvents', data);
        },

        async saveEvent({getters, commit}, {event, isDelete}) {
            const {status} = await createEvent(getters.token, event);
            if (!isDelete) {
                errorHandler({name: 'eventEditor', status});
            } else {
                commit('deleteEvent', event.id);
            }
        },
        async saveRater({getters}, data) {
            const {status} = await saveRater(getters.token, data);
            errorHandler({name: 'appointment', status})
            return status
        },
        async fetchEventsStatistic({commit, getters}, eventId) {
            const {data} = await fetchEventsStatistic(getters.token, eventId);
            commit('setEventsStatistic', {data, eventId});
        },
        async fetchEventsResults({commit, getters}, eventId) {
            const {data} = await fetchEventsResults(getters.token, eventId);
            commit('setEventsResults', JSON.parse(data));
        },
        async saveStatistics({getters}, eventId) {
            const {status} = await saveStatistics(getters.token, getters.statistic, eventId);
            errorHandler({name: 'appointment', status})
        },
        async calculateCharts({getters, commit}, {index, tag1, tag2, type}) {
            if (!tag1 || !tag2 || !type) {
                return;
            }
            if (tag1 === 'sex') {
                tag1 = tag2;
                tag2 = 'sex'
            }

            if (tag2 === 'birth') {
                tag2 = tag1;
                tag1 = 'birth';
            }


            const {data} = await calculateCharts(getters.token, tag1, tag2, tag1 === 'score' || tag1 === 'stage' ? 'sum' : 'count', getters.statistic?.eventId);
            commit('setChartsData', {index, data: JSON.parse(data), tag1, tag2, type});
        }

    },
    mutations: {

        deleteUser(state, userId) {
            const index = state.usersList?.map(item => item.id).indexOf(userId);
            if (index > -1) {
                state.usersList?.splice(index, 1);
            }
        },
        deleteEvent(state, id) {
            const index = state.ownEvents.map(item => item.id).indexOf(id);
            if (index !== -1) {
                state.ownEvents.splice(index, 1);
            }
        },
        setRaters(state, data) {
            state.raters = data;
        },
        setCurrentUser(state, data) {
            state.currentUser = data;
        },
        setPostMessages(state, {data, page}) {
            if (page === 1) {
                state.postMessages = data;
            } else {
                state.postMessages.push(...data);
            }
        },
        setEventsResults(state, data) {
            state.eventResults = data;
        },
        setOwnEvents(state, data) {
            state.ownEvents = data;
        },
        setUsersList(state, data) {
            state.usersList = data;
        },
        addUsersList(state, data) {
            state.usersList = state.usersList.concat(data);
        },
        async setEventsStatistic(state, {data, eventId}) {
            if (!data.length) {
                state.statistic = {
                    eventId: eventId,
                    charts: []
                    // [
                    // {
                    //     index: 0,
                    //     type: null,
                    //     horizontalParam: null,
                    //     verticalParam: null
                    // }
                    // ]
                }
            } else {
                state.statistic = data[0];
                for (let i = 0; i < state.statistic.charts.length; i++) {
                    state.statistic.charts[i]['index'] = i;
                }
                for (let i = 0; i < state.statistic.charts.length; i++) {

                    if (state.statistic.charts[i].statisticType === 'soloParam') {
                        await this.dispatch('calculateSoloParamCharts', {
                            index: i,
                            type: state.statistic.charts[i].type,
                            funcType: state.statistic.charts[i].funcType,
                            questionId: state.statistic.charts[i].questionId
                        })
                    } else {
                        await this.dispatch('calculateCharts', {
                            index: i,
                            tag1: state.statistic.charts[i].verticalParam,
                            tag2: state.statistic.charts[i].horizontalParam,
                            type: state.statistic.charts[i].type
                        });
                    }

                }

            }
        },

        changeSoloParamStatistic(state, {index, val, type}) {
            state.statistic.charts[index][type] = val;
            if (state.statistic.charts[index].funcType && state.statistic.charts[index].questionId) {
                this.dispatch('calculateSoloParamCharts', {
                    index,
                    type: state.statistic.charts[index].type,
                    funcType: state.statistic.charts[index].funcType,
                    questionId: state.statistic.charts[index].questionId
                })
            }
        },

        changeStatistic(state, {val, index, type}) {
            switch (type) {
                case 'type':
                    state.statistic.charts[index].type = val;
                    break;
                case 'horizontal':
                    state.statistic.charts[index].horizontalParam = val;
                    break;
                case 'vertical':
                    state.statistic.charts[index].verticalParam = val;
                    break;
            }
            if (state.statistic.charts[index].type && state.statistic.charts[index].verticalParam && state.statistic.charts[index].horizontalParam) {
                this.dispatch('calculateCharts', {
                    index,
                    tag1: state.statistic.charts[index].verticalParam,
                    tag2: state.statistic.charts[index].horizontalParam,
                    type: state.statistic.charts[index].type
                });
            }
        },
        setChartsData(state, {index, data, tag1, tag2, type}) {
            let operator = tag1 === 'score' || tag1 === 'stage' ? 'sum' : 'count';
            let chartData = {
                labels: [],
                datasets: []
            };

            if (tag2 === 'sex') {
                chartData.labels = ['Мужчины', 'Женщины'];
            } else {
                for (let i = 0; i < data.length; i++) {
                    chartData.labels.push(data[i][tag2] ? data[i][tag2] : 'Неопределенно')
                }
            }


            for (let i = 0; i < data.length; i++) {
                let add = true;
                for (let j = 0; j < chartData.datasets.length; j++) {
                    if ((data[i][tag1] ? (tag1 === 'birth' ? ageCalculation(data[i][tag1]) : data[i][tag1]) : (operator === 'count' ? 'Неопределенно' : `Среднее количество ${tag1 === 'score' ? 'баллов' : 'пройденных этапов'}`)) === chartData.datasets[j].label) {
                        if (tag2 === 'sex') {
                            if (data[i].sex === 'man') {
                                chartData.datasets[j].data[0] += operator === 'count' ? data[i]._count.sex : Number(data[i].sum);
                            } else {
                                chartData.datasets[j].data[1] += operator === 'count' ? data[i]._count.sex : Number(data[i].sum);
                            }
                        } else {
                            chartData.datasets[j].data.push(operator === 'count' ? data[i]._count[tag2] : Number(data[i].sum))
                        }

                        add = false;
                    }
                }

                if (add) {
                    if (tag2 === 'sex') {
                        chartData.datasets.push({
                            label: data[i][tag1] ? (tag1 === 'birth' ? ageCalculation(data[i][tag1]) : data[i][tag1]) : (operator === 'count' ? 'Неопределенно' : `Среднее количество ${tag1 === 'score' ? 'баллов' : 'пройденных этапов'}`),
                            data: [data[i].sex === 'man' ? (operator === 'count' ? data[i]._count.sex : Number(data[i].sum)) : 0, data[i].sex === 'girl' ? (operator === 'count' ? data[i]._count.sex : Number(data[i].sum)) : 0],
                            backgroundColor: type === 'pie' ? (chartData.datasets.length ? chartData.datasets[chartData.datasets.length - 1].backgroundColor : ['#5EC9CA', '#11CCCC']) : '#5EC9CA'
                        })
                    } else {
                        chartData.datasets.push({
                            label: data[i][tag1] ? (tag1 === 'birth' ? ageCalculation(data[i][tag1]) : data[i][tag1]) : (operator === 'count' ? 'Неопределенно' : `Среднее количество ${tag1 === 'score' ? 'баллов' : 'пройденных этапов'}`),
                            data: [operator === 'count' ? data[i]._count[tag2] : Number(data[i].sum)],
                            backgroundColor: type === 'pie' ? (chartData.datasets.length ? chartData.datasets[chartData.datasets.length - 1].backgroundColor : generateColor(chartData.labels.length)) : '#5EC9CA'
                        })
                    }
                }

            }
            state.statistic.charts[index]['chartData'] = chartData
        },
        addCharts(state) {
            state.statistic.charts.push({
                index: state.statistic.charts.length,
                statisticType: 'ratio',
                type: null,
                horizontalParam: null,
                verticalParam: null
            })
        },
        addSoloCharts(state) {
            state.statistic.charts.push({
                index: state.statistic.charts.length,
                statisticType: 'soloParam',
                type: 'pie',
                funcType: null,
                horizontalParam: null,
                questionId: null
            })
        },
        setSoloChartsData(state, {data, index, funcType, type, questionId}) {

            let chartData = {
                labels: [],
                datasets: []
            };
            if (funcType !== 'distr') {
                chartData.datasets.push(data.value);

            } else {
                let val = data.map(item => item.openAnswer).filter(item => item != null).length ? 'openAnswer' : 'answers';
                // //LABELS
                // for (let i = 0; i < data.length; i++) {
                //     let add = true;
                //     for (let j = 0; j < chartData.labels.length; j++) {
                //         if ((data[i].openAnswer && data[i].openAnswer !== 'null' ? data[i].openAnswer : 'Не определенно') === chartData.labels[j]) {
                //             add = false;
                //             break;
                //         }
                //     }
                //     if (add) {
                //         chartData.labels.push(data[i].openAnswer && data[i].openAnswer !== 'null' ? data[i].openAnswer : 'Не определенно')
                //     }
                // }
                // //DATASETS
                // for (let i = 0; i < data.length; i++) {
                //     let add = true;
                //     for (let j = 0; j < chartData.datasets.length; j++) {
                //         if ((data[i].openAnswer && data[i].openAnswer !== 'null' ? data[i].openAnswer : 'Не определенно') === chartData.datasets[j].label) {
                //             add = false;
                //             chartData.datasets[j].data.push(data[i]._count.openAnswer)
                //             break;
                //         }
                //     }
                //     if (add) {
                //         chartData.datasets.push({
                //             label: (data[i].openAnswer && data[i].openAnswer !== 'null' ? data[i].openAnswer : 'Не определенно'),
                //             data: [data[i]._count.openAnswer]
                //         })
                //     }
                // }

                if (type === 'bar') {
                    chartData.labels = data.map(item => item[val]).filter(item => item && item !== 'null');
                    chartData.datasets = [
                        {
                            data: data.filter(item => item[val] && item[val] !== 'null').map(item => item._count[val]),
                            // label: state.questionsInSoloParamCharts.map(question =>
                            //     question.id).indexOf(questionId) !== -1 ? state.questionsInSoloParamCharts[state.questionsInSoloParamCharts.map(question =>
                            //     question.id).indexOf(questionId)].body : ''

                        }
                    ]
                } else {
                    chartData.labels = data.map(item => item[val]).filter(item => item && item !== 'null');
                    chartData.datasets = [
                        {
                            data: data.filter(item => item[val] && item[val] !== 'null').map(item => item._count[val]),
                            backgroundColor: generateColor(chartData.labels.length)
                        }
                    ]
                    for (let i = 0; i < chartData.labels.length; i++) {
                        chartData.labels[i] = state.questionsInSoloParamCharts.map(question =>
                            question.id).indexOf(questionId) !== -1 && state.questionsInSoloParamCharts[state.questionsInSoloParamCharts.map(question =>
                            question.id).indexOf(questionId)].answers.map(answer => answer.id).indexOf(chartData.labels[i]) !== -1 ?
                            state.questionsInSoloParamCharts[state.questionsInSoloParamCharts.map(question =>
                                question.id).indexOf(questionId)].answers[state.questionsInSoloParamCharts[state.questionsInSoloParamCharts.map(question =>
                                question.id).indexOf(questionId)].answers.map(answer => answer.id).indexOf(chartData.labels[i])].body : chartData.labels[i];
                    }

                }

            }
            state.statistic.charts[index]['chartData'] = chartData

        },
        addSoloParamChartsQuestions(state, data) {
            state.questionsInSoloParamCharts = state.questionsInSoloParamCharts.concat(data);
            state.questionsInSoloParamCharts = state.questionsInSoloParamCharts.filter(({id}) => (!state.questionsInSoloParamCharts[id] && (state.questionsInSoloParamCharts[id] = 1)));
        }

    },
    state: {
        usersList: null,
        ownEvents: null,
        statistic: null,
        eventResults: null,

        questionsInSoloParamCharts: [],

        postMessages: [],

        currentUser: null,

        raters: null
    },
    getters: {
        usersList: state => state.usersList,
        ownEvents: state => state.ownEvents,
        statistic: state => state.statistic,
        eventResults: state => state.eventResults,
        questionsInSoloParamCharts: state => state.questionsInSoloParamCharts,
        postMessages: state => state.postMessages,
        currentUser: state => state.currentUser,
        raters: state => state.raters
    }
}


import {randomColor} from 'randomcolor'
import {savePassingTesting} from "@/store/events/serverInterationMethods";
import JSZip from "jszip";
import dayjs from "dayjs";

function generateColor(count) {
    if (count == null) {
        return Math.floor(Math.random() * 2) ? '#11CCCC' : '#5EC9CA'
    } else {
        let array = [];
        for (let i = 0; i < count; i++) {
            if (i === 0) {
                array.push('#11CCCC');
                continue
            }
            if (i === 1) {
                array.push('#5EC9CA');
                continue
            }
            array.push(randomColor({
                hue: Math.floor(Math.random() * 2) ? '#11CCCC' : '#5EC9CA'
            }))
        }
        return array;
    }

}

