<template>


  <div class="card-container">
    <div class="card-header">
      <a-typography-title :level="5" style="margin: 8px;" type="secondary">
        Соотношения
      </a-typography-title>
      <a-popover placement="bottom" v-model:visible="visible" trigger="click">
        <template #content>
          <div class="param-el">
            <a-select
                @change="val=>$store.commit('changeStatistic',{val,index:item.index,type:'type'})"
                style="width: 300px"
                placeholder="Тип"
                :value="item.type">
              <a-select-option value="line">
                График
              </a-select-option>
              <a-select-option value="pie">
                Диаграмма
              </a-select-option>
              <a-select-option value="bar">
                Гистограмма
              </a-select-option>
            </a-select>
            <a-select
                @change="val=>$store.commit('changeStatistic',{val,index:item.index,type:'horizontal'})"
                style="width: 300px"
                placeholder="По горизонтали"
                :value="item.horizontalParam">
              <a-select-option :disabled="item.verticalParam===item2.value"
                               v-for="item2 in params.filter(item=>item.value==='sex'||item.value==='birth'||item.value==='city')"
                               :key="item2.label"
                               :value="item2.value">
                {{ item2.label }}
              </a-select-option>
            </a-select>
            <a-select
                @change="val=>$store.commit('changeStatistic',{val,index:item.index,type:'vertical'})"
                style="width: 300px"
                placeholder="По вертикали"
                :value="item.verticalParam">
              <a-select-option :disabled="item.horizontalParam===item2.value"
                               v-for="item2 in params"
                               :key="item2.label"
                               :value="item2.value">
                {{ item2.label }}
              </a-select-option>
            </a-select>
          </div>
        </template>
        <button style="width: 30px;text-align: center;cursor: pointer" @click="visible=!visible">
          <down-outlined style="color: rgba(0, 0, 0, 0.45);font-size: 15px" v-if="!visible"/>
          <up-outlined style="color: rgba(0, 0, 0, 0.45);font-size: 15px" v-if="visible"/>
        </button>
      </a-popover>
    </div>
    <a-divider style="margin: 8px;height: 1px"></a-divider>

    <div class="card-content">
      <div class="charts-block"
           v-if="(item.type&&item.horizontalParam&&item.verticalParam&&item.chartData)">
        <Line :data="item.chartData"
              v-if="item.type==='line'&& item.chartData.labels.length"
              :options="chartOptions"/>
        <Bar :data="item.chartData"
             v-else-if="item.type==='bar'&& item.chartData.labels.length"
             :options="chartOptions"/>
        <Pie :data="item.chartData"
             v-else-if=" item.type==='pie'&& item.chartData.labels.length"
             :options="chartOptions"/>
        <a-empty style="color: black"
                 v-if="item.statisticType==='soloParam'&&item.funcType==='distr'&&!item.chartData.labels.length"
                 description="Не хватает данных для статистики"/>
      </div>
      <a-empty style="color: black" class="charts-block" v-else
               description="Выберете тип, параметры по горизонтали и вертикали"/>
    </div>
  </div>

</template>

<script>
import {DownOutlined, UpOutlined} from "@ant-design/icons-vue";
import {Line, Bar, Pie} from 'vue-chartjs';
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  ArcElement
} from 'chart.js'

ChartJS.register(Title, Tooltip, Legend, PointElement,
    BarElement, LineElement, CategoryScale, LinearScale, ArcElement)

export default {
  components: {
    Line, Bar, Pie,
    DownOutlined, UpOutlined
  },
  props: ['item', 'params'],
  name: "ChartRatio",
  data() {
    return {
      visible: false,
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false
      },
    }
  },
}
</script>

<style scoped>

.card-container {
  display: flex;
  flex-direction: column;
  padding: 35px;

}

.card-header {
  display: flex;
  justify-content: space-between;
}


.charts-block {
  height: 260px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}


.param-el {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

</style>