<template>
  <div class="container">
    <div class="filling">
      <div class="table-caption">
        <a-typography-title
            :level="2"
            class="table-caption__title"
        >
          Список этапов
        </a-typography-title>
        <div class="mode-container">
          <a-radio-group v-model:value="mode">
            <a-radio value="test">Тестирования</a-radio>
            <a-radio value="exercise">Практические задания</a-radio>
            <a-radio value="form">Анкеты</a-radio>
            <a-radio value="interview">Видео-интервью</a-radio>
            <a-radio value="stream">Трансляции</a-radio>
          </a-radio-group>
          <a-button v-if="false" shape="round" type="primary" class="create-button" @click="editTesting('new')">Создать
          </a-button>
        </div>
      </div>
      <a-table class="table" v-if="allTesting||allInterviews||streamsList"
               :columns="mode==='interview'?interviewColumns:columns"
               :data-source="mode==='interview'? allInterviews: mode==='stream' ? streamsList : allTesting?.filter(item=>item.type===mode)">
        <template #bodyCell="{column,record}">
          <div v-if="column.key==='actions'">
            <a-button shape="round" type="primary" class="blue-buttons" @click="editTesting(record.id)">
              <template #icon>
                <edit-outlined style="font-size: 20px;"/>
              </template>
              Редактировать
            </a-button>
          </div>
          <div v-if="column.key==='startDate'||column.key==='endDate'">
            {{
              record[column.key] ? dayjs(record[column.key]).format('DD-MM-YYYY') : 'Дата не назначена'
            }}
          </div>
        </template>
        <template #customFilterDropdown="{ confirm, column}">
          <div class="filter-container" v-if="column.key==='title'">
            <div class="filter-el">
              <a-input v-model:value="name" placeholder="Найти по названию"></a-input>
            </div>
            <div class="filter-el">
              <div class="filter-btn">
                <a-button shape="round" type="primary" @click="fetchAllTesting(confirm)">Найти</a-button>
                <a-button shape="round" @click="(name='',fetchAllTesting(confirm))">Сбросить</a-button>
              </div>
            </div>
          </div>
        </template>
        <template #customFilterIcon>
          <search-outlined :style="{ color: name ? '#108ee9' : undefined }"/>
        </template>
      </a-table>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import {EditOutlined, SearchOutlined} from "@ant-design/icons-vue";
import dayjs from "dayjs";

export default {
  name: "TestingListPage",
  computed: {
    ...mapGetters(['allTesting', 'allInterviews', 'streamsList'])
  },
  components: {
    EditOutlined, SearchOutlined
  },
  watch: {
    mode: function (newVal) {

      switch (newVal) {
        case 'interview':
          this.$store.dispatch('fetchAllInterviews');
          break;
        case 'stream':
          this.$store.dispatch('fetchStreams');
          break;
        default:
          this.fetchAllTesting();
      }

      this.$store.commit('setStageMode', newVal);
    }
  },
  data() {
    return {
      //FILTERS

      name: '',

      mode: 'test',

      dateOptions: {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      },

      columns: [
        {
          title: 'Название',
          dataIndex: 'title',
          key: 'title',
          customFilterDropdown: true,
          width: 400,
          ellipsis: true

        },
        {
          title: 'Описание',
          dataIndex: 'description',
          key: 'description',
          ellipsis: true
        },
        {
          title: 'Действия',
          key: 'actions',
          width: 142
        },
      ],

      interviewColumns: [
        {
          title: 'Дата начала',
          dataIndex: 'startDate',
          key: 'startDate',
          width: 400
        },
        {
          title: 'Дата конца',
          dataIndex: 'endDate',
          key: 'endDate',
        },
        {
          title: 'Действия',
          key: 'actions',
          width: 142
        },
      ],

    }
  },
  methods: {
    dayjs,
    editTesting(id) {
      if (this.mode === 'interview') {
        this.$router.push({name: 'interviewEditor', params: {id}});
      } else if (this.mode === 'stream') {
        this.$router.push({name: 'streamEditor', params: {streamId: id}});
      } else {
        this.$router.push({name: 'testingEditor', params: {mode: this.mode, testingId: id}});
      }
    },

    fetchAllTesting(confirm) {
      this.$store.dispatch('fetchAllTesting', {name: this.name}).then(() => {
        if (confirm) {
          confirm();
        }
      });
    },
    createNewStage() {
      this.editTesting('new');
    }
  },
  beforeMount() {
    scroll(0, 0);
    this.fetchAllTesting();
    this.$store.commit('setStageMode', 'test');
  },
  mounted() {
    document.addEventListener('createNewStage', this.createNewStage);
  },
  beforeUnmount() {
    document.removeEventListener('createNewStage', this.createNewStage);
  },
  beforeRouteLeave() {
    document.removeEventListener('createNewStage', this.createNewStage);
  }
}
</script>

