<template>
  <div class="container">
    <div class="table-container">
      <div class="table-header">
        <a-typography-title class="title" :level="3">{{
            currentRater ? 'Редактирование оценщика' : 'Список оценщиков'
          }}
        </a-typography-title>
        <div class="buttons-container" v-if="!currentRater">
          <a-dropdown :trigger="['click']" v-model:visible="viewColumns">
            <a-button>
              <template #icon>
                <unordered-list-outlined/>
              </template>
              Выбрать колонки
            </a-button>
            <template #overlay>
              <a-menu>
                <a-menu-item>
                  <a-checkbox-group style="display: flex; flex-direction: column; gap: 6px"
                                    v-model:value="selectedColumns" :options="columns.map(item => {
                      return {
                        value: item.key,
                        label: item.title
                      }
                    })">{{
                      item.title
                    }}
                  </a-checkbox-group>
                </a-menu-item>
                <a-menu-item>
                  <a-button @click="selectedColumns = []">Сбросить</a-button>
                </a-menu-item>
              </a-menu>
            </template>
          </a-dropdown>
        </div>
      </div>
      <div class="table-content" v-if="!currentRater">
        <a-table v-if="raters" :data-source="raters" :columns="checkColumns()" class="table">
          <template #bodyCell="{ column, record }">
            <div class="" v-if="column.key === 'actions'">
              <a-button @click="currentRater = record">Редактировать</a-button>
              <a-button v-if="currentRater" @click="saveRaterData">Сохранить</a-button>
            </div>
            <div class="" style="display: flex;align-items: center;justify-content: center"
                 v-if="column.key === 'status'">
              <a-tooltip v-if="record.status">
                <template #title>
                  {{
                    record.status === 'good' ? 'Отлично' :
                        record.status === 'mid' ? 'Нормально' :
                            record.status === 'bad' ? 'Плохо' :
                                ''
                  }}
                </template>
                <a-badge :color="record.status === 'good' ? 'green' :
                          record.status === 'mid' ? 'orange' :
                            record.status === 'bad' ? 'red' :
                              ''"/>
              </a-tooltip>

            </div>
            <div class="" v-if="column.key === 'name'">
              <div class="" v-if="currentRater && currentRater.id === record.id">
                <a-input v-model:value="currentRater.name"></a-input>
              </div>
              <div class="" v-else>{{ record.name }}</div>
            </div>
            <div class="" v-if="column.key === 'sex'">
              {{ record.sex === 'man' ? 'Мужской' : record.sex === 'girl' ? 'Женский' : '' }}
            </div>
            <div class="" v-if="column.key === 'createDate' || column.key === 'birthDate'">
              {{ record.birth ? dayjs(record.birth).format('DD-MM-YYYY') : '' }}
            </div>
          </template>
        </a-table>
      </div>
      <div class="rater-settings" v-if="currentRater">
        <div class="buttons-container">
          <a-button @click="currentRater = null">Назад</a-button>
          <a-button @click="saveRaterData">Сохранить</a-button>
        </div>
        <div class="settings-fields">
          <div class="settings-fields-el">
            <a-typography-title style="margin: 0" :level="5">Имя:</a-typography-title>
            <a-input v-model:value="currentRater.name"></a-input>
          </div>
          <div class="settings-fields-el">
            <a-typography-title style="margin: 0" :level="5">Статус:</a-typography-title>
            <a-select v-model:value="currentRater.status">
              <a-select-option value="good">Хорошо</a-select-option>
              <a-select-option value="mid">Нормально</a-select-option>
              <a-select-option value="bad">Плохо</a-select-option>
            </a-select>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {UnorderedListOutlined} from "@ant-design/icons-vue";
import dayjs from "dayjs";

export default {
  name: "RatersPage",
  components: {
    UnorderedListOutlined
  },
  computed: {
    raters() {
      return this.$store.getters.raters;
    }
  },
  data() {
    return {
      columns: [
        {
          title: 'Статус',
          key: 'status',
          dataIndex: 'status',
          width: 90,
        },
        {
          title: 'ФИО',
          key: 'name',
          dataIndex: 'name'
        },
        {
          title: 'Дата рождения',
          key: 'birthDate',
          dataIndex: 'birthDate'
        },
        {
          title: 'Город',
          key: 'city',
          dataIndex: 'city'
        },
        {
          title: 'Телефон',
          key: 'phone',
          dataIndex: 'phone'
        },
        {
          title: 'Почта',
          key: 'email',
          dataIndex: 'email'
        },
        {
          title: 'Дата регистрации',
          key: 'createDate',
          dataIndex: 'createDate'
        },
        {
          title: 'Действия',
          key: 'actions',
        },
      ],
      dateOptions: {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      },

      viewColumns: false,
      selectedColumns: [],
      page: 1,
      limit: 20,

      currentRater: null
    }
  },
  methods: {
    dayjs,
    checkColumns() {
      if (!this.selectedColumns.length) {
        return this.columns
      } else {
        return this.columns.filter(item => this.selectedColumns.includes(item.key));
      }
    },
    saveRaterData() {
      const rater = {
        id: this.currentRater.id,
        name: this.currentRater.name,
        status: this.currentRater.status,
      }
      this.$store.dispatch('saveRaterData', rater);
    }
  },
  beforeMount() {
    this.$store.dispatch('fetchRaters', {page: this.page, limit: this.limit});
  }
}
</script>

