<template>
  <a-card>
    <template #title>
      {{ stage.order }}
      {{
        stage.type === 'test' ? 'Тестирование' : stage.type === 'form' ? 'Анкета' : stage.type === 'interview' ? 'Видео-интервью' : stage.type === 'exercise' ? 'Практическое задание' : stage.type === 'stream' ? 'Трансляция' : null
      }}
    </template>
    <template #cover>
      <a-tag color="red" v-if="new Date()>new Date(stage.endDate)">
        <template #icon>
          <check-circle-outlined/>
        </template>
        завершено
      </a-tag>
      <a-tag color="processing"
             v-if="new Date(stage.startDate)<new Date()&&new Date() < new Date(stage.endDate)">
        <template #icon>
          <sync-outlined/>
        </template>
        в процессе
      </a-tag>
      <a-tag v-if="new Date(stage.startDate) > new Date()">
        <template #icon>
          <minus-circle-outlined/>
        </template>
        не начался
      </a-tag>
    </template>

    <a-card-meta>
      <template #description>
        {{
          new Date(stage.startDate).toLocaleDateString('ru-RU', dateOptions) + ' - ' + new Date(stage.endDate).toLocaleDateString('ru-RU', dateOptions)
        }}
      </template>
    </a-card-meta>

    <template #actions>
      <div class="" style="display: flex; justify-content: center; gap: 10px">
        <a-button shape="round" type="primary" @click="$emit('edit',stage)">Редактировать</a-button>
        <a-button shape="round" type="primary" danger @click="$emit('delete',stage.order)">Удалить</a-button>
      </div>

    </template>
  </a-card>
</template>

<script>
import {MinusCircleOutlined, SyncOutlined, CheckCircleOutlined} from "@ant-design/icons-vue";


export default {
  components: {MinusCircleOutlined, SyncOutlined, CheckCircleOutlined},
  name: "StageCard",
  props: ['stage', 'index'],
  data() {
    return {
      dateOptions: {
        month: 'long', day: 'numeric'
      }
    }
  }
}
</script>

