<script>
import {mapGetters} from "vuex";
import {
  UploadOutlined,
  CheckSquareOutlined,
  ArrowLeftOutlined,
  SmileOutlined,
  MehOutlined,
  FrownOutlined,
} from "@ant-design/icons-vue";
import Cookies from "js-cookie";
import cities from "@/lib/cities.JSON"
import dayjs from "dayjs";
import {errorHandler} from "@/lib/errorHandler";
import locale from "ant-design-vue/es/date-picker/locale/ru_RU";
import "dayjs/locale/ru";


export default {
  name: "FormPage",
  props: ["formId", "eventId", "passingId", "role"],
  components: {
    UploadOutlined,
    CheckSquareOutlined,
    ArrowLeftOutlined,
    SmileOutlined,
    MehOutlined,
    FrownOutlined,
  },
  computed: {
    ...mapGetters(['currentStage', 'passingTest', 'testingScore', 'currentBlank', 'passingBlank', 'passingBlankAllScore']),
    isNotAllPassedRequired() {
      return this.currentStage?.questions?.map(question => {
        if (question.required) {
          const index = this.passingTest?.testingPassing?.answers?.map(item => item.questionId).indexOf(question.id);
          if (index >= 0) {
            switch (question.type) {
              case 'single':
                return !!(this.passingTest?.testingPassing?.answers[index]?.answers && this.passingTest?.testingPassing?.answers[index]?.answers !== 'null');
              case 'multi':
                return !!(this.passingTest?.testingPassing?.answers[index]?.answers && this.passingTest?.testingPassing?.answers[index]?.answers !== 'null' && this.passingTest?.testingPassing?.answers[index]?.answers.length );
              case 'open':
                return !!(this.passingTest?.testingPassing?.answers[index]?.openAnswer && this.passingTest?.testingPassing?.answers[index]?.openAnswer !== 'null');
              case 'date':
                return !!(this.passingTest?.testingPassing?.answers[index]?.openAnswer && this.passingTest?.testingPassing?.answers[index]?.openAnswer !== 'null');
              case 'file':
                return !!(this.passingTest?.testingPassing?.answers[index]?.attachmentAnswerFile && this.passingTest?.testingPassing?.answers[index]?.attachmentAnswerFile !== 'null');
              case 'select':
                return !!(this.passingTest?.testingPassing?.answers[index]?.answers && this.passingTest?.testingPassing?.answers[index]?.answers !== 'null')
              case 'text':
                return !!(this.passingTest?.testingPassing?.answers[index]?.openAnswer && this.passingTest?.testingPassing?.answers[index]?.openAnswer !== 'null');
              case 'cities':
                return !!(this.passingTest?.testingPassing?.answers[index]?.openAnswer && this.passingTest?.testingPassing?.answers[index]?.openAnswer !== 'null');
              default:
                return false;
            }
          }
          return false;
        }
        return true;
      })
    }

  },
  data() {
    return {
      dateOptions: {
        month: "long",
        day: "numeric",
      },
      files: [],
      cities,

      viewBlank: true,
      disabledButton: false,
      viewFormModal: false,

      locale
    };
  },

  methods: {
    getAnswerDate(item) {
      const index = this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(item.id);
      if (index >= 0) {
        return (this.passingTest.testingPassing.answers[index].openAnswer && this.passingTest.testingPassing.answers[index].openAnswer !== 'null') ? dayjs(this.passingTest.testingPassing.answers[index].openAnswer) : null;
      }
      return null;
    },
    dayjs,
    changFile(ref, questionId) {
      let file = ref.files[ref.files.length - 1];
      this.files.push({file, questionId});
      this.$store.commit("pickAnswer", {
        questionId,
        answerId: URL.createObjectURL(file),
        type: "file",
      });
    },

    async closeModal() {
      if (this.currentBlank && !this.viewBlank) {
        this.viewBlank = true;
        scroll(0, 0);
        return;
      }

      if (
          this.passingTest.testingPassing.finalScore != null &&
          this.role === "rater"
      ) {
        this.$router.go(-1);
      } else {
        if (this.files && this.files.length) {
          for (let i = 0; i < this.files.length; i++) {
            await this.$store
                .dispatch("loadFile", this.files[i].file)
                .then((url) => {
                  this.$store.commit("pickAnswer", {
                    questionId: this.files[i].questionId,
                    answerId: url,
                    type: "file",
                  });
                });
          }
        }

        this.disabledButton = true;
        await this.$store.dispatch("savePassingTesting", {role: this.role});
      }
    },

    async saveFrom() {
      if (this.currentBlank && !this.viewBlank) {
        this.viewBlank = true;
        scroll(0, 0);
        return;
      }

      if (this.passingTest.testingPassing.finalScore != null && this.role === "rater") {
        this.$router.go(-1);
      } else {

        if (this.isNotAllPassedRequired?.includes(false)) {
          const msg = this.isNotAllPassedRequired?.map((item, index) => {
            return item ? null : index + 1
          })?.filter(item => item)?.join(',');


          errorHandler({name: 'notAllPass', msg});
          return
        }


        if (this.files && this.files.length) {
          for (let i = 0; i < this.files.length; i++) {
            await this.$store
                .dispatch("loadFile", this.files[i].file)
                .then((url) => {
                  this.$store.commit("pickAnswer", {
                    questionId: this.files[i].questionId,
                    answerId: url,
                    type: "file",
                  });
                });
          }
        }

        if (
            this.formId === "d14b27a9-63dd-48f7-a1ed-dff0cf6ceb69" &&
            this.role !== "rater"
        ) {
          if (!this.viewFormModal) {
            this.viewFormModal = true;
            return;
          }
        }


        this.disabledButton = true;
        await this.$store.dispatch("savePassingTesting", {role: this.role});
      }
    },
    setColor(e, color) {
      if (this.passingTest.testingPassing.finalScore !== null) {
        e.preventDefault();
        return false;
      }
      this.$store.commit("setBlankColor", color);
    },
  },
  beforeMount() {
    scroll(0, 0);
    let passingTestingId = Cookies.get("testingPassingId");
    if (this.role === "rater") {
      if (passingTestingId && passingTestingId !== "null") {
        this.$store.commit("setTestingPassingId", passingTestingId);
      } else {
        this.$router.go(-1);
      }
    }
    this.$store.dispatch("fetchEventsTesting", {
      testingId: this.formId,
      eventId: this.eventId,
      passingId: this.passingId,
      role: this.role,
      isPassTesting: true,
    });
  },
  beforeRouteLeave() {
    this.$store.commit("clearPassing");
    Cookies.remove("testingPassingId");
    this.$store.commit("setPassingForCVS", null);
  },
};
</script>

