<template>
  <loading :active="loader"
           :is-full-page="true"/>
  <div class="editor-container">
    <a-button shape="round" type="primary" ghost @click="$router.go(-1)">Назад</a-button>
    <a-tabs v-model:activeKey="activeKey">
      <a-tab-pane key="1" v-if="currentTesting"
                  :tab="mode === 'test' ? 'Тестирование' : mode === 'exercise' ? 'Практическое задание' : mode === 'form' ? 'Анкета' : ''">
        <div class="editor-filling">
          <div class="editor-element">
            <a-typography-title :level="5">Название:</a-typography-title>
            <a-input placeholder="Название" :value="currentTesting.title"
                     @change="e=>$store.commit('setTestingTitle',e.target.value)"></a-input>
          </div>
          <div class="editor-element">
            <a-typography-title :level="5">Описание:</a-typography-title>
            <a-textarea placeholder="Описание" :value="currentTesting.description" :rows="6"
                        @change="e=>$store.commit('setTestingDescription',e.target.value)"></a-textarea>
          </div>
          <div class="editor-element">
            <a-typography-title v-if="mode==='form'" :level="5">Краткое описание:</a-typography-title>
            <a-textarea placeholder="Краткое описание" :rows="4"
                        v-model:value="shortDescription"></a-textarea>
          </div>
          <div class="editor-element" v-if="mode==='test'">
            <a-typography-title :level="5">Время прохождения:</a-typography-title>
            <a-slider tooltipPlacement="top" :tipFormatter="(val)=>val + ' минут'" :value="currentTesting.limitSeconds"
                      @change="val=>$store.commit('setTimer',val)"/>
          </div>
          <div class="questions-list">
            <TestingQuestion v-for="(item,index) in currentTesting.questions.sort((a,b)=>a.order-b.order)"
                             :key="item.id" :mode="mode"
                             :q-index="index"
                             :questionsLength="currentTesting.questions.length"
                             :question="item"></TestingQuestion>
          </div>
          <a-button shape="round" style="width: 220px"
                    v-if="mode!=='exercise'||(mode==='exercise' && !currentTesting.questions.length)"
                    ghost @click="$store.commit('addQuestion')" type="primary">Добавить вопрос
          </a-button>
        </div>

      </a-tab-pane>
      <a-tab-pane key="2" v-if="testingId!=='new'&&currentBlank&&mode!=='test'" tab="Опросник" force-render>
        <div class="editor-filling pd-40">
          <div class="questions-list">
            <div class="tooltip-container">
              <a-typography-title :level="4">Текст для общей оценки</a-typography-title>
              <div class="tooltip-el">
                <a-typography>Текст для «отлично»</a-typography>
                <a-input @change="$store.commit('setBlankEstimationText',{val:$event.target.value,key:'successText'})"
                         :value="currentBlank.successText">
                </a-input>
              </div>
              <div class="tooltip-el">
                <a-typography>Текст для «нормально»</a-typography>
                <a-input @change="$store.commit('setBlankEstimationText',{val:$event.target.value,key:'mediumText'})"
                         :value="currentBlank.mediumText">
                </a-input>
              </div>
              <div class="tooltip-el">
                <a-typography>Текст для «плохо»
                </a-typography>
                <a-input @change="$store.commit('setBlankEstimationText',{val:$event.target.value,key:'lowText'})"
                         :value="currentBlank.lowText">
                </a-input>
              </div>

            </div>
            <a-typography-title :level="4">Критерии оценки</a-typography-title>


            <BlankElement v-for="(item,index) in currentBlank.blankCriterias"
                          v-show="!item.delete"
                          :key="item.id"
                          :index="index"
                          :blank="item"></BlankElement>
          </div>
          <a-button shape="round" style="width: 250px;margin: 0 auto" ghost @click="$store.commit('addBlank')"
                    type="primary">
            <template #icon>
              <plus-outlined></plus-outlined>
            </template>
            Добавить критерий
          </a-button>
        </div>

      </a-tab-pane>
    </a-tabs>
    <div class="save-button-block">
      <a-button shape="round" type="primary"
                @click="saveTesting">Сохранить
      </a-button>

    </div>
  </div>
</template>

<script>
import TestingQuestion from "@/components/testing/TestingQuestion";
import BlankElement from "@/components/testing/BlankElement";
import {mapGetters} from "vuex";
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/css/index.css';
import {errorHandler} from "@/lib/errorHandler";
import {PlusOutlined} from "@ant-design/icons-vue";

export default {
  name: "TestingEditorPage",
  computed: {
    ...mapGetters(['currentTesting', 'loader', 'currentBlank']),
    shortDescription: {
      get() {
        if (this.currentTesting?.sideData?.shortDescription) {
          return this.currentTesting?.sideData?.shortDescription;
        }
        return null
      },
      set(val) {
        this.$store.commit('setTestingShortDescription', val);
      }
    }
  },
  components: {TestingQuestion, Loading, BlankElement, PlusOutlined},
  props: ['testingId', 'mode'],
  beforeMount() {
    this.$store.dispatch('fetchCurrentTesting', {id: this.testingId, type: this.mode}).then(() => {
      if (this.testingId !== 'new') {
        this.$store.dispatch('fetchCurrentBlank', {id: this.testingId, edit: true});
      }
    });

  },
  data() {
    return {
      errText: 'Незаполненные данные :\n',
      activeKey: '1'
    }
  },
  methods: {
    checkTesting() {
      let approve = true;
      if (this.activeKey === '1') {
        if (!this.currentTesting.title || this.currentTesting.title.trim() === '') {
          this.errText += 'Название\n'
          approve = false;
        }
        if (!this.currentTesting.description || this.currentTesting.description.trim() === '') {
          this.errText += 'Описание\n'
          approve = false;
        }
        if (!this.currentTesting.questions.length) {
          this.errText += 'Недостаточно вопросов\n'
          approve = false;
        } else {
          for (let i = 0; i < this.currentTesting.questions.length; i++) {
            let question = this.currentTesting.questions[i]
            if (!question.type || question.type.trim() === '') {
              this.errText += `Вопрос ${i + 1} - тип\n`
              approve = false;
            } else {
              if ((!question.body || question.body.trim() === '') && question.type !== 'conformity') {
                this.errText += `Вопрос ${i + 1} - описание вопроса\n`
                approve = false;
              }
              if ((question.type === 'single' || question.type === 'multi' || question.type === 'select') && question.answers.length < 2) {
                this.errText += `Вопрос ${i + 1} - недостаточно ответов\n`
                approve = false;
              } else {
                for (let j = 0; j < question.answers.length; j++) {
                  let answer = question.answers[j];
                  if (!answer.body || answer.body.trim() === '') {
                    this.errText += `Вопрос ${i + 1} / Ответ ${j + 1} - текст ответа\n`
                    approve = false;
                  }
                  if (question.type === 'conformity') {
                    if (((answer.matchingFileUrl === 'null' && answer.body === 'null') || (answer.matchingFileUrl === 'null' && !answer.body || answer.body.trim() === '')) && !answer.matchingCorrectBody || answer.matchingCorrectBody.trim() === '') {
                      this.errText += `Вопрос ${i + 1} - сопоставление незаполненно\n`
                      approve = false;
                    }
                  }
                }

              }
            }
          }
        }
      } else {
        for (let i = 0; i < this.currentBlank.blankCriterias.length; i++) {
          if (!this.currentBlank.blankCriterias[i].description || this.currentBlank.blankCriterias[i].description.trim() === '') {
            this.errText += `Критерий №${i + 1}: описание`
            approve = false;
          }
          if (!this.currentBlank.blankCriterias[i].maxScore) {
            this.errText += `Критерий №${i + 1}: количество баллов`
            approve = false;
          }
        }
      }


      return approve
    },

    saveTesting() {
      if (!this.checkTesting()) {
        errorHandler({name: 'testEditor', status: 300, meta: {body: this.errText}});
        this.errText = 'Незаполненные данные :\n'
        return;
      }
      if (this.activeKey === '1') {
        this.$store.dispatch('saveTesting');
      } else {
        this.$store.dispatch('saveBlank');
      }

    },
  },
  beforeRouteLeave() {
    this.$store.commit('setCurrentBlank', {data: null})
    this.$store.commit('setCurrentTesting', null)
  }
}
</script>

<style scoped>

.editor-container {
  margin-top: 28px;
  padding: 0 17px 40px 17px;
}

.editor-filling {
  margin: 0px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.questions-list {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.editor-element {
  max-width: 800px;
  width: 100%;
}

.save-button-block {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tooltip-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.tooltip-el {
  width: 50%;
}

@media (max-width: 700px) {
  .editor-container {
    margin-top: 10px;
    padding: 0 10px;
    gap: 20px;
    margin-bottom: 40px;
  }

  .questions-list {
    margin: 10px 0;
  }
}

.pd-40 {
  padding: 40px;
}
</style>