import {fetchAllInterviews, fetchInterviewById, saveInterview} from "@/store/interview/serverInterationMethods";
import dayjs from "dayjs";
import {errorHandler} from "@/lib/errorHandler";

export const interview = {
    actions: {
        async fetchInterviewById({getters, commit}, id) {
            let data;

            if (id === 'new') {
                data = {
                    startDate: null,
                    endDate: null,
                    slots: []
                }

            } else {
                const response = await fetchInterviewById(getters.token, id);
                data = response.data;
            }
            commit('setInterview', {data, user: getters.user});
        },
        async saveInterview({commit, getters}, {id, role}) {
            commit('parseInterviewForSave', {id});
            const {status} = await saveInterview(getters.token, getters.saveInterviewObject);
            errorHandler({name: 'interview', status, meta: {role}});
        },
        async fetchAllInterviews({commit, getters}) {
            const {data} = await fetchAllInterviews(getters.token, getters.interview);
            commit('setAllInterviews', data);
        },
        async bookInterviewSlot({getters, commit}, id) {
            commit('bookInterviewSlot', {user: getters.user, id});
        }

    },
    mutations: {
        setInterview(state, {data, user}) {
            state.interview = data;
            const slots = [];
            state.interview['slots'] = state.interview.interviewStageSlots || state.interview.slots;
            if (state.interview.slots) {
                for (let i = 0; i < state.interview.slots.length; i++) {
                    let add = true;
                    for (let j = 0; j < slots.length; j++) {
                        if (dayjs(state.interview.slots[i].day).unix() === slots[j].day.unix()) {
                            add = false;
                            slots[j].times.push({
                                id: state.interview.slots[i].id ? state.interview.slots[i].id : null,
                                startTime: dayjs(state.interview.slots[i].startTime),
                                endTime: dayjs(state.interview.slots[i].endTime),
                                slotsCount: state.interview.slots[i].slotsCount,
                                usersInSlots: state.interview.slots[i].usersInSlots,
                            });
                            let bookedInterviewIndex = state.interview.slots[i].usersInSlots.map(item => item.clientId).indexOf(user.id);
                            if (bookedInterviewIndex !== -1) {
                                state.interviewBookedId = state.interview.slots[i].id;
                                state.userInterviewPassed = true;
                            }
                        }
                    }
                    if (add) {
                        slots.push({
                            day: dayjs(state.interview.slots[i].day),
                            times: [
                                {
                                    id: state.interview.slots[i].id ? state.interview.slots[i].id : null,
                                    startTime: dayjs(state.interview.slots[i].startTime),
                                    endTime: dayjs(state.interview.slots[i].endTime),
                                    slotsCount: state.interview.slots[i].slotsCount,
                                    usersInSlots: state.interview.slots[i].usersInSlots
                                }
                            ]
                        });
                        let bookedInterviewIndex = state.interview.slots[i].usersInSlots.map(item => item.clientId).indexOf(user.id);
                        if (bookedInterviewIndex !== -1) {
                            state.interviewBookedId = state.interview.slots[i].id;
                            state.userInterviewPassed = true;
                        }
                    }
                }
            }
            state.interview.slots = slots;
        },
        setInterviewDates(state, data) {
            state.interview.startDate = dayjs(data[0]).toDate();
            state.interview.endDate = dayjs(data[1]).toDate();
        },
        addInterviewSlot(state) {
            state.interview.slots.push({
                day: dayjs().startOf('day'),
                times: []
            });
        },
        setSlotsDay(state, {val, index}) {
            state.interview.slots[index].day = val ? dayjs(val).startOf('day') : null;
        },
        addInterviewTimes(state, index) {
            state.interview.slots[index].times.push({
                    id: null,
                    startTime: dayjs(),
                    endTime: dayjs(),
                    slotsCount: 1
                }
            );
        },
        setTimesParams(state, {key, val, dayIndex, timeIndex}) {
            state.interview.slots[dayIndex].times[timeIndex][key] = val;
        },
        parseInterviewForSave(state, {id}) {
            state.saveInterviewObject = JSON.parse(JSON.stringify(state.interview));
            state.saveInterviewObject['id'] = id !== 'new' ? id : null;
            const slots = [];
            for (let i = 0; i < state.saveInterviewObject.slots.length; i++) {
                for (let j = 0; j < state.saveInterviewObject.slots[i].times.length; j++) {
                    slots.push({
                        id: state.saveInterviewObject.slots[i].times[j].id ? state.saveInterviewObject.slots[i].times[j].id : null, // nullable
                        day: dayjs(state.saveInterviewObject.slots[i].day).toDate(),
                        startTime: dayjs(state.saveInterviewObject.slots[i].times[j].startTime).toDate(), // кол-во минут от начала дня
                        endTime: dayjs(state.saveInterviewObject.slots[i].times[j].endTime).toDate(),
                        slotsCount: state.saveInterviewObject.slots[i].times[j].slotsCount,
                        usersInSlots: state.saveInterviewObject.slots[i].times[j].usersInSlots,

                    })
                }
            }
            state.saveInterviewObject.slots = slots;
        },
        setAllInterviews(state, data) {
            state.allInterviews = data;
        },

        bookInterviewSlot(state, {user, id}) {
            for (let i = 0; i < state.interview.slots.length; i++) {
                let isBreak = false
                for (let j = 0; j < state.interview.slots[i].times.length; j++) {
                    if (state.interview.slots[i].times[j].id === id) {
                        let userBookedIndex = state.interview.slots[i].times[j].usersInSlots.map(item => item.clientId).indexOf(user.id)
                        if (userBookedIndex !== -1) {
                            state.interview.slots[i].times[j].usersInSlots.splice(userBookedIndex, 1);
                            state.interviewBookedId = null;
                            isBreak = true;
                            break
                        }

                        state.interview.slots[i].times[j].usersInSlots.push({
                            id: null,
                            clientId: user.id,
                            interviewerId: null
                        });
                        state.interviewBookedId = state.interview.slots[i].times[j].id;
                        isBreak = true;
                        break;
                    }
                    if (isBreak) {
                        break;
                    }
                }
            }
        },
        setInterviewDescription(state, {slot, times, user, description, interviewerId}) {
            state.interview.slots.map(slotItem => {
                if (slotItem.day === slot.day) {
                    slotItem.times.map(timesItem => {
                        if (timesItem.id === times.id) {
                            timesItem.usersInSlots.map(userItem => {
                                if (userItem.clientId === user.clientId) {
                                    userItem.interviewerId = interviewerId;
                                    userItem['description'] = description;
                                }
                            })
                        }
                    })
                }
            })
        },
        clearInterview(state) {
            state.interview = null;
            state.allInterviews = null;
            state.interviewBookedId = null;
            state.saveInterviewObject = null;
            state.userInterviewPassed = false
        }
    },
    state: {
        interview: null,
        allInterviews: null,
        interviewBookedId: null,
        saveInterviewObject: null,
        userInterviewPassed: false
    },
    getters: {
        interview: state => state.interview,
        allInterviews: state => state.allInterviews,
        interviewBookedId: state => state.interviewBookedId,
        saveInterviewObject: state => state.saveInterviewObject,
        userInterviewPassed: state => state.userInterviewPassed,

    }
}