<script>
import locale from "ant-design-vue/es/date-picker/locale/ru_RU";
import dayjs from "dayjs";
import cities from "@/lib/cities.JSON";
import {IMaskComponent} from "vue-imask";
import {errorHandler} from "@/lib/errorHandler";

export default {
  name: "CreateMemberPage",
  props: ['id'],
  components: {
    IMaskComponent
  },
  computed: {
    currentUser() {
      return this.$store.getters.currentUser;
    }
  },
  data() {
    return {
      userData: null,
      file: null,
      locale,
      dayjs,
      cities,
      mask: "+{7}(000)000-00-00",

      unmaskedPhone: null,

      firstLoad: true,

      viewChangePassword: false,


      changePassword: {
        newPassword: null,
        newPasswordConfirm: null
      }
    }
  },
  watch: {
    currentUser(val) {
      if (this.firstLoad) {
        this.userData = JSON.parse(JSON.stringify(val));
        if (this.id !== 'new') {
          this.splitName();
        }
        this.userData.birth = dayjs(
            this.userData.birth ? this.userData.birth : new Date()
        );
        this.firstLoad = false;
      }
    }
  },
  methods: {
    splitName() {
      this.userData.name = this.userData.name.split(" ");
      if (this.userData.name.length < 3) {
        for (let i = 0; i < 3 - this.userData.name.length; i++) {
          this.userData.name.push("");
        }
      }
    },
    loadFile() {
      let el = this.$refs.file;
      this.file = el.files[el.files.length - 1];
      this.userData.avatarUrl = URL.createObjectURL(this.file);
    },

    saveNewPassword() {
      const msg = [];
      if (!this.changePassword.newPassword) {
        msg.push('новый пароль')
      }

      if (!this.changePassword.newPasswordConfirm) {
        msg.push('подтверждение пароля')
      }

      if (msg.length) {
        errorHandler({name: 'requiredField', msg: msg.join(',')});
        return;
      }

      if (this.changePassword.newPassword !== this.changePassword.newPasswordConfirm) {
        errorHandler({name: 'incorrectPasswordEnter'});
        return;
      }


      let user = {
        id: this.id,
        name: this.currentUser.name,
        phone: this.currentUser.phone,
        city: this.currentUser.city,
        sex: this.currentUser.sex,
        birth: this.currentUser.birth,
        email: this.currentUser.email,
        password: this.changePassword.newPassword
      };

      this.$store.dispatch('saveMemberData', {data: user, id: this.id}).then(() => {
        this.viewChangePassword = false;
      })

    },

    async saveMember() {
      if (this.file) {
        await this.$store
            .dispatch("loadFile", this.file)
            .then((url) => (this.userData.avatarUrl = url));
      }


      for (let i = 0; i < this.userData.name.length; i++) {
        this.userData.name[i] = this.userData.name[i].trim();
      }

      let user = {
        id: this.id !== 'new' ? this.id : null,
        name: this.userData.name.join(" "),
        phone: this.unmaskedPhone,
        city: this.userData.city,
        sex: this.userData.sex,
        birth: this.userData.birth,
        email: this.userData.email
      };

      if (user.email && !/^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(user.email)) {
        errorHandler({name: 'incorrectEmail'})
        return;
      }

      this.$store.dispatch('saveMemberData', {id: this.id, data: user}).then(status => {
        if (status === 200) {
          this.$router.go(-1);
        }
      });
    }
  },
  beforeMount() {
    this.$store.dispatch('fetchUserById', this.id);
  },
  mounted() {
    document.addEventListener('saveMember', this.saveMember);
  },
  beforeUnmount() {
    this.$store.commit('clearMember')
    document.removeEventListener('saveMember', this.saveMember);
  },
  beforeRouteLeave() {
    this.$store.commit('clearMember')
    document.removeEventListener('saveMember', this.saveMember);
  }
}
</script>

