<script>
import Cookies from "js-cookie";
import "../../public/css/main-page.css";

import Vue3Autocounter from "vue3-autocounter";

import { Carousel, Navigation, Slide, Pagination } from "vue3-carousel";
import "vue3-carousel/dist/carousel.css";

import VideoWrapper from "@/components/landing/VideoWrapper.vue";
import { appConfig } from "@/lib/appConfig";

export default {
  computed: {
    token() {
      return this.$store.getters.token;
    },
  },
  name: "MainPage",
  components: {
    Pagination,
    Carousel,
    Slide,
    Navigation,
    VideoWrapper,
    Vue3Autocounter,
  },
  data: () => ({
    menuOpen: false,
    formatText1: false,
    formatText2: true,
    formatText3: false,
    facesList1: false,
    facesList2: true,
    facesList3: false,

    currentBlockHover: 0,

    finishCounting: false,

    settings: {
      itemsToShow: 1,
      snapAlign: "center",
    },
    educationSettings: {
      wrapAround: true,
      itemsToShow: 1,
      touchDrag: true,
    },
    slides: [
      {
        text1: "Залим Маргушев ",
        info: "Блогер ",
        text2:
          " Залим живет в селе Залукодес в Кабардино-Балкарии. Занимается фермерством и ведет свой блог, в котором интересно показывает сельскую жизнь. Ведет общественную деятельность, эксперт по работе с молодежью. ",
        videoUrl: `${appConfig.domain}/file/student-zalim.mp4`,
        videoPoster: require("@/assets/homePage/video-poster/student-zalim.png"),
      },
      {
        text1: "Юлия Бондарева",
        info: "Блогер ",
        text2:
          "Решила, что должна своей работой нести в мир добро. После обучения в Мастерской новых медиа она создала видеопроект о социальных предпринимателях «Добрый бизнес».",
        videoUrl: `${appConfig.domain}/file/student-bond.mp4`,
        videoPoster: require("@/assets/homePage/video-poster/student-bond.png"),
      },
      {
        text1: "Илья Гогуа",
        info: "Журналист, ведущий стримов «Телега online»",
        text2:
          " Илья считает, что просто рассказывать новости не достаточно. Поэтому он говорит с аудиторией про кибербезопасность, развенчивает фейки и интересно рассказывает о политике. ",
        videoUrl: `${appConfig.domain}/file/student-gogu.mp4`,
        videoPoster: require("@/assets/homePage/video-poster/student-gogu.png"),
      },
      {
        text1: "Маргарита Жемчугова",
        info: "Главный редактор «Кавказ сегодня»",
        text2:
          " Маргарита решила, что пора разрушать мифы и стереотипы о Кавказе. И создала проект, чтобы рассказывать людям, как они могут изменить свою жизнь и жизнь общества – «Кавказ накормит».",
        videoUrl: `${appConfig.domain}/file/student-zhem.mp4`,
        videoPoster: require("@/assets/homePage/video-poster/student-zhem.png"),
      },
    ],
    education: [
      {
        videoUrl: `${appConfig.domain}/file/lesson-pr.mp4`,
        videoPoster: require("@/assets/homePage/video-poster/lesson-pr.png"),
      },
      {
        videoUrl: `${appConfig.domain}/file/lesson-future.mp4`,
        videoPoster: require("@/assets/homePage/video-poster/lesson-future.png"),
      },
      {
        videoUrl: `${appConfig.domain}/file/lesson-challenges.mp4`,
        videoPoster: require("@/assets/homePage/video-poster/lesson-challenges.png"),
      },
      // {
      //   videoUrl: `${appConfig.domain}/file/lesson-help.mp4`,
      //   videoPoster: require("@/assets/homePage/video-poster/lesson-help.png"),
      // },
    ],
    breakpoints: {
      400: {
        itemsToShow: 1,
      },
      1100: {
        itemsToShow: 1.69,
      },
    },
  }),
  mounted() {
    let observer = new IntersectionObserver(
      (entry) => {
        entry.forEach((change) => {
          if (change.isIntersecting && !this.finishCounting) {
            this.$refs.spec?.start();
            this.$refs.grant?.start();
            this.$refs.grantMobile?.start();
            this.$refs.region?.start();
            this.finishCounting = true;
          }
        });
      },
      {
        threshold: [0.5],
      }
    );
    observer.observe(document.getElementById("graduates"));

    let textObserverBlock = new IntersectionObserver(
      function (entry) {
        entry.forEach((change) => {
          if (change.isIntersecting) {
            switch (change.target.id) {
              case "partners-block":
                document
                  .getElementById("partners-imgs")
                  .classList.add("partners-show");
                break;
            }
          }
        });
      },
      {
        threshold: [0],
      }
    );
    textObserverBlock.observe(document.getElementById("partners-block"));
  },
  methods: {
    searchEvents() {
      if (this.token) {
        Cookies.set("search_name", this.name);
        this.$router.push({ name: "allEvents" });
      } else {
        this.$router.push({ name: "authorization" });
      }
    },
  },
};
</script>

