.items-wrap {
  position: relative;
  display: flex;
  overflow: hidden;
  user-select: none;
  background: #5ec9ca;
}

.items-wrap:before,
.items-wrap:after {
  content: "";
  height: 100%;
  top: 0;
  width: 13.25rem;
  position: absolute;
  z-index: 1;
  pointer-events: none;
}

.items-wrap:before {
  left: 0;
  background: linear-gradient(
    90deg,
    rgba(26, 32, 38, 1) 0%,
    rgba(94, 201, 202, 0) 100%
  );
}

.items-wrap:after {
  right: 0;
  background: linear-gradient(
    90deg,
    rgba(94, 201, 202, 0) 0%,
    rgba(26, 32, 38, 1) 100%
  );
}

.items {
  flex-shrink: 0;
  display: flex;
  min-width: 100%;
  align-items: center;
}

.marquee {
  animation: scroll 20s linear infinite;
}

/* .items-wrap:hover .marquee {
  animation-play-state: paused;
} */

@keyframes scroll {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(calc(-100%));
  }
}

