  width: 100%;
  position: relative;
  height: auto;
  background: #dcd1fa;
}

.carousel__pagination {
  position: absolute;
  left: 0;
  bottom: 0;
}

.reviews :deep(.carousel__track) {
  margin-bottom: 0 !important;
}

.reviews :deep(.carousel__viewport) {
  overflow: visible !important;
}

.reviews :deep(.carousel__slide--next) {
  opacity: 0;
}

.reviews :deep(.carousel__slide) {
  transition: all 1s ease;
}

.reviews :deep(.carousel__slide--prev) {
  opacity: 0;
}

.reviews :deep(.carousel__next) {
  margin: 0;
  right: -3rem;
  width: 3rem;
  height: 3rem;
  z-index: 6;
}

.reviews :deep(.carousel__prev) {
  left: -3rem;
  margin: 0;
  width: 3rem;
  height: 3rem;
  z-index: 6;
}

.reviews :deep(.carousel__next svg) {
  width: 100%;
  height: 100%;
  fill: #ffffff;
}

.reviews :deep(.carousel__prev svg) {
  width: 100%;
  height: 100%;
  fill: #dfdfe0;
}

.reviews :deep(.carousel__pagination-button::after) {
  width: 0.5rem;
  height: 0.5rem;
  background: #fff;
}

.reviews :deep(.carousel__pagination-button--active::after) {
  width: 2rem;
  height: 0.5rem;
  background: #3f1c9d;
}

.carousel__decoration {
  position: absolute;
}

.carousel__decoration-1 {
