.education {
  margin-top: 9rem;
}

.education__title {
  color: #19171c;
  text-align: center;
  font-size: 2.5rem;
  font-weight: 600;
  line-height: 130%;
}

.education-carousel {
  margin-top: 3.12rem;
}

.education iframe {
  height: 100%;
}

.education .carousel__viewport {
  perspective: 138.89vw;
}

.education .carousel__track {
  transform-style: preserve-3d;
}

.education :deep(.carousel__track) {
  padding-bottom: 1rem !important;
}

.education .carousel__slide--sliding {
  transition: 0.5s;
}

.education .carousel__slide {
  transform: rotateY(-20deg) scale(0.29);
  height: 24.53656rem;
  width: 46.0625rem;
  opacity: 0;
}

.education .carousel__slide--active ~ .carousel__slide {
  transform: rotateY(20deg) scale(0.29);
  opacity: 1;
}

.education .carousel__slide--prev {
  transform: rotateY(-10deg) scale(0.29);
  left: 13.7rem;
  opacity: 1;
}

.education .carousel__slide--next {
  transform: rotateY(10deg) scale(0.29);
  left: -13.7rem;
  opacity: 1;
}

.education .carousel__slide--active {
  transform: rotateY(0) scale(1);
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 1;
}

.education :deep(.carousel__next) {
  margin: 0;
  right: -3rem;
  width: 3rem;
  height: 3rem;
  z-index: 6;
}

