    <StreamModalNew v-if="false">
    </StreamModalNew>
    <a-modal :width="windowWidth <= 850 ? '100%' : '50%'" :title="stream?.title" v-if="stream"
      v-model:visible="viewStream" class="stream-modal" :ok-button-props="{ hidden: true }" :cancel-text="'Закрыть'">
      <a-typography-text :level="2">{{ stream?.description }}
      </a-typography-text>
      <div class="stream-container" v-html="stream?.body" id="stream-container"></div>
    </a-modal>
    <div class="photo-block" :style="{ backgroundImage: 'url(' + currentEvents.previewUrl + ')' }" v-if="false">
      <div class="title-block">
        <a-typography-title style="color: white" :level="4" class="caption-title">
          {{
            new Date(currentEvents.startDate).toLocaleDateString(
              "ru-RU",
              dateOptions
            ) +
            " - " +
            new Date(currentEvents.endDate).toLocaleDateString(
              "ru-RU",
              dateOptions
            )
          }}
        </a-typography-title>
        <a-typography-title style="color: white" :level="1" class="caption-description">
          {{ currentEvents.title }}
        </a-typography-title>
        <a-typography-text style="color: white" class="caption-text">
          {{ currentEvents.description }}
        </a-typography-text>
      </div>
    </div>
    <a-button shape="round" @click="viewStatistic = true" v-if="role === 'admin'" style="display: none">Статистика
      прохождения
    </a-button>
    <div class="steps-block">
      <a-timeline>
        <div class="time-line-element" v-for="item in currentEvents.stages.sort((a, b) => a.order - b.order)"
          :key="item.id" :class="{
            'event-disabled': new Date(item.startDate) > new Date(),
            'event-process':
              new Date(item.startDate) < new Date() &&
              new Date() < new Date(item.endDate) &&
              getStagePrecent(item) !== 100,
            'event-sucses':
              new Date() > new Date(item.endDate) ||
              getStagePrecent(item) === 100,
          }">
          <a-timeline-item style="margin-left: auto">
            <div class="time-line-filling">
              <p class="date">
                {{
                  new Date(item.startDate).toLocaleDateString(
                    "ru-RU",
                    dateOptions
                  ) +
                  " - " +
                  new Date(item.endDate).toLocaleDateString(
                    "ru-RU",
                    dateOptions
                  )
                }}
              </p>
              <div class="timeline-title">
                <fund-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'test'" />
                <video-camera-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'interview'" />
                <schedule-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'form'" />
                <profile-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'exercise'" />
                <customer-service-outlined style="color: #5ec9ca; font-size: 16px" v-if="item.type === 'stream'" />
                <a-typography-title :level="5">
                  {{
                    item.type === "test"
                    ? (item?.testing?.title ? `${item?.testing?.title}` : 'Тестирование')
                    : item.type === "form"
                      ? (item?.testing?.title ? `${item?.testing?.title}` : 'Анкета')
                      : item.type === "interview"
                        ? "Видео-интервью"
                        : item.type === "exercise"
                          ? "Практическое задание"
                          : item.type === "stream"
                            ? "Трансляция"
                            : null
                  }}
                </a-typography-title>
                <!-- <div
                    class="status"
                  
                    v-if="
                    checkScore(item) !== 'score' && checkScore(item) !== 'pass'
                  "
                >
                  <a-tag color="red" v-if="new Date() > new Date(item.endDate)">
                    <template #icon>
                      <check-circle-outlined/>
                    </template>
                    завершено
                  </a-tag>
                  <a-tag
                      color="processing"
                      v-if="
                      new Date(item.startDate) < new Date() &&
                      new Date() < new Date(item.endDate)
                    "
                  >
                    <template #icon>
                      <sync-outlined/>
                    </template>
                    в процессе
                  </a-tag>
                  <a-tag v-if="new Date(item.startDate) > new Date()">
                    <template #icon>
                      <minus-circle-outlined/>
                    </template>
                    не начался
                  </a-tag>
                </div>
                <div class="" v-else>
                  <a-tag color="#87d068" v-if="checkScore(item) === 'score'"
                  >
                    <template #icon>
                      <check-outlined/>
                    </template>
                    Пройдено
                  </a-tag
                  >
                  <a-tag color="#108ee9" v-if="checkScore(item) === 'pass'">
                    <template #icon>
                      <field-time-outlined/>
                    </template>
                    Ожидает проверки
                  </a-tag
                  >
                </div> -->
              </div>
              <a-typography-text v-if="item?.type === 'form' && getShortDescription(item)" type="secondary"
                class="text text-second">
                {{
                  textSymbolLimit(getShortDescription(item), viewMoreDescription === item.id ? getShortDescription()?.length
                    : descriptionSymbolLimit)
                }}
                <button style="font-weight: 500" v-if="getShortDescription(item)?.length > descriptionSymbolLimit"
                  @click="viewMoreDescription = (viewMoreDescription === item.id ? null : item.id)">
                  {{ viewMoreDescription === item.id ? 'Скрыть' : 'Показать больше' }}
                </button>
              </a-typography-text>

              <a-typography-text v-if="(item.type === 'exercise' || item.type === 'test') &&
                checkScore(item) === 'score'
                " type="secondary" class="text">
                Правильность прохождения:
                {{
                  Math.trunc(
                    (eventPassing.testingPassing[
                      eventPassing.testingPassing
                        .map((pass) => pass.testingId)
                        .indexOf(item.testId)
                    ].finalScore /
                      item.maxScore) *
                    100
                  )
                }}%
              </a-typography-text>
              <br />

              <a-button :class="{
                    blocked: [
                      'Не началось',
                      'Пройдено',
                      'Ожидает проверки',
                      'Завершено',
                      'Не пройдено',
                      'Заполненo',
                    ].includes(getButtonText(item)),
                  }" @click="goToStage(item)">
                {{ getButtonText(item) }}
              </a-button>
            </div>
            <template #dot>
              <a-progress :stroke-width="4" :format="() => item.order" :stroke-color="'#0fcbcc'" type="circle"
                status="active" :percent="getStagePrecent(item)" :width="38" />
            </template>
          </a-timeline-item>
        </div>

        <div class="time-line-element" :class="{
          'event-unapproved': eventPassing?.passingStatus !== 'passed',
          'event-sucses': eventPassing?.passingStatus === 'passed',
        }">
          <a-timeline-item style="margin-left: auto">
            <template #dot>
              <a-progress :stroke-width="4" :stroke-color="eventPassing?.passingStatus === 'passed'
                ? '#0fcbcc'
                : '#808080'
                " type="circle" :percent="100" :width="38" />
            </template>
          </a-timeline-item>
        </div>
      </a-timeline>
    </div>
    <div class="decorations desktop">
      <img src="../../assets/icons/decoration-1.svg" alt="" class="decoration-1" />
      <img src="../../assets/icons/decoration-3.svg" alt="" class="decoration-3" />
    </div>
    <div class="decorations mobile">
      <img src="../../assets/icons/decoration-1-mob.svg" alt="" class="decoration-1" />
      <img src="../../assets/icons/decoration-2-mob.svg" alt="" class="decoration-2" />
      <img src="../../assets/icons/decoration-3-mob.svg" alt="" class="decoration-3" />
    </div>
  </div>
  <check-outlined v-if="false" />
</template>

<script>
import { mapGetters } from "vuex";
import {
  // FieldTimeOutlined,
  // CheckCircleOutlined,
  // SyncOutlined,
  // MinusCircleOutlined,
  CheckOutlined,
  FundOutlined,
  VideoCameraOutlined,
  ScheduleOutlined,
  ProfileOutlined,
  CustomerServiceOutlined,
} from "@ant-design/icons-vue";
import { errorHandler } from "@/lib/errorHandler";
import { textSymbolLimit } from "@/lib/textSymbolLimit";
import StreamModalNew from "@/components/stream/StreamModalNew.vue";
// import StatisticWindow from "@/components/events/StatisticWindow";

export default {
  name: "SeparateEventsPage",
  props: ["eventId"],
  components: {
    // StatisticWindow,
    // CheckCircleOutlined,
    // SyncOutlined,
    // MinusCircleOutlined,
    // FieldTimeOutlined,
    CheckOutlined,
    FundOutlined,
    VideoCameraOutlined,
    ScheduleOutlined,
    ProfileOutlined,
    CustomerServiceOutlined,
    StreamModalNew
  },
  computed: {
    ...mapGetters(["currentEvents", "eventPassing", "role", "stream"]),
  },
  watch: {
    viewStream: function (val) {
      if (!val) {
        this.$store.commit("setStreamData", null);
      }
    },
  },
  beforeMount() {
    scroll(0, 0);
    this.$store
      .dispatch("fetchCurrentEvents", this.eventId)
      .then(async (data) => {
        if (this.role !== 'admin') {
          if (!data || new Date() > new Date(data.endDate)) {
            this.$router.push({ name: "allEvents" });
            return;
          }
        }
        document.dispatchEvent(
          new CustomEvent("setTitle", { detail: data.title })
        );
        await this.$store.dispatch(
          "fetchEventPassing",
          data.eventPassings.length ? data.eventPassings[0].id : null
        );
      });
  },
  data() {
    return {
      CheckOutlined,
      dateOptions: {
        month: "long",
        day: "numeric",
      },
      viewStatistic: false,
      viewStream: false,
      windowWidth: window?.innerWidth,
      descriptionSymbolLimit: 120,
      viewMoreDescription: null
    };
  },
  methods: {
    textSymbolLimit,
    pickStream(stage) {
      this.$store.dispatch("fetchStreamById", stage.translationId).then(() => {
        this.viewStream = true;
      });
    },
    getShortDescription(item) {
      try {
        const sideData = JSON.parse(item?.testing?.sideData);
        return sideData?.shortDescription;
      } catch (e) {
        return null;
      }
    },
    checkScore(item) {
      if (this.eventPassing) {
        if (item.type === "interview") {
          // let index = this.eventPassing.event.stages
          //     .map((stage) => stage.id)
          //     .indexOf(item.id);
          // if (index !== -1) {
          //   if (
          //       this.eventPassing.event.stages[index].interviewBookedTime &&
          //       this.eventPassing.event.stages[index].interviewBookedTime
          //           .length &&
          //       this.eventPassing.event.stages[index].interviewBookedTime[0]
          //           .description === "" && (this.eventPassing.event.stages[index].interviewBookedTime[0].whenDate &&
          //           new Date(this.eventPassing.event.stages[index].interviewBookedTime[0].whenDate) < new Date())
          //   ) {
          //     return "pass";
          //   } else {
          //     return "score";
          //   }
          // } else {
          //   return null;
          // }
          return "score";
        } else {
          let index = this.eventPassing.testingPassing
            .map((pass) => pass.testingId)
            .indexOf(item.testId);
          if (index !== -1) {
            if (this.eventPassing.testingPassing[index].finalScore != null) {
              return "score";
            } else {
              return "pass";
            }
          }
        }
      } else {
        return null;
      }
    },
    getButtonText(item) {
      if (new Date(item.startDate) > new Date()) {
        return "Не началось";
      }
      if (item.type === "form") {
        const index =
          this.eventPassing && this.eventPassing.testingPassing
            ? this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(item.testId)
            : -1;
        if (index !== -1) {
          if (this.eventPassing.testingPassing[index].finalScore != null)
            return "Заполненo";
          return "Заполнено";
        }
        return "Заполнить";
      }

      if (item.type === "test" || item.type === "exercise") {
        const index =
          this.eventPassing && this.eventPassing.testingPassing
            ? this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(item.testId)
            : -1;
        if (index !== -1) {
          if (this.eventPassing.testingPassing[index].finalScore != null)
            return "Пройдено";
          return "Ожидает проверки";
        }
        return "Пройти";
      }

      if (item.type === "interview") {
        if (new Date() > new Date(item.endDate)) {
          return "Завершено";
        }

        return "Календарь";
      }

      if (item.type === "stream") {
        if (new Date() > new Date(item.endDate)) {
          return "Завершено";
        }
        return "Смотреть";
      }

      if (new Date() > new Date(item.endDate)) {
        return "Не пройдено";
      }
      return "";
    },
    goToStage(stage) {
      let passingId = this.currentEvents.eventPassings.length
        ? this.currentEvents.eventPassings[0].id
        : "null";

      if (new Date() > new Date(stage.endDate)) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Этап закончился",
          },
        });
        return;
      }
      if (new Date(stage.startDate) > new Date()) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Этап не начался",
          },
        });
        return;
      }

      if (stage.type === "stream") {
        this.pickStream(stage);
        return;
      }

      // if (this.eventPassing?.currentStageNumber && stage.order - this.eventPassing?.currentStageNumber !== 0) {
      //   errorHandler({
      //     name: "appointment",
      //     status: 300,
      //     meta: {
      //       body: "Вы не прошли предыдущий этап",
      //     },
      //   });
      //   return;
      // } else if (!this.eventPassing && stage.order > 1) {
      //   errorHandler({
      //     name: "appointment",
      //     status: 300,
      //     meta: {
      //       body: "Вы не прошли предыдущий этап",
      //     },
      //   });
      //   return;
      // }

      if (
        (stage.type !== "interview" &&
          this.eventPassing &&
          this.eventPassing.testingPassing &&
          this.eventPassing.testingPassing
            .map((pass) => pass.testingId)
            .indexOf(stage.testId) !== -1 &&
          stage.type !== "form") ||
        (stage.type === "form" &&
          this.eventPassing &&
          this.eventPassing.testingPassing &&
          this.eventPassing.testingPassing
            .map((pass) => pass.testingId)
            .indexOf(stage.testId) !== -1 &&
          this.eventPassing.testingPassing[
            this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(stage.testId)
          ].finalScore != null)
      ) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Вы уже прошли этот этап",
          },
        });
        return;
      }
      switch (stage.type) {
        case "interview":
          this.$router.push({
            name: "interview",
            params: {
              interviewId: stage.interviewId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
        case "test":
          this.$router.push({
            name: "testing",
            params: {
              testingId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
              score: "null",
            },
          });
          break;
        case "form":
          this.$router.push({
            name: "form",
            params: {
              formId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
        case "exercise":
          this.$router.push({
            name: "exercise",
            params: {
              exerciseId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
      }
    },
    getStagePrecent(item) {
      return item.type !== "interview" && this.checkScore(item) === "score"
        ? item.type === "form"
          ? this.checkScore(item) === "score"
            ? 100
            : 0
          : (this.eventPassing.testingPassing[
            this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(item.testId)
          ].finalScore /
            item.maxScore) *
          100
        : 0;
    },
  },
  mounted() {
    window.onresize = (event) => {
      this.windowWidth = event.target?.innerWidth;
    };

    if (document.getElementById("separateEventCSS")) return;
    const element = document.createElement("link");
    element.rel = "stylesheet";
    element.href = "/css/separateEvent.css";
    element.id = "separateEventCSS";
    document.body.append(element);
  },
  beforeUnmount() {
    document.getElementById("separateEventCSS")?.remove();
    this.$store.commit("setEventPassing", null);
    window.onresize = null;
  },
};
</script>

