<script>
import {PlusOutlined} from "@ant-design/icons-vue";
import locale from "ant-design-vue/es/date-picker/locale/ru_RU"
import {arraySplit} from "@/lib/over";
import ChartSoloParam from "@/components/events/ChartSoloParam";
import ChartRatio from "@/components/events/ChartRatio";


export default {
  name: "StatisticWindow",
  props: ['visible', 'title', 'id', 'stages'],
  computed: {
    statistic() {
      return this.$store.getters.statistic;
    },
    results() {
      return this.$store.getters.eventResults;
    },
    questionsInSoloParamCharts() {
      return this.$store.getters.questionsInSoloParamCharts
    }
  },
  components: {ChartRatio, ChartSoloParam, PlusOutlined},
  data() {
    return {
      arraySplit,
      locale,


      type: null,

      params: [
        {
          label: 'Пол',
          value: 'sex'
        }, {
          label: 'Город',
          value: 'city'
        }, {
          label: 'Возраст',
          value: 'birth'
        }, {
          label: 'Количество баллов',
          value: 'score'
        }, {
          label: 'Этап',
          value: 'stage'
        },
      ],

      //RESULTS
      viewResults: false,
      columns: [
        {
          title: 'Аватар',
          dataIndex: 'avatarUrl',
          key: 'avatarUrl',
        },
        {
          title: 'Имя',
          dataIndex: 'name',
          key: 'name',
        },
        {
          title: 'Номер телефона',
          dataIndex: 'phone',
          key: 'phone',
        },
        {
          title: 'Сумма баллов',
          dataIndex: 'finalscore',
          key: 'finalscore',
        },
      ],


    }
  },
  methods: {
    saveStatistics() {
      this.$store.dispatch('saveStatistics', this.id).then(() => {
        this.$emit('close');
      })
    },
    loadResults() {
      this.$store.dispatch('fetchEventsResults', this.id).then(() => {
        this.viewResults = true
      })
    },
    addChart(mode) {
      if (mode === 'ratio') {
        this.$store.commit('addCharts')
      }
      if (mode === 'solo') {
        this.$store.commit('addSoloCharts')
      }
    }
  },
  beforeMount() {
    this.$store.dispatch('fetchEventsStatistic', this.id)
  }
}
</script>

