<template>
  <div class="editor-container">
    <div class="editor-filling">
      <div class="editor-el">
        <a-typography-text>Название</a-typography-text>
        <a-input @change="(event) =>
          changeHackathonData({ field: 'name', val: event?.target?.value })
          " :value="hackathon?.name" size="large" placeholder="Напишите заголовок"></a-input>
      </div>
      <div class="editor-el">
        <a-typography-text>Фоновое изображение</a-typography-text>
        <div class="preview-container">
          <a-image :src="hackathon?.previewUrl
            ? hackathon.previewUrl
            : require('@/assets/placeholder-image.png')
            " width="310px" height="290px">
          </a-image>
          <a-button type="primary" @click="fileInput?.click()">
            <template #icon>
              <plus-outlined />
            </template>
            Загрузить фото
          </a-button>
          <input ref="fileInput" accept="image/*" hidden="hidden" type="file" @change="loadFile" />
        </div>
      </div>
      <div class="editor-el">
        <a-typography-text>Описание хакатона</a-typography-text>
        <a-textarea :auto-size="{ minRows: 1, maxRows: 3 }" @change="(event) =>
          changeHackathonData({
            field: 'description',
            val: event?.target?.value,
          })
          " :value="hackathon?.description" size="large" placeholder="Напишите поисание"></a-textarea>
      </div>
      <div class="editor-el">
        <a-typography-text>Выберете дату начала и окончания</a-typography-text>
        <a-range-picker v-model:value="hackathonDates" />
      </div>
    </div>
    <div class="table-container">
      <a-typography-title :level="4">Таблица</a-typography-title>
      <div class="table-filling">
        <div class="table-buttons" v-if="hackathon?.table?.length">
          <a-button @click="addColumn">Добавить колонку</a-button>
          <a-button @click="addRow">Добавить строку</a-button>
        </div>
        <a-empty v-if="!hackathon?.table?.length" :image="require('@/assets/icons/Group 2.svg')"
          :image-style="{ height: '50px' }">
          <template #description>
            <span> Вы еще не добавили таблицу </span>
          </template>
          <a-button @click="createTable">
            <template #icon>
              <plus-outlined></plus-outlined>
            </template>
            Добавить таблицу
          </a-button>
        </a-empty>
        <hackathon-table v-else :can-edit="true" :hackathon-id="hackathonId"></hackathon-table>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, defineComponent, onBeforeUnmount, ref } from "vue";
import { useStore } from "vuex";
import dayjs from "dayjs";
import { PlusOutlined } from "@ant-design/icons-vue";
import { useRouter } from "vue-router";
import HackathonTable from "@/components/hackathon/HackathonTable.vue";

export default defineComponent({
  props: ["hackathonId"],
  name: "HackathonEditorPage",
  components: {
    HackathonTable,
    PlusOutlined,
  },
  setup(props) {
    const store = useStore();
    const router = useRouter();
    store.dispatch("fetchHackathonById", props.hackathonId);
    const fileInput = ref(null);
    const previewFile = ref(null);

    const hackathon = computed(() => store.getters.hackathon);
    const hackathonDates = computed({
      get: () => {
        return hackathon.value
          ? [
            hackathon.value?.startDate
              ? dayjs(hackathon.value.startDate)
              : null,
            hackathon.value?.endDate ? dayjs(hackathon.value.endDate) : null,
          ]
          : [null, null];
      },
      set: (val) => {
        store.commit("setHackathonData", {
          val: val[0]?.startOf("day")?.toDate(),
          field: "startDate",
        });
        store.commit("setHackathonData", {
          val: val[1]?.endOf("day")?.toDate(),
          field: "endDate",
        });
      },
    });
    const changeHackathonData = ({ field, val }) =>
      store.commit("setHackathonData", { field, val });
    const loadFile = (event) => {
      try {
        previewFile.value =
          event?.target?.files[event?.target?.files?.length - 1];
        store.commit("setHackathonData", {
          val: URL.createObjectURL(previewFile.value),
          field: "previewUrl",
        });
      } catch (e) {
        return;
      }
    };
    const saveHackathon = async () => {
      if (previewFile.value) {
        try {
          await store.dispatch("loadFile", previewFile.value).then((val) => {
            store.commit("setHackathonData", { val, field: "previewUrl" });
          });
        } catch (e) {
          console.log(e);
        }
      }
      await store
        .dispatch("saveHackathon", props.hackathonId)
        .then((status) => {
          if (status === 200) router.push({ name: "hackathons" });
        });
    };
    document.addEventListener("saveHackathon", saveHackathon);

    const addRow = () => {
      store.commit("addHackathonRow");
    };

    const addColumn = () => {
      store.commit("addHackathonColumn");
    };
    const createTable = () => {
      addColumn();
      addRow();
    };

    onBeforeUnmount(() =>
      document.removeEventListener("saveHackathon", saveHackathon)
    );

    return {
      hackathon,
      hackathonDates,
      fileInput,

      changeHackathonData,
      loadFile,
      saveHackathon,
      createTable,
      addColumn,
      addRow,
    };
  },
});
</script>

