import {defaultHttpResHandler} from "@/store/helpers/defaultHttpResHandler";
import {makeGetReq, makePostReq} from "@/store/helpers/request";
import {appConfig} from "@/lib/appConfig";


export const fetchAllTesting = async (token, name) => await defaultHttpResHandler(async () => defaultHttpResHandler(async () => await makeGetReq(appConfig.domain + '/testing?name=' + name, {token})));

export const fetchCurrentTesting = async (token, id) => await defaultHttpResHandler(async () =>
        await makeGetReq(appConfig.domain + '/testing/' + id, {token})
    // {
    //     return {
    //         data: id,
    //         status: 200
    //     }
    // }
)
export const fetchCurrentBlank = async (token, id) => await defaultHttpResHandler(async () =>
        await makeGetReq(appConfig.domain + '/blank/def/' + id, {token})
    // {
    //     return {
    //         data: id,
    //         status: 200
    //     }
    // }
)


export const saveTesting = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(appConfig.domain + '/testing', data, {
    data: true,
    token
}))

export const saveBlank = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(appConfig.domain + '/blank/def', data, {
    data: true,
    token
}))



