    </div>
    <a-typography-title :level="5">
      {{
        'Вопрос ' + (index + 1) + '. '
      }}
    </a-typography-title>
    <a-typography-text>
      {{ (question.type === 'conformity' ? 'Выберете соответствие: ' : question.body) }}
    </a-typography-text>

    <div class="answers-wrapper">
      <SingleAnswer :answers="question.answers" v-if="question.type==='single'" type="single" :question-id="question.id"
                    :rater="rater"></SingleAnswer>
      <MultiAnswer :answers="question.answers" :rater="rater" v-if="question.type==='multi'" :question-id="question.id"
