                  @change="e=>$store.commit('pickAnswer', {questionId: question.id, answerId: e.target.value, type: 'open'})"
                  placeholder="Введите ответ" :rows="4"></a-textarea>


      <div class="save-block">
        <div class="question-buttons">
          <a-button shape="round" type="primary" class="blue-buttons-ghost" ghost v-if="currentQuestion>0&&role!=='rater'"
