      <h3>Максимальный балл: {{ question.maxScore ? question.maxScore : 1 }}</h3>
      <!--      <a-button shape="round" type="primary"-->
      <!--                :ghost="testingScore.map(item=>item.questionId).indexOf(question.id)!==-1?testingScore [testingScore.map(item=>item.questionId).indexOf(question.id)].estimate===true :false"-->
      <!--                @click="$store.commit('setScore', {questionId:question.id,estimate:true})">-->
      <!--        Принять-->
      <!--      </a-button>-->
      <!--      <a-button shape="round" type="primary" danger-->
      <!--                :ghost="testingScore.map(item=>item.questionId).indexOf(question.id)!==-1?testingScore [testingScore.map(item=>item.questionId).indexOf(question.id)].estimate===false :false"-->
      <!--                @click="$store.commit('setScore', {questionId:question.id,estimate:false})">-->
      <!--        Отклонить-->
      <!--      </a-button>-->
    </div>

  </div>
  <div v-else class="question-wrapper">
