      У вас будет {{ convertTimeInMinuteAndSeconds(limitSeconds) }} минут на прохождение данного тестирования. Не
      покидайте данную страницу, иначе результаты не
      сохранятся.
    </a-typography-title>
    <div class="preview-data">
      <a-progress type="circle" strokeLinecap="square" stroke-color="#5EC9CA" status="active" :percent="100"
