      <div class="preview-questions">
        <a-typography-text>
          Количество
          <br>
          вопросов:
        </a-typography-text>
        <a-typography-title style="color: #5EC9CA;" :level="5">
          {{ passingTest.testingPassing.answers.length }}
        </a-typography-title>
      </div>
      <a-button shape="round" type="primary" class="blue-buttons" size="large" v-if=" viewPreview"
                @click="$emit('incrementQuestion')">
        Начать тестирование

      </a-button>
    </div>

  </div>
</template>

<script>
import SingleAnswer from "@/components/events/testing/SingleAnswer";
import MultiAnswer from "@/components/events/testing/MultiAnswer";
import SelectAnswer from "@/components/events/testing/SelectAnswer";
import MatchingAnswer from "@/components/events/testing/MatchingAnswer";
import {convertTimeInMinuteAndSeconds} from "@/lib/convertTime";

export default {
  name: "QuestionElement",
  data() {
    return {
      convertTimeInMinuteAndSeconds
    }
  },
  components: {MatchingAnswer, MultiAnswer, SingleAnswer, SelectAnswer},
  props: ['question', 'index', 'rater', 'viewPreview', 'limitSeconds', 'currentQuestion', 'currentStage', 'role', 'score'],
  computed: {
    passingTest() {
      return this.$store.getters.passingTest;
    },
    testingScore() {
      return this.$store.getters.testingScore;
    }
  },
}
</script>

