<script>
import {mapGetters} from "vuex";
import {EditOutlined} from "@ant-design/icons-vue";
import AchievementElement from "@/components/dashboard/AchievementElement.vue";
import ProjectsElement from "@/components/dashboard/ProjectsElement.vue";
import {ageCalculation} from "@/lib/convertTime";
import dayjs from "dayjs";

export default {

  name: "DashboardPage",
  methods: {
    dayjs,
    checkStatus(record) {
      const {hackaton} = record;
      if (!hackaton?.startDate || !hackaton?.endDate || new Date() < new Date(hackaton?.startDate)) {
        return 'notStart';
      }
      return new Date() > new Date(hackaton?.endDate) ? 'end' : 'process';
    }
  },
  components: {EditOutlined, AchievementElement, ProjectsElement},
  computed: {
    ...mapGetters(["user", "eventsHistory", 'hackathonsResults']),
  },
  data() {
    return {
      columns: [
        {
          title: 'Мероприятие',
          dataIndex: 'title',
          key: 'title',
        },
        {
          title: 'Этап',
          dataIndex: 'type',
          key: 'type',
        },
        {
          title: 'Дата начала',
          dataIndex: 'startDate',
          key: 'startDate',
        },
        {
          title: 'Дата прохождения',
          dataIndex: 'createDate',
          key: 'createDate',
        },
        {
          title: 'Статус',
          dataIndex: 'score',
          key: 'score',
        },
      ],
      hackathonsColumns: [
        {
          title: 'Название',
          dataIndex: ['hackaton', 'name'],
          key: 'name',
          ellipsis: true
        },
        {
          title: 'Описание',
          dataIndex: ['hackaton', 'description'],
          key: 'descriptiom',
          ellipsis: true
        },
        {
          title: 'Дата начала',
          dataIndex: ['hackaton', 'startDate'],
          key: 'startDate',
        },
        {
          title: 'Дата окончания',
          dataIndex: ['hackaton', 'endDate'],
          key: 'endDate',
        },
        {
          title: 'Статус',
          key: 'status',
        },
        // {
        //   title: 'Ссылка',
        //   key: 'link',
        // },

      ],
      ageCalculation,
      projects: null,
      achievements: null,

    };
  },
  beforeMount() {
    scroll(0, 0);
    this.$store.dispatch('fetchEventsHistory');
    this.$store.dispatch('fetchUserEvents', {status: 'process'}).then((data) => {
      this.projects = data;
    })
    this.$store.dispatch('fetchUserEvents', {status: 'passed'}).then(data => {
      this.achievements = data;
    });
    this.$store.dispatch('fetchHackathonsResults');
  },
};
</script>

