  <div class="container" v-if="interview">
    <div class="main-wrapper">
      <div class="" style="display: flex; gap: 10px">
        <a-button shape="round" class="back-button blue-buttons-ghost" ghost
                  @click="timesSelected?timesSelected=null: slotSelected?slotSelected=null:$router.go(-1)"
                  type="primary"
        >Назад
          <template #icon>
            <arrow-left-outlined/>
          </template>
        </a-button>
        <a-button @click="bookingTime"
                  v-if="slotSelected&&interviewBookedId&&role==='user'&&!userInterviewPassed"
                  shape="round"
                  class="back-button"
