          Сохранить
        </a-button>
      </div>

      <div class="filling" v-if="!slotSelected&&!timesSelected">
        <div class="info-block">
          <a-typography-title v-if="role!=='rater'" :level="4">
            Выберете удобную дату прохождения видео-интервью:
          </a-typography-title>
          <div class="calendar-block" v-if="role!=='rater'"
          >
            <a-calendar
                :locale="locale"
                @select="selectDate"
                @panelChange="panelChange"
                class="calendar"
                :value="dateSelected">
              <template #dateCellRender="{ current }">
                <div class="interview-day"
                     v-if="interview.slots.map(item=>dayjs (item.day).startOf('day').unix()).indexOf(current.startOf('day').unix())!==-1">
                  <video-camera-outlined
                      :style="{color:interviewPassed(current)?'#5EC9CA' :'#8C8C8C', fontSize: '20px'}"/>
                  <div class=""
                       v-if="interviewPassed(current)">
                    Видео-интервью
                  </div>
                </div>
              </template>
            </a-calendar>
          </div>
          <a-table style="width: 100%" :columns="raterColumns" :data-source="interview.slots" v-else>
            <template #bodyCell="{column,record}">
              <div class="" v-if="column.key==='day'">
                {{ dayjs(record.day).toDate().toLocaleDateString('ru-RU', dateOptions) }}
              </div>
              <div class="" v-if="column.key==='times'">
                {{ record.times.length }}
              </div>
              <div class="" v-if="column.key==='slots'">
                {{
                  record.times.filter((time => {
                    return time.usersInSlots.filter(userInSlot => userInSlot.interviewerId === user.id).length;
                  })).length
                }}
              </div>
              <div class="" v-if="column.key==='actions'">
                <a-button @click="selectDate(record.day)">Раскрыть</a-button>
              </div>
            </template>
          </a-table>

        </div>
      </div>
      <div class="filling" v-if="slotSelected&&!timesSelected">
        <a-table style="width: 100%" :columns="columns" :data-source="slotSelected.times">
          <template #bodyCell="{column,record}">
            <div class="" v-if="column.key==='startTime'||column.key==='endTime'">
              {{ `${record[column.key].format('HH:mm')}` }}
            </div>
            <div class="" v-if="column.key==='slotsCount'">
              {{ record.slotsCount - record.usersInSlots.length }}
            </div>
            <div class="" v-if="column.key==='bookedSlots'">
              {{ record.usersInSlots.length }}
            </div>

            <div class="" v-if="column.key==='bookedSlotsMy'">
              {{ record.usersInSlots.filter(userInSlot => userInSlot.interviewerId === user.id).length }}
            </div>

            <div class="" v-if="column.key==='actions'">
              <a-button v-if="role==='user'&&!(interviewBookedId && interviewBookedId === record.id)"
