                        :disabled="interviewBookedId&&interviewBookedId!==record.id||(record.slotsCount - record.usersInSlots.length)<=0"
                        type="primary"
                        @click="$store.dispatch('bookInterviewSlot',record.id)">Записаться
              </a-button>

              <a-button v-if="role==='user'&&interviewBookedId && interviewBookedId === record.id"
                        :ghost="interviewBookedId&&interviewBookedId===record.id"
                        type="primary"
                        @click="$store.dispatch('bookInterviewSlot',record.id)">
                Отменить
              </a-button>


              <a-button v-if="role==='rater'" @click="timesSelected = record">Показать пользователей</a-button>
            </div>
          </template>


        </a-table>
      </div>
      <div class="filling" v-if="slotSelected&&timesSelected">
        <a-table style="width: 100%" :columns="userColumns" :data-source="timesSelected.usersInSlots">
          <template #bodyCell="{column,record}">
            <div class="" v-if="column.key==='avatarUrl'">
              <a-avatar :src="record.client.avatarUrl"></a-avatar>
            </div>

            <div class="" v-if="column.key==='sex'">
              {{ record.client.sex === 'man' ? 'Мужской' : 'Женский' }}
            </div>

            <div class="" v-if="column.key==='birth'">
