            </div>

            <div class="" v-if="column.key==='interviewer'">
              {{
                record.interviewer ? (record.interviewer.id === user.id ? 'Я' : record.interviewer.name) : 'Не назначено'
              }}
            </div>


            <div class="" v-if="column.key==='actions'">
              <a-button v-if="!record.interviewerId" @click="openInterviewModal(record)">Принять на интервью</a-button>
              <a-tag color="success" style="font-size: 14px" v-else>Принято</a-tag>
            </div>
