<script>
import {mapGetters} from "vuex";
import {ArrowLeftOutlined, VideoCameraOutlined} from "@ant-design/icons-vue";
import locale from "ant-design-vue/es/date-picker/locale/ru_RU";
import dayjs from "dayjs";

export default {
  name: "InterviewPage",
  components: {ArrowLeftOutlined, VideoCameraOutlined},
  computed: {
    ...mapGetters(["currentStage", 'eventPassing', 'interview', 'interviewBookedId', 'user', 'userInterviewPassed']),
    isAdmin() {
      return this.$store.getters.role;
    },
  },
  data() {
    return {
      dateOptions: {
        month: "long",
        day: "numeric",
        year: "numeric"
      },
      dayjs,
      locale,
      dateSelected: dayjs(new Date()),
      slotSelected: null,
      timesSelected: null,
      userSelected: null,

      // viewInterviewTime: false,
      // bookedTime: null,
      // allTimes: [],

      currentInterviewId: null,
      viewInterviewModal: false,
      interviewDescription: null,

      //RESULTS
      viewResultsInterview: false,
      interviewResults: null,

      changeMonth: null,

      modalWidth: '50%',
      raterColumns: [
        {
          title: 'Дата',
          dataIndex: 'day',
          key: 'day'
        },
        {
          title: 'Количество слотов',
          dataIndex: 'times',
          key: 'times'
        },
        {
          title: 'Проверяемые слоты',
          key: 'slots'
        },
        {
          title: 'Действия',
          key: 'actions'
        },
      ],

      columns: [
        {
          title: 'Время начала',
          dataIndex: 'startTime',
          key: 'startTime'
        },
        {
          title: 'Время конца',
          dataIndex: 'endTime',
          key: 'endTime'
        },
        {
          title: 'Количество свободных мест',
          dataIndex: 'slotsCount',
          key: 'slotsCount'
        },
        {
          title: 'Количество занятых мест',
          key: 'bookedSlots'
        },

        {
          title: 'Пользователей на проверке',
          key: 'bookedSlotsMy'
        },

        {
          title: 'Действия',
          key: 'actions'
        },
      ],
      userColumns: [
        {
          title: 'Фото профиля',
          dataIndex: ['client', 'avatarUrl'],
          key: 'avatarUrl'
        },
        {
          title: 'ФИО',
          dataIndex: ['client', 'name'],
          key: 'name'
        },
        {
          title: 'Пол',
          dataIndex: ['client', 'sex'],
          key: 'sex'
        },
        {
          title: 'Город',
          dataIndex: ['client', 'city'],
          key: 'city'
        },
        {
          title: 'Номер телефона',
          dataIndex: ['client', 'phone'],
          key: 'phone'
        },
        {
          title: 'Интервьюер',
          key: 'interviewer'
        },
        {
          title: 'Действия',
          dataIndex: 'actions',
          key: 'actions'
        },
      ],
    };
  },
  methods: {
    panelChange(date, mode) {
      this.changeMonth = mode;
    },
    interviewPassed(date) {
      let slotIndex = this.interview.slots.map(item => dayjs(item.day).startOf('day').unix()).indexOf(date.startOf('day').unix());
      if (slotIndex !== -1) {
        const timesIndex = this.interview.slots[slotIndex].times.map(times => times.id).indexOf(this.interviewBookedId);
        if (timesIndex !== -1) {
          const userIndex = this.interview.slots[slotIndex].times[timesIndex].usersInSlots.map(user => user.clientId).indexOf(this.user?.id);
          if (userIndex !== -1) {
            return this.interview.slots[slotIndex].times[timesIndex].usersInSlots[userIndex].id !== null;
          }
        }
        return false;
      } else {
        return false
      }

      // for (let i = 0; i < this.allTimes.length; i++) {
      //   if (dayjs(this.interview[i].whenDate).startOf('date').unix() === date.startOf('date').unix() && this.allTimes[i].self === true) {
      //     return true;
      //   }
      // }
      // return false;
    },

    selectDate(date) {
      this.dateSelected = date.startOf('day');
      let slotIndex = this.interview.slots.map(item => item.day.startOf('day').unix()).indexOf(this.dateSelected.unix());
      if (slotIndex !== -1 && (!this.interviewPassed(date) || this.role === 'rater')) {
        this.slotSelected = this.interview.slots[slotIndex];
      }

      if (this.changeMonth === 'month' || this.changeMonth === 'year') {
        this.changeMonth = null;
        return;
      }

      if (this.interviewPassed(date) && this.role !== 'rater') {
        const timesIndex = this.interview.slots[slotIndex].times.map(item => item.id).indexOf(this.interviewBookedId);
        if (timesIndex !== -1) {
          this.interviewResults = {
            day: this.interview.slots[slotIndex].day,
            startTime: this.interview.slots[slotIndex].times[timesIndex].startTime,
            endTime: this.interview.slots[slotIndex].times[timesIndex].endTime,
          }
          const userIndex = this.interview.slots[slotIndex].times[timesIndex].usersInSlots.map(item => item.clientId).indexOf(this.user.id);
          if (userIndex !== -1) {
            this.interviewResults['interviewerId'] = this.interview.slots[slotIndex].times[timesIndex].usersInSlots[userIndex].interviewerId;
            this.interviewResults['description'] = this.interview.slots[slotIndex].times[timesIndex].usersInSlots[userIndex].description;
          }
          this.viewResultsInterview = true;
        }

      }

      // if (this.slotSelected) {
      //
      //   let index = this.allTimes.map(item => item.self).indexOf(true)
      //   if (index === -1 || this.role === 'rater') {
      //     this.bookedTime = this.allTimes.filter(
      //         (item) =>
      //             dayjs(item.whenDate).get('date') === this.dateSelected.get("date")
      //     );
      //     this.viewInterviewTime = true;
      //   } else {
      //     for (let i = 0; i < this.allTimes.length; i++) {
      //       if (dayjs(this.allTimes[i].whenDate).startOf('date').unix() === date.startOf('date').unix() && this.allTimes[i].self === true) {
      //         this.interviewResults = this.allTimes[i];
      //         this.viewResultsInterview = true;
      //         break;
      //       }
      //     }
      //   }
      // }
    },
    // closeModal() {
    //   this.viewInterviewTime = false;
    //   this.bookedTime = [];
    // },
    bookingTime() {
      // this.$store
      //     .dispatch(
      //         "saveBookedTime",
      //         this.dateSelected
      //             .hour(item)
      //             .minute(0)
      //             .second(0)
      //             .millisecond(0)
      //             .toDate()
      //     )
      //     .then((status) => {
      //       if (status === 200) {
      //         this.closeModal();
      //         this.$store.dispatch("fetchTimeByInterviewDate").then((data) => {
      //           this.allTimes = data;
      //         });
      //       }
      //     });
      this.$store.dispatch('saveInterview', {id: this.interviewId, role: 'user'}).then(() => {
        if (this.role === 'user') {
          this.$store.dispatch('createInterviewPassing');
        }
        this.$store.dispatch('fetchInterviewById', this.interviewId);
        this.slotSelected = null;
      })
    },
    closeInterviewModal() {
      this.interviewDescription = null;
      this.userSelected = null;
      this.viewInterviewModal = false;
    },
    approveInterview() {
      this.$store.commit('setInterviewDescription', {
        slot: this.slotSelected,
        times: this.timesSelected,
        user: this.userSelected,
        description: this.interviewDescription,
        interviewerId: this.user.id
      });
      this.$store.dispatch('saveInterview', {id: this.interviewId, role: 'rater'}).then(() => {
        this.$store.dispatch('fetchInterviewById', this.interviewId).then(this.closeInterviewModal);
      })
    },
    openInterviewModal(user) {
      this.userSelected = user;
      this.viewInterviewModal = true;
    },
  },
  props: ["interviewId", "eventId", "role", "passingId"],
  beforeMount() {
    scroll(0, 0);
    this.$store.dispatch("fetchCurrentEvents", this.eventId);
    this.$store.dispatch('fetchInterviewById', this.interviewId);
    this.$store.dispatch('fetchEventPassing', this.passingId);

  },
  mounted() {
    if (window.innerWidth < 1100) {
      this.modalWidth = '60%'
    }
    if (window.innerWidth < 1000) {
      this.modalWidth = '70%'
    }
    if (window.innerWidth < 900) {
      this.modalWidth = '80%'
    }
    if (window.innerWidth < 800) {
      this.modalWidth = '90%'
    }
    if (window.innerWidth < 700) {
      this.modalWidth = '100%'
    }
  },
  beforeRouteLeave() {
    this.$store.commit('clearPassing');
    this.$store.commit('clearInterview');
  },

};
</script>

