<template>
  <div class="answer-container">
    <a-list
        :grid="{ gutter: 16, xs: 1, sm: 2, md: 2, lg: 3, xl: 3, xxl: 3, xxxl: 3 }"
        :data-source="answers"
        style="max-width: 100%; width:100%;"
    >
      <template #renderItem="{ item }">
        <a-list-item style="max-width: 100%; width:100%;">
          <a-card>
            <template #cover v-if="item.matchingFileUrl!=='null'">
              <img
                  alt="example"
                  :src="item.matchingFileUrl"
                  class="card-image"
              />
            </template>


            <template #title v-if="!rater&&item.matchingFileUrl==='null'">
              {{ item.body }}
            </template>
            <template #actions v-if="!rater">
              <a-select placeholder="Выберете"
                        @deselect="val=>$store.commit('deleteMatchingAnswer',{questionId,answerId:val})"
                        :allowClear="true"
                        :class="{'blocked':rater}"
                        :value=" returnValue(item)"
                        @change="val=>$store.commit('pickAnswer',{questionId,answerId:val,type,meta:{body:item.body!=='null'?item.body:item.matchingFileUrl}})"
                        style="width: 120px">
                <a-select-option
                    :disabled="passingTest.testingPassing.answers.map(question=>question.questionId).indexOf(questionId)===-1?null:( passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question=>question.questionId).indexOf(questionId)].matchingAnswers.map(item2=>item2.answerId).indexOf(answer.id)!==-1)"
                    v-for="answer in answers" :key="answer.id" :value="answer.id">
                  {{ answer.matchingCorrectBody }}
                </a-select-option>
              </a-select>
              <a-icon v-if="rater" style="margin-left: 10px">
                <close-circle-outlined v-if="!checkCorrect(item)" style="color: red;font-size: 17px"/>
                <check-circle-outlined v-if="checkCorrect(item)" style="color:lawngreen;font-size: 17px"/>
              </a-icon>

            </template>


            <div class="" v-if="rater">
              <div class="" v-if="item.matchingFileUrl==='null'">
                <a-typography-title style="margin: 0;margin-top: 20px" :level="4">
                  {{ item.body }}
                </a-typography-title>
              </div>


              <div class=""
                   style="margin: 0; display: flex; align-items: center; justify-content: flex-start;text-align: center;margin-top: 30px">
<!--                {{-->
<!--                  passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId) === -1 ? null : (passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId)].matchingAnswers[passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId)].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl)] ? passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId)].matchingAnswers[passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId)].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl)].body : 'Нет ответа')-->
<!--                }}-->
                {{
                  returnLabel(item)
                }}
                <a-icon v-if="rater" style="margin-left: 10px">
                  <close-circle-outlined v-if="!checkCorrect(item)" style="color: red;font-size: 17px"/>
                  <check-circle-outlined v-if="checkCorrect(item)" style="color:lawngreen;font-size: 17px"/>
                </a-icon>
              </div>
            </div>


          </a-card>
        </a-list-item>
      </template>
    </a-list>
  </div>
</template>

<script>

import {CheckCircleOutlined, CloseCircleOutlined} from "@ant-design/icons-vue";

export default {
  components: {CheckCircleOutlined, CloseCircleOutlined},
  name: "MatchingAnswer",
  props: ['answers', 'questionId', 'type', 'rater'],
  computed: {
    passingTest() {
      return this.$store.getters.passingTest;
    },
    currentStage() {
      return this.$store.getters.currentStage;
    }
  },
  methods: {
    returnLabel(item) {
      const questionId = this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId);
      if (questionId !== -1) {
        const matchingIndex = this.passingTest.testingPassing.answers[questionId].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl);
        if (matchingIndex !== -1) {
          let answerId = this.passingTest.testingPassing.answers[questionId].matchingAnswers[matchingIndex].answerId
          const answerIndex = this.answers.map(answer => answer.id).indexOf(answerId);
          if (answerIndex !== -1) {
            return this.answers[answerIndex].matchingCorrectBody;
          }
        }
      }
    },
    returnValue(item) {
      const questionId = this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId);
      if (questionId !== -1) {
        const matchingIndex = this.passingTest.testingPassing.answers[questionId].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl);
        if (matchingIndex !== -1) {
          return this.passingTest.testingPassing.answers[questionId].matchingAnswers[matchingIndex].answerId
        }
      }
      return null;
    },
    // checkValue(item) {
    //   passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId) === -1 ? null : (
    //       passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId)].matchingAnswers[passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId)].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl)] ? passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId)].matchingAnswers[passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question => question.questionId).indexOf(questionId)].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl)].answerId : null)
    //
    // },
    checkCorrect(item) {
      let body = this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId) === -1 ? false :
          (this.passingTest.testingPassing.answers[this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId)].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl) === -1 ? false :
              (this.passingTest.testingPassing.answers[this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId)].matchingAnswers[this.passingTest.testingPassing.answers[this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId)].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl)].body === (item.body !== 'null' ? item.body : item.matchingFileUrl)));
      let answer = this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId) === -1 ? false :
          (this.passingTest.testingPassing.answers[this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId)].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl) === -1 ? false : (this.passingTest.testingPassing.answers[this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId)].matchingAnswers[this.passingTest.testingPassing.answers[this.passingTest.testingPassing.answers.map(question => question.questionId).indexOf(this.questionId)].matchingAnswers.map(answer => answer.body).indexOf(item.body !== 'null' ? item.body : item.matchingFileUrl)].answerId === item.id));
      return body && answer;
    },
  }

}
</script>

<style scoped>

.answer-container {
  width: 100%;
  max-width: 100%;
  display: flex;
  align-items: center;
}

.blocked {
  cursor: default;
  pointer-events: none;
}


</style>