import {defaultHttpResHandler} from "@/store/helpers/defaultHttpResHandler";
import {makeDeleteReq, makeGetReq, makePostReq} from "@/store/helpers/request";
import {appConfig} from "@/lib/appConfig";


export const fetchStreamById = async (token, id) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/translation/${id}`, {
    token
}))

export const fetchStreams = async (token) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/translation`, {
    token
}))


export const saveStream = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(`${appConfig.domain}/translation`, data, {
    token,
    data: true
}));


export const deleteStream = async (token, data) => await defaultHttpResHandler(async () => await makeDeleteReq(`${appConfig.domain}/stream`, data, {
    token,
    data: true
}))





