import {
    fetchAllTesting,
    fetchCurrentTesting, saveBlank,
    saveTesting
} from "@/store/testing/serverInterationMethods";
import {errorHandler} from "@/lib/errorHandler";

export const testing = {
    actions: {


        async fetchAllTesting({getters, commit}, {name}) {
            const {data} = await fetchAllTesting(getters.token, name);
            commit('setAllTesting', data);
        },
        async fetchCurrentTesting({getters, commit}, {id, type}) {
            let data;
            if (id === 'new') {
                data = {
                    title: null,
                    description: null,
                    sideData: {},
                    type,
                    questions: []
                }
                if (type === 'testing') {
                    data['limitSeconds'] = 0
                }
            } else {
                const response = await fetchCurrentTesting(getters.token, id);
                data = response.data;
                data.limitSeconds = data.limitSeconds / 60;
            }
            commit('setCurrentTesting', data);
        },


        async saveTesting({getters, commit}) {
            if (getters.testingFilesQueue && getters.testingFilesQueue.length) {
                commit('setLoader', true)
                for (let i = 0; i < getters.testingFilesQueue.length; i++) {
                    await this.dispatch('loadFile', getters.testingFilesQueue[i].file).then(url => {
                        if (getters.testingFilesQueue[i].meta.type === 'questions') {
                            commit('setQuestionFileUrl', {index: getters.testingFilesQueue[i].index, url})
                        }
                        if (getters.testingFilesQueue[i].meta.type === 'answers') {
                            commit('setMatchingAnswerFileUrl', {
                                qIndex: getters.testingFilesQueue[i].index,
                                aIndex: getters.testingFilesQueue[i].meta.index,
                                url
                            })
                        }
                    })
                }
                commit('set')
                commit('clearTestingFilesQueue');
                commit('setLoader', false);
            }
            commit('parseLimitSeconds')
            commit('stringifySideData');
            const {status} = await saveTesting(getters.token, getters.currentTesting);
            errorHandler({name: 'testEditor', status});

        },
        async saveBlank({getters}) {
            const {status} = await saveBlank(getters.token, getters.currentBlank);
            errorHandler({status, name: 'testEditor'})
        }
    },
    mutations: {
        //MAIN
        setAllTesting(state, data) {
            state.allTesting = data;
        },
        stringifySideData(state) {
            if (state.currentTesting?.sideData) {
                state.currentTesting.sideData = JSON.stringify(state.currentTesting.sideData);
            }
        },
        setCurrentTesting(state, data) {
            state.currentTesting = data;


            if (!state.currentTesting) return;

            if (!state.currentTesting?.sideData) {
                state.currentTesting.sideData = {};
            } else {
                try {
                    state.currentTesting.sideData = JSON.parse(state.currentTesting.sideData);
                } catch (e) {
                    state.currentTesting.sideData = {}
                }
            }

            if (!state.currentTesting.questions.filter(item => item.order != null).length) {
                for (let i = 0; i < state.currentTesting.questions.length; i++) {
                    state.currentTesting.questions[i].order = i;
                }
            }
        },
        setTestingTitle(state, data) {
            state.currentTesting.title = data;
        },

        setTestingShortDescription(state, data) {
            if (!state.currentTesting.sideData) {
                state.currentTesting.sideData = {
                    shortDescription: null
                }
            }
            state.currentTesting.sideData.shortDescription = data;
        },

        setTestingDescription(state, data) {
            state.currentTesting.description = data;
        },
        setTimer(state, data) {
            state.currentTesting.limitSeconds = data;
        },

        swapQuestion(state, {from, to}) {
            state.currentTesting.questions[from].order = to;
            state.currentTesting.questions[to].order = from;
        },

        //QUESTIONS
        addQuestion(state) {
            state.currentTesting.questions.push({
                type: null,
                fileUrl: 'null',
                body: null,
                order: state.currentTesting.questions.length,
                maxScore: 0,
                answers: []
            })
        },
        setQuestionBody(state, {val, index}) {
            state.currentTesting.questions[index].body = val;
        },
        setQuestionMaxScore(state, {val, index}) {
            state.currentTesting.questions[index].maxScore = val;
        },
        setQuestionRequired(state, {index}) {
            state.currentTesting.questions[index].required = !state.currentTesting.questions[index].required;
        },
        setQuestionType(state, {index, val}) {
            state.currentTesting.questions[index].type = val;
            state.currentTesting.questions[index].answers = [];
            state.currentTesting.questions[index].body = null;
            if (val === 'conformity') {
                state.currentTesting.questions[index].body = 'null';
                state.currentTesting.questions[index].fileUrl = 'null';
                state.testingFilesQueue = state.testingFilesQueue.filter(item => item.index !== index);
            }
        },
        setQuestionFileUrl(state, {index, url}) {
            state.currentTesting.questions[index].fileUrl = url;
        },
        setMatchingQuestionBody(state, {qIndex, aIndex, val}) {
            state.currentTesting.questions[qIndex].answers[aIndex].body = val;
        },
        deleteQuestion(state, qIndex) {
            state.currentTesting.questions.splice(qIndex, 1);
            state.testingFilesQueue = state.testingFilesQueue.filter(item => item.index !== qIndex);
            for (let i = 0; i < state.testingFilesQueue.length; i++) {
                if (state.testingFilesQueue[i].index > qIndex) {
                    state.testingFilesQueue[i].index--;
                }
            }
        },
        //ANSWERS
        addAnswer(state, {index, meta}) {
            state.currentTesting.questions[index].answers.push({
                body: state.currentTesting.questions[index].type === 'conformity' && meta.type === 'img' ? 'null' : null,
                matchingFileUrl: 'null',
                matchingCorrectBody: state.currentTesting.questions[index].type === 'conformity' ? null : 'null',
                correct: false,
                skill: "mind",
                matchingAnswer: []
            });
        },
        setCorrectAnswer(state, {aIndex, qIndex, type}) {
            if (type === 'single') {
                for (let i = 0; i < state.currentTesting.questions[qIndex].answers.length; i++) {
                    state.currentTesting.questions[qIndex].answers[i].correct = false;
                }
                state.currentTesting.questions[qIndex].answers[aIndex].correct = true;
            }
            if (type === 'multi') {
                state.currentTesting.questions[qIndex].answers[aIndex].correct = !state.currentTesting.questions[qIndex].answers[aIndex].correct;
            }
        },
        setAnswerBody(state, {qIndex, aIndex, val}) {
            state.currentTesting.questions[qIndex].answers[aIndex].body = val;
        },
        setMatchingAnswerBody(state, {qIndex, aIndex, val}) {
            state.currentTesting.questions[qIndex].answers[aIndex].matchingCorrectBody = val;
        },
        setMatchingAnswerFileUrl(state, {qIndex, aIndex, url}) {
            state.currentTesting.questions[qIndex].answers[aIndex].matchingFileUrl = url;
        },
        deleteAnswer(state, {qIndex, aIndex}) {
            state.currentTesting.questions[qIndex].answers.splice(aIndex, 1);
            if (state.currentTesting.questions[qIndex].type === 'conformity') {
                for (let i = 0; i < state.testingFilesQueue.length; i++) {
                    if (state.testingFilesQueue[i].index === qIndex) {
                        if (state.testingFilesQueue[i].meta.index === aIndex) {
                            state.testingFilesQueue.splice(i, 1);
                            continue;
                        }
                        if (state.testingFilesQueue[i].meta.index > aIndex) {
                            state.testingFilesQueue[i].meta.index--;
                        }

                    }
                }
            }
        },
        changeAnswerType(state, {index, type}) {
            for (let i = 0; i < state.currentTesting.questions[index].answers.length; i++) {
                if (type === 'img') {
                    state.currentTesting.questions[index].answers[i].body = 'null';
                }
                if (type === 'text') {
                    state.currentTesting.questions[index].answers[i].body = null;
                    state.currentTesting.questions[index].answers[i].matchingFileUrl = 'null';
                    for (let j = 0; j < state.testingFilesQueue.length; j++) {
                        if (state.testingFilesQueue[j].index === index && state.testingFilesQueue[j].meta.index === i) {
                            state.testingFilesQueue.splice(j, 1);
                            break;
                        }
                    }
                }
                state.currentTesting.questions[index].answers[i].matchingAnswer = [];
            }
        },

        //OTHER
        addTestingFilesQueue(state, {index, file, meta}) {
            state.testingFilesQueue.push({index, file, meta});
            if (meta.type === 'questions') {
                state.currentTesting.questions[index].fileUrl = URL.createObjectURL(file);
            }
            if (meta.type === 'answers') {
                state.currentTesting.questions[index].answers[meta.index].matchingFileUrl = URL.createObjectURL(file);
            }
        },
        clearTestingFilesQueue(state) {
            state.testingFilesQueue = [];
        },

        parseLimitSeconds(state) {
            state.currentTesting.limitSeconds = state.currentTesting.limitSeconds * 60;
        },

        //
        // BLANK
        //

        setStageMode(state, data) {
            state.stageMode = data;
        }

    },
    state: {
        currentTesting: null,
        testingFilesQueue: [],
        allTesting: null,
        stageMode: null
    },
    getters: {
        currentTesting: state => state.currentTesting,
        testingFilesQueue: state => state.testingFilesQueue,
        allTesting: state => state.allTesting,
        stageMode: state => state.stageMode
    }
}