<script>
import {mapGetters} from "vuex";
import {
  ArrowLeftOutlined,
  UploadOutlined,
  CheckSquareOutlined,
  SmileOutlined,
  FrownOutlined,
  MehOutlined
} from "@ant-design/icons-vue";
import Cookies from "js-cookie";

export default {
  components: {
    ArrowLeftOutlined,
    UploadOutlined,
    CheckSquareOutlined,
    SmileOutlined, FrownOutlined, MehOutlined
  },
  name: "ExercisePage",
  props: ['exerciseId', 'eventId', 'passingId', 'role'],
  computed: {
    ...mapGetters(['currentStage', 'passingTest', 'testingScore', 'currentBlank', 'passingBlank', 'passingBlankAllScore'])
  },
  data() {
    return {
      files: [],
      dateOptions: {
        month: 'long', day: 'numeric'
      },

      viewBlank: true,
      disabledButton: false

    }
  },
  methods: {
    changeFile() {
      const el = this.$refs.file.files;
      let file = el[el.length - 1];
      this.files.push(file)
      this.$store.commit('pickAnswer', {
        questionId: this.currentStage.questions[0].id,
        answerId: URL.createObjectURL(file),
        type: 'files',
        meta: {
          index: this.passingTest.testingPassing.answers[0].attachmentAnswerFile.length
        }
      })
    },
    async saveExercise() {
      if (this.currentBlank && !this.viewBlank) {
        this.viewBlank = true;
        scroll(0, 0);
        return;
      }
      if (this.passingTest.testingPassing.finalScore != null && this.role === 'rater') {
        this.$router.go(-1);
      } else {
        if (this.files.length) {
          for (let i = 0; i < this.files.length; i++) {
            await this.$store.dispatch('loadFile', this.files[i]).then((url) => {
              this.$store.commit('pickAnswer', {
                questionId: this.currentStage.questions[0].id,
                answerId: url,
                type: 'files',
                meta: {index: i}
              })
            })
          }
        }
        this.disabledButton = true;
        await this.$store.dispatch('savePassingTesting', {role: this.role, finish: false});
      }

    },
    setColor(e, color) {
      if (this.passingTest.testingPassing.finalScore !== null) {
        e.preventDefault();
        return false;
      }
      this.$store.commit('setBlankColor', color);
    }

  },
  beforeMount() {
    scroll(0, 0);

    let passingTestingId = Cookies.get('testingPassingId');
    if (this.role === 'rater') {
      if (passingTestingId && passingTestingId !== 'null') {
        this.$store.commit('setTestingPassingId', passingTestingId);
      } else {
        this.$router.go(-1);
      }
    }
    this.$store.dispatch('fetchEventsTesting', {
      testingId: this.exerciseId,
      eventId: this.eventId,
      role: this.role,
      passingId: this.passingId,
      isPassTesting: true
    });
  },
  beforeRouteLeave() {
    this.$store.commit('clearPassing');
    Cookies.remove('testingPassingId');

  }
}
</script>

