<template>
  <div class="answer-wrapper">
    <a-radio v-for="item in answers" :key="item.id" :value="item.id"
             :class="{'blocked':rater}"
             :checked="passingTest.testingPassing.answers.map(question=>question.questionId).indexOf(questionId) !==-1? passingTest.testingPassing.answers[passingTest.testingPassing.answers.map(question=>question.questionId).indexOf(questionId)].answers===item.id:null"
             @change="e=>$store.commit('pickAnswer',{questionId,answerId:item.id,type})">
      {{
        item.body
      }}
      <a-icon style="margin-left: 10px" v-if="rater">
        <close-square-outlined v-if="!item.correct" style="color: red;font-size: 17px"/>
        <check-square-outlined v-if="item.correct" style="color:lawngreen;font-size: 17px"/>
      </a-icon>
    </a-radio>
  </div>
</template>

<script>
import {CloseSquareOutlined, CheckSquareOutlined} from "@ant-design/icons-vue";

export default {
  components: {CloseSquareOutlined, CheckSquareOutlined},
  name: "SingleAnswer",
  props: ['answers', 'questionId', 'type', 'rater'],
  computed: {
    passingTest() {
      return this.$store.getters.passingTest;
    }
  }
}
</script>

<style scoped>
.answer-wrapper {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.blocked {
  cursor: default;
  pointer-events: none;
}

</style>