<script>
import {IMaskComponent} from "vue-imask";
import cities from "@/lib/cities.JSON";
import locale from "ant-design-vue/es/date-picker/locale/ru_RU";
import dayjs from "dayjs";
import "dayjs/locale/ru";
import HeaderStarted from "@/components/personal/HeaderStarted";
import {errorHandler} from "@/lib/errorHandler";

export default {
  name: "RegistrationPage",
  components: {HeaderStarted, IMaskComponent},
  computed: {
    firstName: {
      get() {
        return this.name[0];
      },
      set(val) {
        if (!/^([a-zа-яё]+)$/i.test(val) && val) return;
        this.name[0] = val;
      },
    },
    secondName: {
      get() {
        return this.name[1];
      },
      set(val) {
        if (!/^([a-zа-яё]+)$/i.test(val) && val) return;
        this.name[1] = val;
      },
    },
    thirdName: {
      get() {
        return this.name[2];
      },
      set(val) {
        if (!/^([a-zа-яё]+)$/i.test(val) && val) return;
        this.name[2] = val;
      },
    },
    birth: {
      get() {
        return this.birthDate ? dayjs(this.birthDate) : null;
      },
      set(val) {
        this.birthDate = val?.startOf('day')?.toDate();
      },
    }
  },
  data() {
    return {
      name: [null, null, null],
      phone: null,
      password: null,
      enterPassword: null,
      city: null,
      birthDate: null,
      sex: null,
      email: null,

      //JSON DATA

      cities,

      //MASK

      mask: "+{7}(000)000-00-00",
      unmaskedPhone: null,

      //LANGUAGE
      locale,
      dayjs,
      accept: false,
    };
  },
  methods: {
    submitRegistration() {


      const emptyFields = [];

      if (!this.firstName?.trim()) {
        emptyFields.push('имя')
      }

      if (!this.secondName?.trim()) {
        emptyFields.push('фамилия')
      }

      if (this.phone?.length < 11) {
        emptyFields.push('номер телефона')
      }

      if (!this.password) {
        emptyFields.push('пароль')
      }

      if (!this.sex) {
        emptyFields.push('пол')
      }

      if (!this.city) {
        emptyFields.push('город')
      }

      if (!this.email) {
        emptyFields.push('город')
      }

      if (emptyFields.length) {
        errorHandler({name: 'requiredField', msg: emptyFields.join(', ')})
        return
      }

      if (!/^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(this.email)) {
        errorHandler({name: 'incorrectEmail'})
        return;
      }

      if (this.password && this.password !== this.enterPassword) {
        errorHandler({name: 'incorrectPasswordEnter'});
        return;
      }

      this.$store.dispatch("register", {
        name: this.name.join(" "),
        phone: this.unmaskedPhone,
        password: this.password,
        birth: this.birthDate,
        sex: this.sex,
        city: this.city,
        email: this.email,
        registrationDate: new Date(),
      }).then((status) => {
        errorHandler({name: 'registration', status})
      })
    },
  },
  beforeMount() {
    this.dayjs.locale("ru");
  },
};
</script>

