<template>
  <div class="hackathons-container" v-if="hackathonsList">
    <div class="hackathons-filling">
      <a-table :columns="columns" :data-source="hackathonsList">
        <template #bodyCell="{column,record}">
          <div class="" v-if="column?.dataIndex==='startDate'||column?.dataIndex==='endDate'">
            {{
              record?.[column?.dataIndex] ? dayjs(record?.[column?.dataIndex]).format('DD-MM-YYYY') : 'Не назначено'
            }}
          </div>
          <div class="" v-if="column?.key==='status'">
            <a-tag v-if="checkStatus(record)==='notStart'" style="border-radius: 18px">
              Не началось
            </a-tag>
            <a-tag v-if="checkStatus(record)==='process'" style="border-radius: 18px" color="cyan">
              Идет
            </a-tag>
            <a-tag v-if="checkStatus(record)==='end'" style="border-radius: 18px" color="red">
              Завершено
            </a-tag>
          </div>
          <div class="" v-if="column?.key==='link'">
            <router-link :to="{name:'hackathonTable',params:{hackathonId: record.id}}" target="_blank">Открыть таблицу
            </router-link>

          </div>
          <div class="edit-buttons" v-if="column?.key==='actions'">
            <a-button @click="editHackathon(record)">Редактировать</a-button>
<!--            <a-button danger @click="deleteHackathon(record)">-->
<!--              <template #icon>-->
<!--                <delete-outlined></delete-outlined>-->
<!--              </template>-->
<!--            </a-button>-->
          </div>
        </template>
      </a-table>
    </div>

  </div>
</template>


<script>
import {useStore} from "vuex";
import {computed} from "vue";
import {useRouter} from "vue-router";
import {appConfig} from "@/lib/appConfig";
import dayjs from "dayjs";
// import {DeleteOutlined} from "@ant-design/icons-vue";

export default {
  name: 'HackathonsListPage',
  methods: {dayjs},
  components: {
    // DeleteOutlined
  },
  computed: {
    appConfig() {
      return appConfig
    }
  },
  setup() {
    const store = useStore();
    store.dispatch('fetchHackathonsList');
    const router = useRouter();
    const hackathonsList = computed(() => store.getters.hackathonsList);
    const columns = [
      {
        title: 'Название',
        dataIndex: 'name',
        ellipsis: true
      },
      {
        title: 'Описание',
        dataIndex: 'description',
        ellipsis: true
      },
      {
        title: 'Дата начала',
        dataIndex: 'startDate',
      },
      {
        title: 'Дата окончания',
        dataIndex: 'endDate',
      },
      {
        title: 'Статус',
        key: 'status',
      },
      {
        title: 'Ссылка',
        key: 'link',
      },
      {
        title: 'Действия',
        key: 'actions',
        width: 230
      },
    ];


    const editHackathon = (hackathon) => {
      router.push({name: 'hackathonEditor', params: {hackathonId: hackathon?.id}});
    }

    const checkStatus = (hackathon) => {
      if (!hackathon?.startDate || !hackathon?.endDate || new Date() < new Date(hackathon?.startDate)) {
        return 'notStart';
      }
      return new Date() > new Date(hackathon?.endDate) ? 'end' : 'process';
    }



    return {
      hackathonsList,
      columns,

      editHackathon,
      checkStatus,
      // deleteHackathon
    }

  }
}
</script>


<style scoped>
.hackathons-container {
  padding: 24px;
}

.hackathons-filling {
  padding: 24px;
  box-shadow: 0px 2px 8px 0px #00000026;
}

.edit-buttons {
  display: flex;
  gap: 5px;
}
</style>