import {defaultHttpResHandler} from "@/store/helpers/defaultHttpResHandler";
import {makeDeleteReq, makeGetReq, makePostReq, makePutReq} from "@/store/helpers/request";
import {appConfig} from "@/lib/appConfig";


export const fetchUsersList = async (token, name, city, sex, minBirthDate, maxBirthDate, sortRegDate, externalId, phone, role) => await defaultHttpResHandler(async () => {
        return await makeGetReq(appConfig.domain + '/client/all?name=' + name + '&city=' + city + '&sex=' + sex + (minBirthDate ? '&minBirthDate=' + minBirthDate : '') + (maxBirthDate ? '&maxBirthDate=' + maxBirthDate : '') + `&sortRegDate=${sortRegDate}&externalId=${externalId ? externalId : ''}&phone=${phone ? phone : ''}&userRole=${role ? role : ''}`, {token});
    }
)

export const fetchFromQuestions = async (token, id) => await defaultHttpResHandler(async () => {
        return await makeGetReq(appConfig.domain + '/testing/' + id, {token});
    }
)

export const fetchUserById = async (token, id) => await defaultHttpResHandler(async () => {
        return await makeGetReq(appConfig.domain + '/client/byid?id=' + id, {token});
    }
)

export const createMember = async (token, data) => await defaultHttpResHandler(async () => {
        return await makePostReq(appConfig.domain + '/client', data, {token, data: true});
    }
)
export const updateMember = async (token, data) => await defaultHttpResHandler(async () => {
        return await makePutReq(appConfig.domain + '/participant', data, {token, data: true});
    }
)

export const fetchPostMessages = async (token, page, limit) => await defaultHttpResHandler(async () => {
        return await makeGetReq(appConfig.domain + '/email/all?page=' + page + '&limit=' + limit, {token});

    }
)

export const fetchRaters = async (token, page, limit) => await defaultHttpResHandler(async () => {
        return await makeGetReq(appConfig.domain + '/rater/all?page=' + page + '&limit=' + limit, {token});

    }
)

export const fetchUsersByEventId = async (token, id) => await defaultHttpResHandler(async () => {
        return await makeGetReq(`${appConfig.domain}/client/byevent/${id}`, {token});

    }
)


export const sendPostMessage = async (token, data) => await defaultHttpResHandler(async () => {
        return await makePostReq(appConfig.domain + '/email/send', data, {token, data: true});
    }
)

export const saveRaterData = async (token, data) => await defaultHttpResHandler(async () => {
        return await makePostReq(appConfig.domain + '/rater/edit', data, {token, data: true});
    }
)


export const calculateSoloParamCharts = async (token, funcType, questionId) => await defaultHttpResHandler(async () => {
        return await makeGetReq(appConfig.domain + '/stats/v2?questionId=' + questionId + '&funcType=' + funcType, {token});
    }
)


export const createEvent = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(appConfig.domain + '/event', data, {
        data: true,
        token
    })
)

export const fetchEventXLSByStageId = async (token, eventId, stageId) => await defaultHttpResHandler(async () => await makeGetReq(appConfig.domain + `/event/get/xls/${eventId}?stageId=${stageId}`, {
        isBlob: true,
        token
    })
)


export const fetchOwnEvents = async (token, name, minStartDate, maxStartDate, minEndDate, maxEndDate) => await defaultHttpResHandler(async () =>
    await makeGetReq(appConfig.domain + '/event/get/my?name=' + name + (minStartDate ? '&minStartDate=' + minStartDate : '') + (maxStartDate ? '&maxStartDate=' + maxStartDate : '') + (minEndDate ? '&minEndDate=' + minEndDate : '') + (maxEndDate ? '&maxEndDate=' + maxEndDate : ''), {token})
);

export const saveRater = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(appConfig.domain + '/client/rate', data, {
    data: true,
    token
}))

export const fetchEventsStatistic = async (token, id) => await defaultHttpResHandler(async () => await makeGetReq(appConfig.domain + '/charts/' + id, {
    token
}))

export const fetchEventsResults = async (token, id) => await defaultHttpResHandler(async () => await makeGetReq(appConfig.domain + '/event/final/' + id, {
    token
}))

export const saveStatistics = async (token, data, id) =>
    await defaultHttpResHandler(async () => await makePostReq(appConfig.domain + '/charts/' + id, data, {
        token,
        data: true
    }))


export const calculateCharts = async (token, tag1, tag2, operator, eventId) =>
    await defaultHttpResHandler(async () => await makeGetReq(appConfig.domain + '/stats?tag1=' + tag1 + '&tag2=' + tag2 + '&operators=' + operator + (eventId ? `&eventId=${eventId}` : ''), {
        token,
    }))


export const deleteUser = async (token, userId) =>
    await defaultHttpResHandler(async () => await makeDeleteReq(`${appConfig.domain}/client/${userId}`, null, {
        token,
    }))








