<template>
  <div class="container">
    <div class="table-container">
      <div class="table-header">
        <a-typography-title class="title" :level="3">Список участников</a-typography-title>
        <div class="buttons-container">
          <a-dropdown :trigger="['click']" v-model:visible="viewColumns">
            <a-button shape="round">
              <template #icon>
                <unordered-list-outlined/>
              </template>
              Выбрать колонки
            </a-button>
            <a-button
                :disabled="!searchParams.name&&!searchParams.sex&&!searchParams.city&&!searchParams.phone && !searchParams.minBirthDate && ! searchParams.maxBirthDate"
                shape="round"
                @click="dropAllFilters">Сбросить все фильтры
            </a-button>
            <template #overlay>
              <a-menu>
                <a-menu-item>
                  <a-checkbox-group style="display: flex; flex-direction: column; gap: 6px"
                                    v-model:value="selectedColumns" :options="columns.map(item=>{
                    return {
                      value:item.key,
                      label:item.title
                    }
                  })">{{
                      item.title
                    }}
                  </a-checkbox-group>
                </a-menu-item>
                <a-menu-item>
                  <a-button @click="selectedColumns=[]">Сбросить</a-button>
                </a-menu-item>
              </a-menu>
            </template>
          </a-dropdown>
          <a-dropdown :trigger="['click']" v-if="false">
            <a-button>
              <template #icon>
                <filter-outlined/>
              </template>
              Фильтры
            </a-button>
            <template #overlay>
              <a-menu>
                <a-menu-item>
                  <a href="javascript:;">1st menu item</a>
                </a-menu-item>
                <a-menu-item>
                  <a href="javascript:;">2nd menu item</a>
                </a-menu-item>
                <a-menu-item>
                  <a href="javascript:;">3rd menu item</a>
                </a-menu-item>
              </a-menu>
            </template>
          </a-dropdown>
        </div>
      </div>
      <div class="table-content">
        <a-table :data-source="users" :columns="checkColumns()" class="table">
          <template #bodyCell="{column,record}">
            <div class="" v-if="column.key==='actions'">
              <div class="" style="display: flex; gap: 10px">
                <a-button @click="$router.push({name:'currentMember',params:{id:record.id}})">Редактировать</a-button>
                <a-button danger @click="deleteUser(record.id)">
                  <template #icon>
                    <delete-outlined></delete-outlined>
                  </template>
                </a-button>
              </div>
            </div>
            <div class="" v-if="column.key==='sex'">
              {{ record.sex === 'man' ? 'Мужской' : record.sex === 'girl' ? 'Женский' : '' }}
            </div>
            <div class="" v-if="column.key==='registrationDate'||column.key==='birth'">
              {{ record[column.key] ? dayjs(record?.[column?.dataIndex]).format('DD-MM-YYYY') : '' }}
            </div>

          </template>

          <template #customFilterDropdown="{column,confirm}">
            <div style="padding: 8px">

              <div class="filter-el" v-if="column.dataIndex==='name'">
                <a-input
                    placeholder="Найти по имени"
                    v-model:value="searchParams.name"
                    style="width: 188px; margin-bottom: 8px; display: block"
                    @pressEnter="fetchUserList(confirm)"
                />
              </div>

              <div class="filter-el" v-if="column.dataIndex==='sex'">
                <a-select style="width: 150px" v-if="column.dataIndex==='sex'"
                          v-model:value="searchParams.sex"
                          placeholder="Фильтр по полу">
                  <a-select-option value="man">
                    Мужской
                  </a-select-option>
                  <a-select-option value="girl">
                    Женский
                  </a-select-option>

                </a-select>
              </div>


              <div class="filter-el" v-if="column.dataIndex==='city'">
                <a-select v-if="column.dataIndex==='city'"
                          show-search
                          style="width:100%"
                          v-model:value="searchParams.city"
                          placeholder="Фильтр по городу">
                  <a-select-option v-for="item in cities" :key="item.name+item.subject" :value="item.name">
                    {{ item.name }}
                  </a-select-option>

                </a-select>
              </div>


              <div class="filter-el" v-if="column.dataIndex==='phone'">
                <a-input
                    placeholder="Найти по телефону"
                    v-model:value="searchParams.phone"
                    style="width: 188px; margin-bottom: 8px; display: block"
                    @pressEnter="fetchUserList(confirm)"
                />
              </div>

              <div class="filter-el" v-if="column.dataIndex==='registrationDate'">

                <a-select style="width:100%"
                          v-model:value="searchParams. sortRegDate"
                          placeholder="Сортировать по">

                  <a-select-option value="desc">
                    Сначала новые
                  </a-select-option>
                  <a-select-option value="asc">
                    Сначала старые
                  </a-select-option>


                </a-select>

                <a-date-picker placeholder="От" :locale="locale"
                               v-model:value="searchParams.minBirthDate"></a-date-picker>

                <a-date-picker placeholder="До" :locale="locale"
                               v-model:value="searchParams. maxBirthDate"></a-date-picker>

              </div>


              <a-button shape="round"
                        type="primary"
                        size="small"
                        style="width: 90px; margin-right: 8px"
                        @click="fetchUserList(confirm)"
              >
                Поиск
              </a-button>
              <a-button shape="round" size="small" style="width: 90px" @click="dropValue(column.key,confirm)">
                Сбросить
              </a-button>
            </div>

          </template>
          <template #customFilterIcon="{column}">
            <search-outlined v-if="column.dataIndex==='name'"
                             :style="{ color: searchParams.name ? '#108ee9' : undefined }"/>
            <filter-outlined v-if="column.dataIndex==='sex'"
                             :style="{ color: searchParams.sex ? '#108ee9' : undefined }"/>
            <filter-outlined v-if="column.dataIndex==='city'"
                             :style="{ color: searchParams.city ? '#108ee9' : undefined }"/>
            <filter-outlined v-if="column.dataIndex==='phone'"
                             :style="{ color: searchParams.phone ? '#108ee9' : undefined }"/>
            <filter-outlined v-if="column.dataIndex==='registrationDate'"
                             :style="{ color: searchParams.minBirthDate||searchParams.maxBirthDate ? '#108ee9' : undefined }"/>

          </template>
        </a-table>
      </div>
    </div>
  </div>
</template>

<script>
import {UnorderedListOutlined, FilterOutlined, SearchOutlined, DeleteOutlined} from "@ant-design/icons-vue";
import dayjs from "dayjs";
import locale from "ant-design-vue/es/date-picker/locale/ru_RU"
import 'dayjs/locale/ru';

import cities from '@/lib/cities.JSON'


export default {
  components: {
    DeleteOutlined,
    SearchOutlined,
    UnorderedListOutlined, FilterOutlined
  },
  computed: {
    users() {
      return this.$store.getters.usersList;
    }
  },
  name: "MembersPage",
  data() {
    return {
      cities,
      locale,
      columns: [
        {
          title: 'ФИО',
          key: 'name',
          dataIndex: 'name',
          customFilterDropdown: true
        },
        {
          title: 'Пол',
          key: 'sex',
          dataIndex: 'sex',
          customFilterDropdown: true,
        },
        {
          title: 'Дата рождения',
          key: 'birth',
          dataIndex: 'birth',
        },
        {
          title: 'Город',
          key: 'city',
          dataIndex: 'city',
          customFilterDropdown: true,

        },
        {
          title: 'Телефон',
          key: 'phone',
          dataIndex: 'phone',
          customFilterDropdown: true,

        },
        // {
        //   title: 'Почта',
        //   key: 'email',
        //   dataIndex: 'email'
        // },
        {
          title: 'Дата регистрации',
          key: 'registrationDate',
          dataIndex: 'registrationDate',
          customFilterDropdown: true,

        },
        {
          title: 'Действия',
          key: 'actions',
        },
      ],

      dateOptions: {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      },

      viewColumns: false,
      selectedColumns: [],

      searchParams: {
        name: null,
        city: null,
        sex: '',
        phone: null,
        maxBirthDate: null,
        minBirthDate: null,
        sortRegDate: 'desc'
      }

    }
  },
  methods: {
    deleteUser(userId) {
      this.$store.dispatch('deleteUser', userId);
    },
    dayjs,
    checkColumns() {
      if (!this.selectedColumns.length) {
        return this.columns
      } else {
        return this.columns.filter(item => this.selectedColumns.includes(item.key));
      }
    },
    fetchUserList(confirm) {
      confirm?.();
      this.$store.dispatch('fetchUsersList', this.searchParams);
    },
    dropValue(field, confirm) {
      this.searchParams[field] = null;
      if (field === 'registrationDate') {
        this.searchParams.maxBirthDate = null;
        this.searchParams.minBirthDate = null;
        this.searchParams.sortRegDate = 'desc';
      }
      this.fetchUserList(confirm);
    },
    dropAllFilters() {
      this.searchParams = {
        name: null,
        city: null,
        sex: '',
        phone: null,
        maxBirthDate: null,
        minBirthDate: null,
        sortRegDate: 'desc'
      }
      this.fetchUserList()
    }
  },
  beforeMount() {
    dayjs.locale('ru');
    this.fetchUserList();
  },
}
</script>

