<template>
  <div class="modal" @click="closeModal">
    <div class="modal-filling" id="stream-modal">
      <div class="caption">
        <button class="close" id="close-btn" @click="closeModal"></button>
      </div>
      <img src="@/assets/icons/stream-icon.svg" alt="" class="icon">
      <div class="description">
        <p class="description-title">Ближайшая трансляция</p>
        <p class="description-text">{{ streamTime }}</p>
      </div>
      <div class="info">
        <p class="info-title">{{ stream?.translation?.title }}</p>
        <p class="info-text">{{
            stream?.translation?.description
          }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import {useStore} from "vuex";
import {computed} from "vue";
import {useRouter} from "vue-router";

export default {
  name: "StreamModal",
  props: ['stream'],
  setup(props) {

    const store = useStore();
    const router = useRouter();

    const streamTime = computed(() => {
      if (new Date() > new Date(props.stream?.startDate)) {
        return 'Уже идет'
      } else {
        const difference = new Date(props.stream?.startDate) - new Date();
        const days = Math.floor(difference / 86400000);
        const totalMinutes = ((difference % 86400000) / (60 * 1000));
        const hours = Math.floor(totalMinutes / 60);
        const minutes = Math.floor(totalMinutes % 60);
        if (days) {
          return `Через ${days} ${days === 1 ? 'день' : days < 5 ? 'дня' : 'дней'},  ${hours} ч. ${minutes} мин.`
        }

        return `Через  ${hours} ч. ${minutes} мин.`
      }
    })
    const closeModal = (event) => {
      store.commit('setViewStreamModal', false);
      store.commit('setStreamData', null);

      if (event?.target?.id !== 'close-btn' && document.getElementById('stream-modal')?.contains(event?.target)) {
        router.push({name: 'separateEvent', params: {eventId: props.stream?.eventId}})
      }

    }
    return {
      closeModal,
      streamTime
    }
  }

}
</script>

