import {defaultHttpResHandler} from "@/store/helpers/defaultHttpResHandler";
import {makeGetReq, makePostReq} from "@/store/helpers/request";
import {appConfig} from "@/lib/appConfig";


export const fetchInterviewById = async (token, id) => defaultHttpResHandler(async () => {
    return await makeGetReq(appConfig.domain + '/interview/get/' + id, {token})
});

export const fetchAllInterviews = async (token) => defaultHttpResHandler(async () => {
    return await makeGetReq(appConfig.domain + '/interviews', {token})
});

export const saveInterview = async (token, data) => defaultHttpResHandler(async () => {
    return await makePostReq(appConfig.domain + '/interview/store', data, {token, data: true})
});