import {useToast} from "vue-toastification";
import router from "@/router";
import store from "@/store";

const toast = useToast();

const notification = {
    params: {
        position: "top-center",
        timeout: 5000,
        closeOnClick: true,
        pauseOnFocusLoss: true,
        pauseOnHover: true,
        draggable: true,
        draggablePercent: 0.6,
        showCloseButtonOnHover: false,
        hideProgressBar: false,
        closeButton: "button",
        icon: true,
        rtl: false
    },
    successNotification(app, msg) {
        toast.success(msg, this.params);
    },
    errorNotification(app, msg) {
        toast.error(msg, this.params);
    },
    warningNotification(app, msg) {
        toast.warning(msg, this.params);
    },
    infoNotification(app, msg) {
        toast.info(msg, {
            position: "top-center",
            timeout: 8000,
            closeOnClick: true,
            pauseOnFocusLoss: true,
            pauseOnHover: true,
            draggable: true,
            draggablePercent: 0.6,
            showCloseButtonOnHover: false,
            hideProgressBar: false,
            closeButton: "button",
            icon: true,
            rtl: false
        });
    }
};

export function errorHandler(event) {
    if (event?.status === 400 || event?.status === 401 || event?.status === 403) {

        if (event?.statusCode === 'InvalidToken') {
            notification.infoNotification(this, 'Требуется авторизация');
            store.dispatch('logout').then(function () {
                router.push({name: 'authorization'});
            });
        }

        if (event?.statusCode === 'TokenExpired') {
            notification.infoNotification(this, 'Требуется авторизация');
            store.dispatch('logout').then(function () {
                router.push({name: 'authorization'});
            });
        }
        if (event?.statusCode === 'NoPermissions') {
            notification.infoNotification(this, 'Аккаунт с таким номером или email существует');
        }

        if (event?.statusCode === 'NotFoundError') {
            notification.infoNotification(this, 'Аккаунт не найден, либо пароль неверный');
        }


        return;

    }
    if (event?.status === 500) {
        notification.errorNotification(this, 'Ошибка');
        return;
    }

    switch (event.name) {

        case 'deleteUser':
            if (event.status === 200) {
                notification.infoNotification(this, 'Пользователь удален');
            } else {
                notification.warningNotification(this, 'Не удалось удалить пользователя');
            }
            break;

        case 'notAllPass':
            notification.infoNotification(this, `Необходимо дать ответ на следующие вопросы: ${event?.msg}`);
            break;

        case 'approvedRegistration':
            router.push({name: 'authorization'}).then(() => {
                if (event?.status === 200) {
                    notification.successNotification(this, `Аккаунт подтвержден`);
                } else {
                    notification.warningNotification(this, `Аккаунт не подтвержден`);
                }
            })
            break;

        case 'restorePasswordSendEmail':
            router.push({name: 'authorization'}).then(() => {
                notification.infoNotification(this, `На ваш email отправлено сообщение с инструкциями`);
            })
            break;
        case 'restorePassword':
            router.push({name: 'authorization'}).then(() => {
                notification.successNotification(this, `Ваш пароль успешно обновлен`);
            })
            break;

        case 'incorrectEmail':
            notification.infoNotification(this, `Некорректный email`);
            break;

        case 'streamEdit':
            notification.successNotification(this, 'Успешно');
            router.push({name: 'testingList'})
            break;

        case 'imgSize':
            notification.infoNotification(this, 'Недопустимый размер изображения');
            break;

        case 'requiredField':
            notification.infoNotification(this, `Необходимо заполнить следующие поля: ${event.msg}`);
            break;
        case 'incorrectPasswordEnter':
            notification.warningNotification(this, `Пароли не совпадают`);
            break;

        case 'changeRater':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'Успешно');
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;
        case 'member':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'Успешно');
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;
        case 'postMessage':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'Успешно отправлено');
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;

        case 'registration':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'На указанный вами email отправлено письмо. Для подтверждения регистрации необходимо пройти по ссылке в письме.');
                    router.push({name: 'authorization'});
                    break;
                case 402:
                    notification.warningNotification(this, 'Пользователь с такой почтой или номером телефона уже существует');
                    break;
                case 300:
                    notification.warningNotification(this, 'Не все данные заполнены');
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;
        case 'login':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'Успешно');
                    if (localStorage.getItem('redirectRoute')) {
                        router.push(JSON.parse(localStorage.getItem('redirectRoute'))).then(() => {
                            localStorage.removeItem('redirectRoute');
                        })
                        break;
                    }
                    router.push({name: 'myEvents'});
                    break;
                // case 402:
                //     notification.warningNotification(this, 'Неверный логин/пароль');
                //     break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;
        case 'event':
            switch (event.status) {
                case 200:
                    break;
                case 300:
                    notification.warningNotification(this, 'Этап недоступен');
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;

        case 'updateUser':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'Успешно')
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;
        case 'logout':
            router.push({name: 'authorization'});
            break;

        case 'createEvent':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'Успешно')
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;

        case 'testEditor':
            switch (event.status) {
                case 200:
                    router.push({name: 'testingList'});
                    notification.successNotification(this, 'Успешно');
                    break;

                case 300:
                    notification.infoNotification(this, event.meta.body);
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;

        case 'eventEditor':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'Успешно');
                    break;
                case 300:
                    notification.infoNotification(this, 'Не все данные заполнены');
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;

        case 'passing':
            switch (event.status) {
                case 200:
                    if (event.meta.finish) {
                        notification.infoNotification(this, 'Тестирование завершено');
                        router.push({name: 'separateEvent', params: {eventId: event.meta.eventId}})
                    } else {
                        notification.successNotification(this, 'Успешно');
                    }
                    router.go(-1)
                    break;
                case 300:
                    notification.infoNotification(this, event.meta.body);
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;
        case 'appointment':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'Успешно');
                    break;

                case 300:
                    notification.infoNotification(this, event.meta.body);
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;
        case 'interview':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, 'Успешно');
                    if (event.meta.role === 'admin') {
                        router.push({name: 'testingList'})
                    }
                    break;

                case 300:
                    notification.infoNotification(this, event.meta.body);
                    break;
                default:
                    notification.errorNotification(this, 'Ошибка');
            }
            break;
    }
}