import {
    fetchStreamById,
    fetchStreams,
    saveStream,
} from "@/store/stream/serverInterationMethods";
import {errorHandler} from "@/lib/errorHandler";

export const stream = {
    actions: {

        async fetchStreams({getters, commit}) {
            const {data} = await fetchStreams(getters.token);
            commit('setStreamsList', data);
        },

        async fetchStreamById({getters, commit}, id) {
            let data;

            if (id === 'new') {
                data = {
                    title: null,
                    body: null,
                    description: null,

                }
            } else {
                const response = await fetchStreamById(getters.token, id);
                data = response.data;
            }
            commit('setStreamData', Array.isArray(data) ? data[0] : data);
        },
        async saveStream({getters}) {
            const {status} = await saveStream(getters.token, getters.stream);
            errorHandler({name: 'streamEdit', status});
        },
        async fetchLastStream({getters, commit}) {
            const {data} = await fetchStreamById(getters.token, 'last');
            commit('setStreamData', Array.isArray(data) ? data[0] : data);
            commit('setViewStreamModal', true);
        },
    },
    mutations: {

        setStreamData(state, data) {
            state.stream = data;
        },
        changeStreamData(state, {field, val}) {
            state.stream[field] = val;
        },
        setStreamsList(state, data) {
            state.streamsList = data;
        },
        setViewStreamModal(state, data) {
            state.viewStreamModal = data;
        }
    },
    state: {
        stream: null,
        streamsList: [],
        viewStreamModal: false
    },
    getters: {
        stream: (state) => state.stream,
        streamsList: state => state.streamsList,
        viewStreamModal: state => state.viewStreamModal,

    }
}