<template>
  <div class="main-container">
    <div class="question-container">
      <a-typography-title :level="2">{{ question?.order + 1 }}</a-typography-title>
      <div class="arrow-buttons" v-if="mode!=='exercise'">
        <a-button :disabled="!question.order" @click="swapQuestion(question.order-1)">
          <template #icon>
            <caret-up-outlined/>
          </template>
        </a-button>
        <a-button :disabled="question.order===questionsLength-1" @click="swapQuestion(question.order+1)">
          <template #icon>
            <caret-down-outlined/>
          </template>
        </a-button>
      </div>

      <div class="question-container-filling">
        <div class="question-filling-element">
          <a-typography-text>Тип вопроса:</a-typography-text>
          <a-select :value="question.type"
                    @change="val=>$store.commit('setQuestionType',{index:qIndex,val})"
                    placeholder="Тип вопроса">
            <a-select-option value="single" v-if="mode==='test'||mode==='form'">Выбор ответа</a-select-option>
            <a-select-option value="multi" v-if="mode==='test'||mode==='form'">Несколько вариантов</a-select-option>
            <a-select-option value="multi-open" v-if="mode==='test'">Самостоятельный ввод</a-select-option>

            <a-select-option value="conformity" v-if="mode==='test'">Сопоставление</a-select-option>
            <a-select-option value="date" v-if="mode==='form'">Дата</a-select-option>
            <a-select-option value="file" v-if="mode==='form'">Файл</a-select-option>
            <a-select-option value="select" v-if="mode==='form'">Выпадающий список</a-select-option>
            <a-select-option value="open" v-if="mode==='test'||mode==='form'||mode==='exercise'">Длинный текст
            </a-select-option>
            <a-select-option value="text" v-if="mode==='form'">Короткий текст</a-select-option>
            <a-select-option value="cities" v-if="mode==='form'">Города</a-select-option>

          </a-select>
        </div>
        <div class="question-filling-element" v-if="question.type&&question.type!=='conformity'">
          <a-typography-text>Вопрос:</a-typography-text>
          <a-input placeholder="Вопрос" :value="question.body"
                   @change="e=>$store.commit('setQuestionBody',{val:e.target.value,index:qIndex})"></a-input>
        </div>
        <div class="question-filling-element" v-if="question.type&&mode!=='form'">
          <a-typography-text>Максимальный балл:</a-typography-text>
          <a-input-number style="width: 100%" placeholder="Максимальный балл" :value="question.maxScore" :min="1"
                          @change="val=>$store.commit('setQuestionMaxScore',{val:val,index:qIndex})"></a-input-number>
        </div>
        <div class="question-filling-element" v-if="mode==='form'">
          <a-checkbox style="width: 100%"
                      :checked="question.required"
                      @change="val=>$store.commit('setQuestionRequired',{index:qIndex})">
            Обязательно для заполнения
          </a-checkbox>
        </div>
      </div>
      <div class="img-block" v-if="!(question.type&&question.type==='conformity'||mode==='exercise')&&mode!=='form'">
        <img class="question-file"
             :src="question.fileUrl&&question.fileUrl!=='null'?question.fileUrl:require('@/assets/placeholder-image.png')"
             alt="">
        <input type="file" hidden ref="file" @change="loadFileQueue($refs.file,{type:'questions'})">
        <a-button shape="round" style="width: 200px" type="primary" ghost @click="$refs.file.click()">Загрузить файл
        </a-button>
      </div>
      <a-button v-if="!question.id" shape="round" class="close-btn" @click="$store.commit('deleteQuestion',qIndex)"
                style="margin-left: 15px" size="small" danger ghost>
        <close-outlined></close-outlined>
      </a-button>
    </div>
    <div class="answers-list"
         v-if="question.type&&question.type!=='open'&&question.type!=='file'&&question.type!=='text'&&question.type!=='cities'&&question.type!=='date'">
      <a-typography-title :level="5">Ответы:</a-typography-title>

      <TestingAnswer v-show="question.type!=='conformity'"
                     v-for="(item,index) in question.answers" :key="item.id"
                     :type="question.type" :answer="item"
                     :q-index="qIndex"
                     :a-index="index">
      </TestingAnswer>
      <a-list v-if="question.type==='conformity'"
              :grid="{ gutter: 16, xs: 1, sm: 1, md: 2, lg: 3, xl: 4, xxl: 5, xxxl: 6 }"
              :data-source="question.answers"
      >
        <template #renderItem="{ item,index }">
          <a-list-item>
            <div class="" style="display: flex;gap: 10px">
              <a-card>
                <template #title>
                  <a-button shape="round" v-if="typeAnswer==='img'" @click="$refs['img'+index].click()">Загрузить
                    <input type="file" hidden :ref="'img'+index"
                           @change="loadFileQueue($refs['img'+index],{type:'answers',index})">
                  </a-button>

                  <a-textarea v-if="typeAnswer==='text'" :value="item.body" placeholder="Вопрос"
                              @change="e=>$store.commit('setMatchingQuestionBody',{qIndex ,aIndex: index,val:e.target.value })"></a-textarea>
                </template>

                <template #cover v-if="typeAnswer==='img'">
                  <img v-if="item.matchingFileUrl" :src="item.matchingFileUrl" alt="">
                </template>
                <a-textarea placeholder="Ответ" :value="item.matchingCorrectBody"
                            @change="e=>$store.commit('setMatchingAnswerBody',{qIndex,aIndex:index,val:e.target.value})"></a-textarea>
                <template #actions>
                  <a-select v-model:value="typeAnswer" placeholder="Тип ответа"
                            @change="val=>$store.commit('changeAnswerType',{type:val,index:qIndex})"
                            style="width: 100%;height: 100%">
                    <a-select-option value="text">Текст</a-select-option>
                    <a-select-option value="img">Изображение</a-select-option>
                  </a-select>
                </template>
              </a-card>
              <a-button shape="round" class="close-btn" @click="$store.commit('deleteAnswer',{qIndex,aIndex:index})"
                        danger ghost size="small">
                <close-outlined/>
              </a-button>

            </div>
          </a-list-item>
        </template>
      </a-list>
      <a-button shape="round" style="max-width: 200px; width: 100%;" v-if="question.type!=='cities'"
                @click="$store.commit('addAnswer',{index:qIndex,meta:{type:typeAnswer}})">
        Добавить ответ
      </a-button>
    </div>
  </div>
</template>

<script>
import TestingAnswer from "@/components/testing/TestingAnswer";
import {CloseOutlined, CaretUpOutlined, CaretDownOutlined} from "@ant-design/icons-vue";

export default {
  name: "TestingQuestion",
  components: {TestingAnswer, CloseOutlined, CaretUpOutlined, CaretDownOutlined},
  props: ['question', 'qIndex', 'mode', 'questionsLength'],
  methods: {
    loadFileQueue(ref, meta) {
      let el = ref.files;
      this.$store.commit('addTestingFilesQueue', {index: this.qIndex, file: el[el.length - 1], meta});
    },
    swapQuestion(to) {
      this.$store.commit('swapQuestion', {from: this.question.order, to})
    }
  },
  data() {
    return {
      typeAnswer: 'text'
    }
  },
  beforeMount() {
    if (this.question.answers.length && this.question.type === 'conformity') {
      if (this.question.answers[0].matchingFileUrl !== 'null') {
        this.typeAnswer = 'img'
      }
    }
  }
}
</script>

