  components: {PersonalMenuMain, PersonalHeader, PersonalCaption},
  data() {
    return {
      viewMenu: false
    }
  },
  computed: {
    user() {
      return this.$store.getters.user;
    }
  },
  beforeMount() {

    if (!this.user?.verified && !(sessionStorage.getItem('approveRegistration') === 'true')) {
      notification.info({
        message: 'Необходимо подтверждение регистрации',
        description: 'На ваш email отправлено письмо, необходимо пройти по ссылке в письме.',
        duration: 0,
        key: 'approveRegistration',
        placement: 'topLeft',
        onClose() {
          sessionStorage.setItem('approveRegistration', 'true');
        }
      })
    }
  }
}
