<template>
  <a-layout style="background: #fff">
    <HeaderStarted></HeaderStarted>
    <a-layout-content style="background: #fff">
      <div class="container">
        <div class="filling">
          <h1 class="title">Восстановление пароля</h1>
          <div class="info-block">
            <h3 class="subtitle">Новый пароль:</h3>
            <a-input-password
                class="input"
                v-model:value="password"
                placeholder="Новый пароль"
            ></a-input-password>
            <h3 class="subtitle">Подтвердите новый пароль:</h3>
            <a-input-password
                class="input"
                v-model:value="confirmPassword"
                placeholder="Подтвердите новый пароль"
            ></a-input-password>
          </div>
          <a-button
              shape="round"
              class="submit-btn blue-buttons purple-btn"
              @click="restorePassword"
              type="primary"
              :disabled="!password&&!confirmPassword"
          >Сохранить
          </a-button>
        </div>
        <div class="decorations desktop">
          <img
              src="../../assets/icons/decoration-1.svg"
              alt=""
              class="decoration-1"
          />
          <img
              src="../../assets/icons/decoration-3.svg"
              alt=""
              class="decoration-3"
          />
        </div>
        <div class="decorations mobile">
          <img
              src="../../assets/icons/decoration-1-mob.svg"
              alt=""
              class="decoration-1"
          />
          <img
              src="../../assets/icons/decoration-2-mob.svg"
              alt=""
              class="decoration-2"
          />
          <img
              src="../../assets/icons/decoration-3-mob.svg"
              alt=""
              class="decoration-3"
          />
        </div>
      </div>
    </a-layout-content>
  </a-layout>
</template>

<script>
import HeaderStarted from "@/components/personal/HeaderStarted";
import {errorHandler} from "@/lib/errorHandler";
import {ref} from "vue";
import {useStore} from "vuex";

export default {
  name: "RestorePasswordPage",
  components: {HeaderStarted},
  props: ['uniqueCode'],
  setup(props) {
    const store = useStore();
    const password = ref(null);
    const confirmPassword = ref(null);
    const restorePassword = () => {
      const emptyFields = [];

      if (!password.value) {
        emptyFields.push('пароль')
      }

      if (!confirmPassword.value) {
        emptyFields.push('подтверждение пароля')
      }

      if (emptyFields.length) {
        errorHandler({name: 'requiredField', msg: emptyFields.join(', ')})
        return
      }

      if (password.value !== confirmPassword.value) {
        errorHandler({name: 'incorrectPasswordEnter'});
        return;
      }
      store.dispatch('restorePassword', {
        password: password.value,
        verificationCode: props.uniqueCode
      });
    }
    return {
      password,
      confirmPassword,
      restorePassword
    }
  }
};
</script>

