import {
    fetchHackathonById,
    fetchHackathonsList,
    fetchHackathonsResults,
    saveHackathon, saveHackathonResult
} from "@/store/hackathons/serverInterationMethods";

export const hackathons = {
    actions: {
        async fetchHackathonsList({getters, commit}) {
            const {data} = await fetchHackathonsList(getters.token);
            commit('setHackathonsList', data);
        },

        async saveHackathonResult({getters}, hackatonId) {
            await saveHackathonResult(getters.token, {hackatonId});
        },

        async fetchHackathonsResults({getters, commit}) {
            const {data} = await fetchHackathonsResults(getters.token);
            commit('setHackathonsResults', data);
        },

        async fetchHackathonById({getters, commit}, id) {
            let data;
            if (id === 'new') {
                data = {
                    name: null,
                    previewUrl: null,
                    description: null,
                    table: [],
                    startDate: null,
                    endDate: null,
                }
            } else {
                const response = await fetchHackathonById(getters.token, id);
                data = response?.data && response?.data?.length ? response.data[0] : null;
                data.table = data?.hackatonCells ? JSON.parse(JSON.stringify(data.hackatonCells)) : [];
            }
            commit('setHackathon', data);
        },

        async saveHackathon({getters, commit}) {
            const {status} = await saveHackathon(getters.token, getters.hackathon);
            if (status === 200) {
                commit('setHackathon', null);
            }
            return status;
        },
        updateTableCell({getters}, {socket, cell, hackatonId}) {

            socket?.send(JSON.stringify({
                action: 'HACKATON_UPDATE',
                token: getters.token,
                hackatonId,
                cells: [{x_coord: cell?.x_coord, y_coord: cell?.y_coord, body: cell?.body, id: cell?.id}]
            }));

        }


    },
    mutations: {
        setHackathonsList(state, data) {
            state.hackathonsList = data;
        },
        setHackathon(state, data) {
            state.hackathon = data;
        },
        setHackathonData(state, {val, field}) {
            state.hackathon[field] = val;
        },
        addHackathonColumn(state) {
            // if (column) {
            //     const index = state.hackathon?.table.map(item => item.y_coord ? null : item.x_coord).indexOf(Number(column.prop.replace('x', '')))
            //     if (index > -1) {
            //         state.hackathon.table[index].body = column.name;
            //     }
            //     return;
            // }
            const tableXCellsLength = Array.from(new Set(state.hackathon?.table?.map(item => Number(item?.x_coord)))).length;
            state.hackathon?.table?.push({
                x_coord: tableXCellsLength,
                y_coord: 0,
                updateDate: new Date(),
                body: `Колонка ${tableXCellsLength + 1}`
            });

            const tableYCellsLength = Array.from(new Set(state.hackathon?.table?.filter(item => item?.y_coord).map(item => Number(item?.y_coord)))).length;
            for (let i = tableYCellsLength; i !== 0; i--) {
                state.hackathon?.table?.push({
                    x_coord: tableXCellsLength,
                    y_coord: i,
                    updateDate: new Date(),
                    body: null
                });
            }


        },
        addHackathonRow(state) {
            const tableYCellsLength = Array.from(new Set(state.hackathon?.table?.map(item => Number(item?.y_coord)))).length;
            state.hackathon?.table?.push({
                x_coord: 0,
                y_coord: tableYCellsLength,
                updateDate: new Date(),
                body: null
            });

            const tableXCellsLength = Array.from(new Set(state.hackathon?.table?.filter(item => item?.x_coord).map(item => Number(item?.x_coord)))).length;

            for (let i = tableXCellsLength; i !== 0; i--) {
                state.hackathon?.table?.push({
                    x_coord: i,
                    y_coord: tableYCellsLength,
                    updateDate: new Date(),
                    body: null
                });
            }
        },
        updateTableCell(state, data) {
            let index;
            if (data?.id) {
                index = state.hackathon?.table?.map(item => item?.id).indexOf(data?.id);
            } else {
                index = state.hackathon?.table?.map(item => (item?.y_coord !== data?.y_coord) ? null : item?.x_coord).indexOf(data?.x_coord);
            }
            if (index > -1) {
                state.hackathon.table[index].body = data.body;
                state.hackathon.table[index].updateDate = new Date();
            } else {
                state.hackathon.table.push(data);
            }
        },

        deleteColumn(state, column) {
            for (let i = 0; i < state?.hackathon?.table?.length; i++) {
                if (state?.hackathon?.table[i].x_coord === Number(column?.dataIndex?.replace('x', ''))) {
                    state.hackathon.table[i]['delete'] = true;
                    if (!state.hackathon.table[i].id) {
                        state.hackathon.table[i]['filter'] = true;
                    }
                }
                if (state?.hackathon?.table[i].x_coord > Number(column?.dataIndex?.replace('x', ''))) {
                    state.hackathon.table[i].x_coord--;
                }
                state.hackathon.table = state.hackathon?.table?.filter(cell => !cell.filter)
            }
        },


        deleteRow(state, record) {
            for (let i = 0; i < state?.hackathon?.table?.length; i++) {
                if (state?.hackathon?.table[i].y_coord === record?.y_coord) {
                    state.hackathon.table[i]['delete'] = true;
                    if (!state.hackathon.table[i].id) {
                        state.hackathon.table[i]['filter'] = true;
                    }
                }
                if (state?.hackathon?.table[i].y_coord > record?.y_coord) {
                    state.hackathon.table[i].y_coord--;
                }
            }

            state.hackathon.table = state.hackathon?.table?.filter(cell => !cell.filter)
        },

        setHackathonsResults(state, data) {
            state.hackathonsResults = data;
        }


    },
    state: {
        hackathonsList: [],
        hackathon: null,
        hackathonsResults: []
    },
    getters: {
        hackathonsList: state => state.hackathonsList,
        hackathon: state => state.hackathon,
        hackathonsResults: state => state.hackathonsResults
    }
}