<script>
import { mapGetters } from "vuex";
// import EventCard from "@/components/events/EventCard";
import {
  UploadOutlined,
  PlusOutlined,
  MinusCircleOutlined,
  SearchOutlined,
  EditOutlined,
  FilterOutlined,
  DownloadOutlined,
  TeamOutlined,
  ArrowLeftOutlined,
  CaretRightOutlined,
  CaretLeftOutlined,
  EyeOutlined,
  EyeInvisibleOutlined,
} from "@ant-design/icons-vue";
import locale from "ant-design-vue/es/date-picker/locale/ru_RU";
import dayjs from "dayjs";
import "dayjs/locale/ru";
import StageCard from "@/components/events/StageCard";
import { errorHandler } from "@/lib/errorHandler";

export default {
  name: "OwnEventsPage",
  computed: {
    ...mapGetters(["ownEvents", "allTesting", "allInterviews", "streamsList"]),
    disabledCreateStage() {
      if (this.newStage?.type === "interview") {
        return !this.newStage?.interviewId;
      }

      if (this.newStage?.type === "stream") {
        return !this.newStage?.translationId || this.newStage?.date?.length < 2;
      }

      return !this.newStage?.testId || this.newStage?.date?.length < 2;
    },
    eventStages() {
      if (this.newEvent?.stages?.length) {
        return this.newEvent.stages.map((item) => item.testId);
      }
      return [];
    },
    haveTranslation() {
      return this.newEvent?.stages?.filter(item => !item.delete).map(item => item.type === 'stream').indexOf(true) > -1;
    }
  },
  watch: {
    status: function (val) {
      this.maxStartDate = null;
      this.minStartDate = null;
      this.maxEndDate = null;
      this.minEndDate = null;

      if (!this.ownEvents.length) {
        return;
      }

      let date;
      switch (val) {
        case 0:
          date = dayjs(
            this.ownEvents.sort(
              (a, b) => new Date(b.startDate) - new Date(a.startDate)
            )[0].startDate
          ).startOf("day");
          this.minStartDate = date.day(date.get("day") + 1);
          break;
        case 1:
          this.maxStartDate = dayjs(
            this.ownEvents.sort(
              (a, b) => new Date(a.startDate) - new Date(b.startDate)
            )[0].startDate
          ).startOf("day");
          this.maxEndDate = dayjs(
            this.ownEvents.sort(
              (a, b) => new Date(b.endDate) - new Date(a.endDate)
            )[0].endDate
          ).startOf("day");
          break;
        case 2:
          date = dayjs(
            this.ownEvents.sort(
              (a, b) => new Date(b.endDate) - new Date(a.endDate)
            )[0].endDate
          ).startOf("day");
          this.minEndDate = date.day(date.get("day") - 1);
          break;
      }
    },
  },
  data() {
    return {
      //FILTERS
      name: "",
      minStartDate: null,
      maxStartDate: null,
      minEndDate: null,
      maxEndDate: null,
      status: null,
      //EVENT
      viewEventEditor: false,
      newEvent: null,
      eventDate: null,
      //OTHER
      locale,
      dayjs,
      file: null,

      //STAGE
      viewCreateStage: false,
      createTypeStage: null,
      newStage: null,

      columns: [
        {
          title: "Название",
          dataIndex: "title",
          key: "title",
          ellipsis: true,
          customFilterDropdown: true,
        },
        {
          title: "Описание",
          dataIndex: "description",
          key: "description",
          ellipsis: true,
        },
        {
          title: "Дата начала",
          dataIndex: "startDate",
          key: "startDate",
          customFilterDropdown: true,
          width: 180,
        },
        {
          title: "Дата окончания",
          dataIndex: "endDate",
          key: "endDate",
          width: 180,
        },
        {
          title: "Статус",
          key: "status",
          width: 150,
          customFilterDropdown: true,
        },
        {
          title: "Действия",
          key: "actions",
          width: 180,
        },
      ],

      usersList: [],
      usersColumns: [],
      currentEvent: null,

      dateOptions: {
        month: "long",
        day: "numeric",
      },
    };
  },
  components: {
    StageCard,
    // EventCard,
    UploadOutlined,
    PlusOutlined,
    MinusCircleOutlined,
    EditOutlined,
    SearchOutlined,
    DownloadOutlined,
    FilterOutlined,
    TeamOutlined,
    ArrowLeftOutlined,
    EyeOutlined,
    EyeInvisibleOutlined,
    CaretRightOutlined,
    CaretLeftOutlined,
  },
  methods: {
    deleteEvent(record, val) {
      record["deleted"] = val;
      this.$store
        .dispatch("saveEvent", { event: record, isDelete: true })
        .then(this.fetchOwnEvents);
    },
    disabledApproveUserBtn(record) {
      return this.usersColumns
        .map((column) => {
          return column.type != null
            ? this.checkUserPass(record, column, true)
            : true;
        })
        .includes(false);
    },
    checkUserPass(record, column, disabled) {
      if (column.type === "interview") {
        const index = record.client.currentParticipantInInterview
          ?.map((item) => item.interviewStageSlot.interviewStageId)
          .indexOf(column.interviewId);
        if (index !== -1) {
          return record.client.currentParticipantInInterview[index]
            .interviewerId
            ? disabled
              ? true
              : "Пройдено"
            : disabled
              ? false
              : "На проверке";
        }
      } else {
        const index = record.testingPassing
          .map((item) => item.testingId)
          .indexOf(column.testId);
        if (index !== -1) {
          return record.testingPassing[index].finalScore != null
            ? disabled
              ? true
              : "Пройдено"
            : disabled
              ? false
              : "На проверке";
        }
      }
      return disabled ? false : "Не пройдено";
    },
    acceptUserPassing(record) {
      record.passingStatus = "passed";
      this.$store.dispatch("saveRaterPass", {
        pass: record,
        clientId: record.clientId,
      });
    },
    downloadCVS(record) {
      // const el = document.createElement("a");
      this.$store.dispatch('fetchEventXLS', record);
      // el.download = record.title;
      // el.href = `${appConfig.domain}/event/get/xls/${record.id}`;
      // el.click();
    },
    openUsersList(record) {
      this.currentEvent = record;

      this.usersColumns = [
        {
          title: "ФИО",
          dataIndex: ["client", "name"],
          key: "name",
        },

        {
          title: "Номер телефона",
          dataIndex: ["client", "phone"],
          key: "phone",
        },

        {
          title: "Статус",
          dataIndex: "passingStatus",
          key: "usersStatus",
        },

        {
          title: "Действия",
          key: "usersActions",
        },
      ];

      const stages = record.stages.sort((a, b) => a.order - b.order);
      for (let i = 0; i < stages.length; i++) {
        this.usersColumns.splice(this.usersColumns.length - 2, 0, {
          title:
            stages[i].type === "form"
              ? "Анкета"
              : stages[i].type === "test"
                ? "Тестирование"
                : stages[i].type === "exercise"
                  ? "Практическое задание"
                  : stages[i].type === "interview"
                    ? "Видео-интервью"
                    : "",
          key: "stage",
          type: stages[i].type,
          eventId: record.id,
          testId: stages[i].testId,
          interviewId: stages[i].interviewId,
          id: stages[i].id,
        });
      }
      this.$store.dispatch("fetchUsersByEventId", record).then((data) => {
        this.usersList = data;
      });
    },
    closeUsersList() {
      this.usersList = [];
      this.currentEvent = null;
    },
    //EVENT
    openEventEditor(event) {
      if (event) {
        this.newEvent = JSON.parse(JSON.stringify(event));
        this.eventDate = [
          this.dayjs(this.newEvent.startDate),
          this.dayjs(this.newEvent.endDate),
        ];
      } else {
        this.newEvent = {
          title: null,
          description: null,
          previewUrl: "null",
          createDate: new Date(),
          backgroundColor: "green",
          startDate: null,
          endDate: null,
          stages: [],
        };
      }

      if (this.newEvent.additionalData != null) {
        while (typeof this.newEvent.additionalData === "string") {
          try {
            this.newEvent.additionalData = JSON.parse(
              this.newEvent.additionalData
            );
          } catch (e) {
            break;
          }
        }
      } else {
        this.newEvent["additionalData"] = {
          place: "",
          target: "",
          haveTranslation: this.haveTranslation
        };
      }
      this.viewEventEditor = true;
    },
    closeEventEditor() {
      this.newEvent = null;
      this.eventDate = null;
      this.file = null;
      this.viewEventEditor = false;
    },
    async saveEvent() {
      const emptyFields = [];

      if (!this.newEvent?.title?.trim() || this.newEvent?.title === 'null') {
        emptyFields.push('название');
      }

      if (!this.newEvent?.description?.trim() || this.newEvent?.description === 'null') {
        emptyFields.push('описание');
      }

      if (!this.newEvent?.previewUrl || this.newEvent?.previewUrl === 'null') {
        emptyFields.push('изображение');
      }


      if (this.eventDate?.length !== 2) {
        emptyFields.push('даты начала и конца');
      }


      if (emptyFields.length) {
        errorHandler({ name: 'requiredField', msg: emptyFields.join(', ') });
        return;
      }


      this.newEvent.startDate = dayjs(this.eventDate[0]).startOf('day').toDate();
      this.newEvent.endDate = dayjs(this.eventDate[1]).endOf('day').toDate();
      if (this.newEvent?.additionalData) {
        this.newEvent.additionalData['haveTranslation'] = this.haveTranslation;
      }
      this.newEvent.additionalData = this.newEvent.additionalData
        ? JSON.stringify(this.newEvent.additionalData)
        : this.newEvent.additionalData;

      if (this.file) {
        await this.$store.dispatch("loadFile", this.file).then((url) => {
          this.newEvent.previewUrl = url;
          this.file = null;
        });
      }
      await this.$store
        .dispatch("saveEvent", { event: this.newEvent })
        .then(() => {
          this.closeEventEditor();
          this.fetchOwnEvents();
        });
    },
    //STAGE
    saveStage() {
      if (this.createTypeStage === "interview" && !this.newStage.interviewId) {
        errorHandler({ name: "eventEditor", status: 300 });
        return;
      }

      if (
        this.createTypeStage !== "interview" &&
        this.newStage.date.length !== 2 &&
        !this.newStage.testId
      ) {
        errorHandler({ name: "eventEditor", status: 300 });
        return;
      }

      if (this.createTypeStage === "interview") {
        let index = this.allInterviews
          .map((item) => item.id)
          .indexOf(this.newStage.interviewId);
        if (index !== -1) {
          this.newStage["startDate"] = dayjs(
            this.allInterviews[index].startDate
          )
            .startOf("day")
            .toDate();
          this.newStage["endDate"] = dayjs(this.allInterviews[index].endDate)
            .endOf("day")
            .toDate();
        } else {
          this.newStage["startDate"] = null;
          this.newStage["endDate"] = null;
        }
      } else {
        this.newStage["startDate"] = dayjs(this.newStage.date[0])
          .startOf("day")
          .toDate();
        this.newStage["endDate"] = dayjs(this.newStage.date[1])
          .endOf("day")
          .toDate();
      }
      delete this.newStage.date;

      let index = this.newEvent.stages
        .map((item) => {
          if (this.newStage?.type === "interview") {
            return item.interviewId;
          } else if (this.newStage?.type === "stream") {
            return item.translationId;
          }
          return item.testId;
        })
        .indexOf(
          this.newStage?.type === "interview"
            ? this.newStage.interviewId
            : this.newStage?.type === "stream"
              ? this.newStage.translationId
              : this.newStage.testId
        );

      // if (this.newStage?.type === 'stream' && this.newEvent?.additionalData) {
      //   this.newStage.additionalData['haveTranslation'] = true;
      // }

      if (index !== -1) {
        this.newEvent.stages[index] = this.newStage;
      } else {
        this.newEvent.stages.push(this.newStage);
      }
      this.closeStageEditor();
    },
    closeStageEditor() {
      this.newStage = null;
      this.createTypeStage = null;
      this.viewCreateStage = false;
      this.$store.commit("setAllTesting", null);
      this.$store.commit("setAllInterviews", null);
    },
    createNewStage() {
      let id = this.newStage && this.newStage.id ? this.newStage.id : null;
      this.newStage = {
        order:
          this.newEvent?.stages?.filter((item) => !item?.delete)?.length + 1,
        date: [],
        type: this.createTypeStage,
        testId: null,
        interviewId: null,
        translationId: null,
      };
      id ? (this.newStage["id"] = id) : null;
      if (
        this.createTypeStage !== "interview" &&
        this.createTypeStage !== "stream"
      ) {
        this.$store.dispatch("fetchAllTesting", { name: "" });
      } else if (this.createTypeStage === "stream") {
        this.$store.dispatch("fetchStreams");
      } else {
        this.$store.dispatch("fetchAllInterviews");
      }
    },
    editStage(stage) {
      this.newStage = stage;
      this.newStage["date"] = [
        dayjs(new Date(stage.startDate)).startOf("day"),
        dayjs(new Date(stage.endDate)).endOf("day"),
      ];
      this.createTypeStage = stage.type;
      if (
        this.createTypeStage !== "interview" &&
        this.createTypeStage !== "stream"
      ) {
        this.$store.dispatch("fetchAllTesting", { name: "" });
      } else if (this.createTypeStage === "stream") {
        this.$store.dispatch("fetchStreams");
      } else {
        this.$store.dispatch("fetchAllInterviews");
      }
      this.viewCreateStage = true;
    },
    deleteStage(order) {
      const stages = this.newEvent?.stages?.filter((item) => !item?.delete);

      const index = stages.map((item) => item.order).indexOf(order);
      if (index !== -1) {
        stages[index]["delete"] = true;
        for (let i = 0; i < stages.length; i++) {
          if (stages[i].order > stages[index].order) {
            stages[i].order--;
          }
        }
        // if (this.newEvent.additionalData)
        //   this.newEvent.additionalData['haveTranslation'] = stages?.filter((item) => !item?.delete)?.map(item => item.type === 'stream')?.indexOf(true) > -1;
      }
    },
    //OTHER
    uploadPreview() {
      const file =
        this.$refs.preview.files[this.$refs.preview.files.length - 1];
      const fileUrl = URL.createObjectURL(file);
      const img = new Image();
      img.onload = function () {
        window.dispatchEvent(
          new CustomEvent("CheckPreviewPhotoSize", {
            detail: this.height / this.width,
          })
        );
      };
      img.src = fileUrl;
    },
    fetchOwnEvents(confirm) {
      this.$store
        .dispatch("fetchOwnEvents", {
          name: this.name,
          minStartDate: this.minStartDate
            ? dayjs(this.minStartDate).toDate().toUTCString()
            : "",
          maxStartDate: this.maxStartDate
            ? dayjs(this.maxStartDate).toDate().toUTCString()
            : "",
          minEndDate: this.minEndDate
            ? dayjs(this.minEndDate).toDate().toUTCString()
            : "",
          maxEndDate: this.maxEndDate
            ? dayjs(this.maxEndDate).toDate().toUTCString()
            : "",
        })
        .then(() => {
          if (confirm) {
            confirm();
          }
        });
    },
    createNewEvent() {
      this.openEventEditor(null);
    },
    checkPreviewPhotoSize(event) {
      if (event.detail < 1 && event.detail >= 0.1) {
        this.file =
          this.$refs.preview.files[this.$refs.preview.files.length - 1];
        this.newEvent.previewUrl = URL.createObjectURL(this.file);
      } else {
        errorHandler({ name: "imgSize" });
      }
    },
    swapStage(from, isNext) {
      if (isNext) {
        this.newEvent.stages[from].order++;
        this.newEvent.stages[from + 1].order--;
      } else {
        this.newEvent.stages[from].order--;
        this.newEvent.stages[from - 1].order++;
      }
    },
    disabledDate(date) {
      if (this.eventDate?.length < 2) {
        return true;
      }

      return date < this.eventDate[0] || date > this.eventDate[1];
    },
  },
  beforeMount() {
    this.fetchOwnEvents();
    this.dayjs.locale("ru");
    window.addEventListener(
      "CheckPreviewPhotoSize",
      this.checkPreviewPhotoSize
    );
  },
  mounted() {
    document.addEventListener("createNewEvent", this.createNewEvent);
  },
  beforeUnmount() {
    document.removeEventListener("createNewEvent", this.createNewEvent);
    window.removeEventListener(
      "CheckPreviewPhotoSize",
      this.checkPreviewPhotoSize
    );
  },
  beforeRouteLeave() {
    document.removeEventListener("createNewEvent", this.createNewEvent);
    window.removeEventListener(
      "CheckPreviewPhotoSize",
      this.checkPreviewPhotoSize
    );
  },
};
</script>

