import {createRouter, createWebHistory} from 'vue-router';
import AuthorizationPage from "@/pages/authorization/AuthorizationPage";
import registrationPage from "@/pages/authorization/RegistrationPage";
import MainPage from "@/pages/MainPage";
import PersonalCabinetPage from "@/pages/presonal/PersonalCabinetPage";
import SettingsPage from "@/pages/presonal/SettingsPage";
import {canEnterToUserPortal} from "@/router/helpers";
import SeparateEventsPage from "@/pages/events/SeparateEventsPage";
import TestingPage from "@/pages/events/TestingPage";
import InterviewPage from "@/pages/events/InterviewPage";
import FormPage from "@/pages/events/FormPage";
import ExercisePage from "@/pages/events/ExercisePage";
import EventsPage from "@/pages/presonal/EventsPage";
import StreamsPage from "@/pages/presonal/StreamsPage";
import SeparateStreamPage from "@/pages/presonal/SeparateStreamPage";
import UserEventsPage from "@/pages/presonal/UserEventsPage";
import HistoryPage from "@/pages/presonal/HistoryPage";
import PostPage from "@/pages/presonal/PostPage";
import DashboardPage from "@/pages/presonal/DashboardPage";
import UsersListPage from "@/pages/admin/UsersListPage";
import OwnEventsPage from "@/pages/admin/OwnEventsPage";
import TestingListPage from "@/pages/admin/TestingListPage";
import TestingEditorPage from "@/pages/admin/TestingEditorPage";
import RaterEventsPage from "@/pages/rater/RaterEventsPage";
import MembersPage from "@/pages/admin/MembersPage";
import RatersPage from "@/pages/admin/RatersPage";
import RaterCurrentEventPage from "@/pages/rater/RaterCurrentEventPage";
import CreateMemberPage from "@/pages/admin/CreateMemberPage";
import InterviewEditorPage from "@/pages/admin/InterviewEditorPage";
import TestingEstimationPage from "@/pages/rater/TestingEstimationPage";
import StreamEditorPage from "@/pages/admin/StreamEditorPage.vue";
import HackathonsListPage from "@/pages/admin/HackathonsListPage.vue";
import HackathonEditorPage from "@/pages/admin/HackathonEditorPage.vue";
import HackathonPage from "@/pages/events/HackathonPage.vue";
import RestorePasswordaPage from "@/pages/authorization/RestorePasswordaPage.vue";
import RestorePasswordEmailPage from "@/pages/authorization/RestorePasswordEmailPage.vue";
import EnterUniqieCodePage from "@/pages/authorization/EnterUniqieCodePage.vue";


// creating routes to all pages
const routes = [
    {
        path: '/',
        name: 'MainPage',
        component: MainPage,
        beforeEnter: async (to, from, next) => {
            await canEnterToUserPortal().then(() => {
                next()
            });
        }
    },
    {
        path: '/authorization',
        name: 'authorization',
        component: AuthorizationPage,
        beforeEnter: async (to, from, next) => {
            await canEnterToUserPortal().then(token => {
                if (token) {
                    next({name: 'settings'})
                } else {
                    next();
                }
            })
        }
    },

    {
        path: '/password/restore/:uniqueCode',
        name: 'restorePassword',
        component: RestorePasswordaPage,
        props: true
    },
    {
        path: '/password/restore',
        name: 'restorePasswordEmail',
        component: RestorePasswordEmailPage,
        props: true
    },
    {
        path: '/password/verify/:uniqueCode',
        name: 'enterUniqueCode',
        component: EnterUniqieCodePage,
        props: true
    },


    {
        path: '/registration',
        name: 'registration',
        component: registrationPage,
        beforeEnter: async (to, from, next) => {
            await canEnterToUserPortal().then(token => {
                if (token) {
                    next({name: 'settings'})
                } else {
                    next();
                }
            })
        }
    },
    {
        path: '/cabinet',
        name: 'cabinet',
        component: PersonalCabinetPage,
        children: [


            {
                path: 'hackathons',
                name: 'hackathons',
                component: HackathonsListPage
            },

            {
                path: 'hackathon/table/:hackathonId',
                name: 'hackathonTable',
                component: HackathonPage,
                props: true

            },
            {
                path: 'hackathon/editor/:hackathonId',
                name: 'hackathonEditor',
                component: HackathonEditorPage,
                props: true
            },
            {
                path: 'events/my',
                name: 'myEvents',
                component: UserEventsPage
            },
            {
                path: 'settings',
                name: 'settings',
                component: SettingsPage,
            },
            {
                path: 'events/all',
                name: 'allEvents',
                component: EventsPage,
            },
            {
                path: 'streams',
                name: 'streams',
                component: StreamsPage,
            },
            {
                path: 'stream',
                name: 'stream',
                component: SeparateStreamPage,
            },
            {
                path: 'event/:eventId',
                name: 'separateEvent',
                props: true,
                component: SeparateEventsPage,
            },
            {
                path: 'stage/testing/:testingId/:eventId/:passingId/:role/:score',
                name: 'testing',
                props: true,
                component: TestingPage,
            },
            {
                path: 'stage/interview/:interviewId/:passingId/:eventId/:role',
                name: 'interview',
                props: true,
                component: InterviewPage,
            },
            {
                path: 'stage/form/:formId/:passingId/:eventId/:role',
                name: 'form',
                props: true,
                component: FormPage,
            },
            {
                path: 'stage/exercise/:exerciseId/:passingId/:eventId/:role',
                name: 'exercise',
                props: true,
                component: ExercisePage,
            },
            {
                path: 'history',
                name: 'history',
                component: HistoryPage
            },
            {
                path: 'post',
                name: 'post',
                component: PostPage
            },
            {
                path: 'dashboard',
                name: 'dashboard',
                component: DashboardPage
            },
            {
                path: 'users',
                name: 'users',
                component: UsersListPage
            },
            {
                path: 'events/own',
                name: 'ownEvents',
                component: OwnEventsPage
            },
            {
                path: 'testings',
                name: 'testingList',
                component: TestingListPage
            },
            {
                path: 'testing/editor/:mode/:testingId',
                name: 'testingEditor',
                props: true,
                component: TestingEditorPage
            },
            {
                path: 'interview/editor/:id',
                name: 'interviewEditor',
                props: true,
                component: InterviewEditorPage
            },
            {
                path: 'rater/events',
                name: 'raterEvents',
                props: true,
                component: RaterEventsPage
            },
            {
                path: 'rater/event/:stageId/:stageType/:testId',
                name: 'raterCurrentEvent',
                props: true,
                component: RaterCurrentEventPage
            },
            {
                path: 'members',
                name: 'members',
                component: MembersPage
            },
            {
                path: 'member/:id',
                name: 'currentMember',
                props: true,
                component: CreateMemberPage
            },
            {
                path: 'raters',
                name: 'raters',
                component: RatersPage
            },
            {
                path: 'testing/estimation/:testingId/:eventId/:passingId/:role/:score',
                name: 'testingEstimation',
                props: true,
                component: TestingEstimationPage
            },
            {
                path: 'stream/editor/:streamId',
                name: 'streamEditor',
                props: true,
                component: StreamEditorPage
            },


        ],
        beforeEnter: async (to, from, next) => {
            await canEnterToUserPortal().then(token => {
                if (token) {
                    next();
                } else {
                    localStorage.setItem('redirectRoute', JSON.stringify(to));
                    next({name: 'authorization'});
                }
            })
        }
    },
    {
        path: "/:pathMatch(.*)*",
        name: '404',
        beforeEnter: (to, from, next) => {
            next({name: 'authorization'});
        }
    },

];

// creating Vue-Router instance with normal history mode
const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;