import {defaultHttpResHandler} from "@/store/helpers/defaultHttpResHandler";
import {makeGetReq, makePostReq} from "@/store/helpers/request";
import {appConfig} from "@/lib/appConfig";

export const fetchUserEvents = async (token, name, status) => await defaultHttpResHandler(async () => {
    return await makeGetReq(appConfig.domain + '/event/get/user?name=' + (name ? name : '') + (status ? '&status=' + status : ''), {token})
});

export const fetchAllEvents = async (token, name, type) => await defaultHttpResHandler(async () => {
    return await makeGetReq(`${appConfig.domain}/event/get/all?name=${name}&type=${type}`, {token})

});


export const fetchCurrentEvents = async (token, id) => await defaultHttpResHandler(async () => {
        return await makeGetReq(appConfig.domain + '/event/get/byid/' + id, {token})
    }
);
export const fetchEventPassValid = async (token, data) => await defaultHttpResHandler(async () => {
        return await makePostReq(appConfig.domain + '/event/pass_valid', data, {token, data: true})
    }
);

export const fetchEventsInterview = async (token, id) => await defaultHttpResHandler(async () => {
    return {
        status: 200,
        data: {
            id: id,  // uuid
            eventId: 'eventId', // uuid
            userId: 'userId', // uuid
            interviewerId: 'test',  // uuid
            whenDate: new Date(new Date().setDate(new Date().getDate() - 2)),
            description: 'Это мероприятие позволит кандидатам пройти отбор на позицию создателя медиа контента, а также будет полезен для желающих найти работу',
            startDate: new Date(new Date().setDate(new Date().getDate() - 3)),
            endDate: new Date()
        }

    }

});

