<template>
  <div class="container">
    <a-modal :visible="viewModal" :ok-button-props="{class:'blue-buttons',disabled:!currentEvent||!currentStage}"
             title="Назначить оценщика"
             cancel-text="Отменить" ok-text="Назначить" @ok="saveRater"
             @cancel="closeModal">

      <div class="select-list">
        <div class="select-element">
          <a-typography-title :level="4">Мероприятия</a-typography-title>
          <a-select v-model:value="currentEvent" style="width: 60%" placeholder="Выберите мероприятие">
            <a-select-option v-for="item in ownEvents" :key="item.id" :value="item.id"

            >
              {{ item.title }}
            </a-select-option>
          </a-select>
        </div>

        <div class="select-element" v-if="currentEvent">
          <a-typography-title :level="4">Этапы</a-typography-title>
          <a-select v-model:value="currentStage" style="width: 60%" placeholder="Выберите этап">
            <a-select-option v-for="item in ownEvents[ownEvents.map(event=>event.id).indexOf(currentEvent)].stages"
                             :key="item.id" :value="item.id"
                             :disabled="checkDisabled(item)"

            >
              {{
                `${item.order}) ${item.type === 'test' ? 'Тестирование' : item.type === 'interview' ? 'Видео-интервью' : item.type === 'exercise' ? 'Практическое задание' : item.type === 'form' ? 'Анкета' : null}`
              }}
            </a-select-option>
          </a-select>
        </div>
      </div>
    </a-modal>

    <!--    <div class="buttons-container" v-if="name||sex||city">-->

    <!--    </div>-->


    <div class="filling">
      <div class="table-caption">
        <a-typography-title
            :level="2"
            class="table-caption__title"
        >
          Список пользователей
        </a-typography-title>
        <div class="" style="display: flex; gap: 5px">

          <a-popover>
            <template #content>
              <div class="" style="display: flex; gap: 5px">
                <a-button shape="round" type="primary" @click="downloadTxtUsersInfo">TXT</a-button>
                <a-button shape="round" type="primary" @click="$refs.download.click()">EXCEL
                  <a :href="appConfig.domain+ '/client/all/xls'" hidden ref="download"
                     :download="appConfig.domain+ '/client/all/xls'"></a></a-button>

                <json-c-s-v :data="cvsData" id="json2cvs">
                  <a-button shape="round" type="primary">CSV
                  </a-button>
                </json-c-s-v>


              </div>
            </template>
            <a-button shape="round" type="primary" ghost>
              Выгрузить данные
            </a-button>
          </a-popover>


          <a-button :disabled="!name&&!sex&&!city&&!externalId&&!phone&&!role" shape="round"
                    @click="dropAllFilters">Сбросить все фильтры
          </a-button>
        </div>

      </div>
      <a-table class="table" :dataSource="usersList" :columns="columns">
        <template #bodyCell="{column,record}">
          <template v-if="column.key === 'avatarUrl'">
            <a-avatar :src="record.avatarUrl?record.avatarUrl:require('@/assets/avatar.png') " size="large"></a-avatar>
          </template>
          <template v-if="column.key === 'role'">
            <div class="role-element">
              {{
                record.role === 'user' ? 'Пользователь' : record.role === 'rater' ? 'Оценщик' : record.role === 'admin' ? 'Администратор' : null
              }}
              <a-button shape="round" class="blue-buttons-ghost" type="primary" ghost @click="openModal(record)">
                Назначить
              </a-button>
            </div>
          </template>
          <template v-if="column.key === 'sex'">
            {{ record.sex ? (record.sex === 'man' ? 'Мужской' : 'Женский') : 'Не определенно' }}
          </template>
          <template v-if="column.key === 'registrationDate'">
            {{
              record.registrationDate ? dayjs(record.registrationDate).format('DD-MM-YYYY') : ''
            }}
          </template>
          <template v-if="column.key === 'city'">
            {{ record.city ? record.city : 'Не определенно' }}
          </template>

          <template v-if="column.key==='actions'">
            <a-button danger @click="deleteUser(record.id)">
              <template #icon>
                <delete-outlined></delete-outlined>
              </template>
            </a-button>
          </template>
        </template>
        <template #customFilterDropdown="{column,confirm}">
          <div style="padding: 8px" v-if="column.dataIndex==='name'">
            <a-input
                placeholder="Найти по имени"
                v-model:value="name"
                style="width: 188px; margin-bottom: 8px; display: block"
                @pressEnter="fetchUserList(confirm)"
            />
            <a-button shape="round"
                      type="primary"
                      size="small"
                      style="width: 90px; margin-right: 8px"
                      @click="fetchUserList(confirm,column.dataIndex)"
            >
              Поиск
            </a-button>
            <a-button shape="round" size="small" style="width: 90px" @click="dropValue('name',confirm)">
              Сбросить
            </a-button>
          </div>
          <div style="padding: 8px;display: flex;flex-direction: column;gap: 9px; align-items: center" v-else>
            <a-select style="width: 150px" v-if="column.dataIndex==='sex'"
                      v-model:value="sex"
                      placeholder="Фильтр по полу">
              <a-select-option value="man">
                Мужской
              </a-select-option>
              <a-select-option value="girl">
                Женский
              </a-select-option>

            </a-select>

            <a-select v-if="column.dataIndex==='city'"
                      show-search
                      style="width:100%"
                      v-model:value="city"
                      placeholder="Фильтр по городу">
              <a-select-option v-for="item in cities" :key="item.name+item.subject" :value="item.name">
                {{ item.name }}
              </a-select-option>

            </a-select>

            <div class="filter-container" v-if="column.key==='registrationDate'">

              <div class="filter-el">
                Сортировка:
                <a-select style="width:100%"
                          v-model:value="sortRegDate"
                          placeholder="Сортировать по">

                  <a-select-option value="desc">
                    Сначала новые
                  </a-select-option>
                  <a-select-option value="asc">
                    Сначала старые
                  </a-select-option>


                </a-select>
              </div>

              <div class="filter-el">
                От:
                <a-date-picker :locale="locale" v-model:value="minBirthDate"></a-date-picker>
              </div>
              <div class="filter-el">
                До:
                <a-date-picker :locale="locale" v-model:value="maxBirthDate"></a-date-picker>
              </div>

            </div>

            <div class="" v-if="column.key==='externalId'">
              <a-input
                  placeholder="Найти по id"
                  v-model:value="externalId"
                  style="width: 188px; margin-bottom: 8px; display: block"
                  @pressEnter="fetchUserList(confirm)"
              />
            </div>
            <div class="" v-if="column.key==='phone'">
              <a-input
                  placeholder="Найти по телефону"
                  v-model:value="phone"
                  style="width: 188px; margin-bottom: 8px; display: block"
                  @pressEnter="fetchUserList(confirm)"
              />

            </div>
            <div class="" v-if="column.key==='role'">
              <a-select
                  show-search
                  style="width: 180px"
                  v-model:value="role"
                  placeholder="Фильтр по роли">
                <a-select-option value="">
                  Все
                </a-select-option>
                <a-select-option value="user">
                  Пользователи
                </a-select-option>
                <a-select-option value="rater">
                  Оценщики
                </a-select-option>
                <a-select-option value="admin">
                  Администраторы
                </a-select-option>

              </a-select>
            </div>

            <div class="filter-buttons">
              <a-button shape="round"
                        type="primary"
                        size="small"
                        style="width: 90px; margin-right: 8px"
                        @click="fetchUserList(confirm)"
              >
                Поиск
              </a-button>
              <a-button shape="round" size="small" style="width: 90px" @click="dropValue(column.dataIndex,confirm)">
                Сбросить
              </a-button>
            </div>

          </div>

        </template>
        <template #customFilterIcon="{column}">
          <search-outlined v-if="column.dataIndex==='name'" :style="{ color: name ? '#108ee9' : undefined }"/>
          <filter-outlined v-if="column.dataIndex==='sex'" :style="{ color: sex ? '#108ee9' : undefined }"/>
          <filter-outlined v-if="column.dataIndex==='city'" :style="{ color: city ? '#108ee9' : undefined }"/>
          <filter-outlined v-if="column.dataIndex==='externalId'"
                           :style="{ color: externalId ? '#108ee9' : undefined }"/>
          <filter-outlined v-if="column.dataIndex==='phone'" :style="{ color: phone ? '#108ee9' : undefined }"/>
          <filter-outlined v-if="column.dataIndex==='role'" :style="{ color: role ? '#108ee9' : undefined }"/>

          <filter-outlined v-if="column.dataIndex==='registrationDate'"
                           :style="{ color: minBirthDate||maxBirthDate ? '#108ee9' : undefined }"/>

        </template>
      </a-table>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import {SearchOutlined, FilterOutlined, DeleteOutlined} from "@ant-design/icons-vue";
import cities from '@/lib/cities.JSON'
import {appConfig} from "@/lib/appConfig";
import locale from "ant-design-vue/es/date-picker/locale/ru_RU"
import dayjs from "dayjs";
import 'dayjs/locale/ru';
import JsonCSV from 'vue-json-csv'


export default {
  name: "UsersListPage",
  components: {
    SearchOutlined,
    FilterOutlined,
    JsonCSV,
    DeleteOutlined
  },
  computed: {
    ...mapGetters(['usersList', 'ownEvents']),
    cvsData() {
      return this.usersList?.map(item => {
        return {
          'ID': item.id,
          'Идентификатор': item.externalId,
          'ФИО': item.name,
          'Город': item.city,
          'Пол': item.sex === 'man' ? 'Мужской' : 'Женский',
          'День рождения': item.birth ? dayjs(item.birth).format('DD-MM-YYYY') : '',
          'Роль': item.role === 'admin' ? 'Администратор' : item.role === 'rater' ? 'Оценщик' : 'Пользователь',
          'Рейтинг доверия': item.confidenceRating
        }
      });
    }
  },
  data() {
    return {
      appConfig,
      columns: [
        {
          title: 'Аватар',
          dataIndex: 'avatarUrl',
          key: 'avatarUrl',
          width: 100
        },
        {
          title: 'Идентификатор',
          dataIndex: 'externalId',
          key: 'externalId',
          customFilterDropdown: true

        },
        {
          title: 'Имя',
          dataIndex: 'name',
          key: 'name',
          customFilterDropdown: true
        },
        {
          title: 'Пол',
          dataIndex: 'sex',
          key: 'sex',
          customFilterDropdown: true,
        },
        {
          title: 'Город',
          dataIndex: 'city',
          key: 'city',
          customFilterDropdown: true,
        },
        {
          title: 'Дата регистрации',
          dataIndex: 'registrationDate',
          key: 'registrationDate',
          customFilterDropdown: true,
        },
        {
          title: 'Номер телефона',
          dataIndex: 'phone',
          key: 'phone',
          customFilterDropdown: true

        },
        {
          title: 'Роль',
          dataIndex: 'role',
          key: 'role',
          customFilterDropdown: true
        },
        {
          title: 'Действия',
          key: 'actions',
        }


      ],
      viewModal: false,


      currentUser: null,
      currentEvent: null,
      currentStage: null,

      name: null,
      city: null,
      sex: null,
      minBirthDate: null,
      maxBirthDate: null,
      sortRegDate: 'desc',


      externalId: null,
      phone: null,
      role: '',

      cities,
      locale,
      dayjs,

    }
  },

  watch: {
    name: function (val) {
      if (!val) {
        this.fetchUserList();
        return;
      }
      if (val?.length >= 3) {
        this.fetchUserList();
      }

    }
  },
  methods: {
    deleteUser(userId) {
      this.$store.dispatch('deleteUser', userId);
    },

    downloadTxtUsersInfo() {
      const link = document.createElement("a");
      const file = new Blob(
          this.usersList.map(item => {
            return `${item.id}\t${item.externalId}\t${item.name}\t${item.phone}\t${item.avatarUrl}\t${item.city}\t${item.sex === 'man' ? 'Мужской' : 'Женский'}\t${item.birth ? dayjs(item.birth).format('DD-MM-YYYY') : ''}${item.role === 'admin' ? 'Администратор' : item.role === 'rater' ? 'Оценщик' : 'Пользователь'}\t${item.confidenceRating}\n`
          })

          , {type: 'text/plain'});
      link.href = URL.createObjectURL(file);
      link.download = "users.txt";
      link.click();
      URL.revokeObjectURL(link.href);
    },
    openModal(user) {
      this.$store.dispatch('fetchOwnEvents', {name: ''}).then(() => {
        this.currentUser = user;
        this.viewModal = true;
      });

    },
    closeModal() {
      this.viewModal = false;
      this.currentUser = null;
      this.currentEvent = null;
      this.currentStage = null;
    },
    saveRater() {
      this.$store.dispatch('saveRater', {clientId: this.currentUser.id, stageId: this.currentStage}).then((status) => {
        if (status === 200) {
          this.closeModal();
          this.fetchUserList();
        }
      })
    },
    checkDisabled(item) {
      for (let i = 0; i < this.currentUser.stagesForRate.length; i++) {
        if (item.id === this.currentUser.stagesForRate[i].stageId) {
          return true
        }
      }
      return false;
    },
    fetchUserList(confirm) {
      if (confirm) {
        confirm();
      }
      this.$store.dispatch('fetchUsersList', {
        name: this.name,
        city: this.city,
        sex: this.sex,
        externalId: this.externalId,
        phone: this.phone,
        role: this.role,
        maxBirthDate: this.maxBirthDate,
        minBirthDate: this.minBirthDate,
        sortRegDate: this.sortRegDate
      });
    },
    dropValue(field, confirm) {
      this[field] = null;
      if (field === 'registrationDate') {
        this.maxBirthDate = null;
        this.minBirthDate = null;
      }
      this.fetchUserList(confirm);
    },
    dropAllFilters() {
      this.name = null;
      this.sex = null;
      this.city = null;
      this.externalId = null;
      this.phone = null;
      this.role = '';
      this.fetchUserList()
    }
  },
  beforeMount() {
    this.dayjs.locale('ru');
    this.fetchUserList();
  }
}
</script>

