<template>
  <div class="container" v-if="currentStage">

    <div class="testing-caption block">
      <div class="testing-caption-block" v-if="false">
        <p class="testing-caption-block__element"><span>Имя: </span>Мария Игоревна Лошицкая</p>
        <p class="testing-caption-block__element"><span>Город: </span> Москва</p>
        <p class="testing-caption-block__element"><span>Телефон: </span> +79858238422</p>
        <p class="testing-caption-block__element"><span>Дата рождения:</span> 03.01.1993</p>
      </div>
      <div class="testing-caption-block">
        <p class="testing-caption-block__element"><span>Время прохождения тестирования:</span>
          {{ currentStage.limitSeconds / 60 }} минут</p>
        <p class="testing-caption-block__element"><span>Вопросов:</span> {{ currentStage.questions.length }}</p>
        <p class="testing-caption-block__element"><span>Общая оценка:</span> {{
            score !== 'null' ? score : 'Ожидает оценки'
          }}</p>
      </div>
    </div>

    <div class="tests block">
      <SeparateTestEstimationCheckbox v-for="(item,index) in currentStage.questions" :index="index" :question="item"
                                      :passing-test="passingTest" :key="item.id" :score="score"
                                      :testing-score="testingScore">
      </SeparateTestEstimationCheckbox>

      <div class="tests-end">
        <a-button shape="round" v-if="false">Очистить</a-button>
        <a-button shape="round" :disabled="disabledButton" type="primary" @click="saveEstimation">{{
            score === 'null' ? 'Оценить' : 'Назад'
          }}
        </a-button>
      </div>
    </div>
  </div>
</template>

<script>
import SeparateTestEstimationCheckbox from "@/components/testing/SeparateTestEstimationCheckbox";
import Cookies from "js-cookie";
import {mapGetters} from "vuex";

export default {
  name: "TestingEstimationPage",
  components: {SeparateTestEstimationCheckbox},
  data() {
    return {
      dateOptions: {
        month: 'long', day: 'numeric'
      },
      disabledButton: false
    }
  },
  props: ['testingId', 'eventId', 'role', 'passingId', 'score'],
  computed: {
    ...mapGetters(['currentStage', 'passingTest', 'testingScore'])
  },
  beforeMount() {
    let passingTestingId = Cookies.get('testingPassingId');
    if (this.role === 'rater') {
      if (passingTestingId && passingTestingId !== 'null') {
        this.$store.commit('setTestingPassingId', passingTestingId);
      } else {
        this.$router.go(-1);
      }
    }
    this.$store.dispatch('fetchEventsTesting', {
      testingId: this.testingId,
      eventId: this.eventId,
      role: this.role,
      passingId: this.passingId,
    }).then(() => {
      document.dispatchEvent(new CustomEvent('setTitle', {detail: this.currentStage.title}));
    });
  },
  methods: {
    saveEstimation() {
      if (this.score !== 'null') {
        this.$router.go(-1);
        return;
      }
      this.disabledButton = true;
      this.$store.dispatch('savePassingTesting', {role: this.role, finish: false, eventId: this.eventId});
    }
  },
  beforeRouteLeave() {
    this.$store.commit('clearPassing');
    Cookies.remove('testingPassingId');
  }

}
</script>

