import {loadFile} from "@/store/application/serverInterationMethods";


export const application = {
    state: {
        iterationInfo: null,
        loader: false
    },
    mutations: {
        setIterationInfo(store, data) {
            store.iterationInfo = data;
        },
        setLoader(state, data) {
            state.loader = data;
        }
    },
    actions: {
        async loadFile(_, file) {
            const formData = await new FormData();
            await formData.append('file', file);
            const {data} = await loadFile(formData);
            return data.url;
        }
    },
    getters: {
        iterationInfo: (state) => state.iterationInfo,
        loader: state => state.loader
    }
};