<template>
  <a-layout style="background: #fff">
    <HeaderStarted></HeaderStarted>
    <a-layout-content style="background: #fff">
      <div class="container">
        <div class="filling">
          <h1 class="title">Авторизация</h1>

          <div class="info-block">

            <h3 class="subtitle">
              <a-radio-group v-model:value="loginType">
                <a-radio-button value="phone">Номер</a-radio-button>
                <a-radio-button value="email">Email</a-radio-button>

              </a-radio-group>
            </h3>

            <a-input
                v-if="loginType==='phone'"
                :value="login"
                class="input"
                @input="phoneMask"
                @keydown.enter="authorization"
                placeholder="Телефон"
            ></a-input>
            <a-input
                v-else
                v-model:value="login"
                class="input"
                @keydown.enter="authorization"
                placeholder="Email"
            ></a-input>
            <h3 class="subtitle">Пароль:</h3>
            <a-input-password
                class="input"
                @keydown.enter="authorization"
                v-model:value="password"
                placeholder="Пароль"
            ></a-input-password>
          </div>
          <a-button
              shape="round"
              class="submit-btn blue-buttons purple-btn"
              @click="authorization"
              type="primary"
          >Войти
          </a-button>
          <div class="link-wrapper">
            <p class="link-text">Нет аккаунта?</p>
            <router-link class="link" :to="{ name: 'registration' }">
              Зарегистрироваться
            </router-link>
          </div>
          <div class="link-wrapper">
            <p class="link-text">Забыли пароль?</p>
            <router-link class="link" :to="{ name: 'restorePasswordEmail' }">
              Восстановить
            </router-link>
          </div>
        </div>
        <div class="decorations desktop">
          <img
              src="../../assets/icons/decoration-1.svg"
              alt=""
              class="decoration-1"
          />
          <img
              src="../../assets/icons/decoration-3.svg"
              alt=""
              class="decoration-3"
          />
        </div>
        <div class="decorations mobile">
          <img
              src="../../assets/icons/decoration-1-mob.svg"
              alt=""
              class="decoration-1"
          />
          <img
              src="../../assets/icons/decoration-2-mob.svg"
              alt=""
              class="decoration-2"
          />
          <img
              src="../../assets/icons/decoration-3-mob.svg"
              alt=""
              class="decoration-3"
          />
        </div>
      </div>
    </a-layout-content>
  </a-layout>
</template>

<script>
import IMask from "imask";
import HeaderStarted from "@/components/personal/HeaderStarted";

export default {
  name: "AuthorizationPage",
  components: {HeaderStarted},
  data() {
    return {
      login: null,
      password: null,
      mask: "+{7}(000)000-00-00",
      unmaskedLogin: null,
      loginType: 'phone'
    };
  },
  watch: {
    loginType() {
      this.login = null;
      this.unmaskedLogin = null;
    }
  },

  methods: {
    authorization() {

      if (this.login && this.password) {
        if (this.loginType === 'email' && !/^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(this.login)) return;
        this.$store.dispatch("login", {
          login: this.loginType === 'phone' ? this.unmaskedLogin : this.login,
          password: this.password,
        });
      }
    },
    phoneMask(e) {
      let phone = IMask(e.target, {
        mask: this.mask,
      });
      phone.updateValue();
      this.login = phone.value;
      this.unmaskedLogin = phone.unmaskedValue;
    },
  },
};
</script>

