<template>


  <div class="card-container">
    <div class="card-header">
      <a-typography-title :level="5" style="margin: 8px;" type="secondary">
        {{
          item.funcType === '_min' ? 'Минимум' : item.funcType === '_avg' ? 'Среднее' : item.funcType ===
          '_max' ? 'Максимум' : item.funcType === 'distr' ? 'Распределение' : 'Выберете параметры'
        }}
      </a-typography-title>
      <a-popover placement="bottom" v-model:visible="visible" trigger="click">
        <template #content>
          <div class="param-el">
            <a-select placeholder="Выберете анкету" :value="item.horizontalParam"
                      @change="changeFrom">
              <a-select-option v-for="stage in stages.filter(stage1=>stage1.type==='form')" :value="stage.testId"
                               :key="stage.testId">
                {{ `Анкета ${stage.order}` }}
              </a-select-option>
            </a-select>
          </div>
          <div class="param-el">
            <a-select v-if="form&&form.questions" placeholder="Выберете вопрос" :disabled="!form"
                      :value="item.questionId"
                      @change="val=>$store.commit('changeSoloParamStatistic', {val, index: item.index, type: 'questionId'})">
              <a-select-option

                  v-for="question in form.questions.filter(questionItem=>questionItem.type!=='file')"
                  :value="question.id"
                  :key="question.id">
                {{ question.body }}
              </a-select-option>
            </a-select>
          </div>
          <div class="param-el">
            <a-select
                @change="val=>$store.commit('changeSoloParamStatistic',{val,index:item.index,type:'funcType'})"
                style="width: 300px"
                placeholder="Параметр"
                :value="item.funcType">
              <a-select-option value="_min">
                Минимальное значение
              </a-select-option>
              <a-select-option value="_max">
                Максимальное значение
              </a-select-option>
              <a-select-option value="_avg">
                Среднее значение
              </a-select-option>
              <a-select-option value="distr">
                Распределение
              </a-select-option>
            </a-select>
          </div>
          <div class="param-el">
            <a-select
                v-if="false"
                @change="val=>$store.commit('changeSoloParamStatistic',{val,index:item.index,type:'type'})"
                style="width: 300px"
                placeholder="Тип диаграммы"
                :value="item.type">
              <a-select-option value="pie">
                Диаграмма
              </a-select-option>
              <a-select-option value="bar">
                Гистограмма
              </a-select-option>
            </a-select>
          </div>
        </template>
        <button style="width: 30px;text-align: center;cursor: pointer" @click="visible=!visible">
          <down-outlined style="color: rgba(0, 0, 0, 0.45);font-size: 15px" v-if="!visible"/>
          <up-outlined style="color: rgba(0, 0, 0, 0.45);font-size: 15px" v-if="visible"/>
        </button>
      </a-popover>

    </div>
    <a-divider style="margin: 8px;height: 1px"></a-divider>
    <div class="card-content">
      <div class="charts-block"
           v-if="item.statisticType==='soloParam'&&item.horizontalParam&&item.funcType&&item.chartData!=null">
        <Line :data="item.chartData"
              v-if="item.type==='line'&& item.chartData.labels.length&&(item.statisticType==='ratio'||!item.statisticType)"
              :options="chartOptions"/>
        <Bar :data="item.chartData"
             v-else-if="item.type==='bar'&& item.chartData.labels.length&&((item.statisticType==='ratio'||!item.statisticType)||(item.statisticType==='soloParam'&&item.funcType==='distr'))"
             :options="chartOptions"/>
        <Doughnut :data="item.chartData"
             v-else-if=" item.type==='pie'&& item.chartData.labels.length&&((item.statisticType==='ratio'||!item.statisticType)||(item.statisticType==='soloParam'&&item.funcType==='distr'))"
             :options="chartOptions"/>
        <a-empty style="color: black"
                 v-if="item.statisticType==='soloParam'&&item.funcType==='distr'&&!item.chartData.labels.length"
                 description="Не хватает данных для статистики"/>

        <div class="solo-param-data"
             v-if="item.statisticType==='soloParam'&&item.funcType!=='distr'">

          <p class="solo-param-answer">
            {{
              `${item.funcType === '_min' ? 'МИНИМАЛЬНОЕ значение в вопросе\n' : item.funcType === '_avg' ? 'СРЕДНЕЕ значение в вопросе' : item.funcType ===
              '_max' ? 'МАКСИМАЛЬНОЕ значение в вопросе\n' : ''}:`
            }} <br> {{
              `${item.funcType !== 'distr' && questionsInSoloParamCharts.map(question =>
                  question.id).indexOf(item.questionId) !== -1 ?
                  questionsInSoloParamCharts[questionsInSoloParamCharts.map(question =>
                      question.id).indexOf(item.questionId)].body : ''} `
            }}
          </p>

          <a-divider>

          </a-divider>
          <p class="solo-param-answer">{{
              `«${item.funcType !== 'distr' && questionsInSoloParamCharts.map(question =>
                  question.id).indexOf(item.questionId) !== -1 && questionsInSoloParamCharts[questionsInSoloParamCharts.map(question =>
                  question.id).indexOf(item.questionId)].answers.map(answer => answer.id).indexOf(item.chartData.datasets[0]) !== -1 ?
                  questionsInSoloParamCharts[questionsInSoloParamCharts.map(question =>
                      question.id).indexOf(item.questionId)].answers[questionsInSoloParamCharts[questionsInSoloParamCharts.map(question =>
                      question.id).indexOf(item.questionId)].answers.map(answer => answer.id).indexOf(item.chartData.datasets[0])].body : ''}»`
            }}</p>
        </div>
      </div>
      <a-empty class="charts-block" style="color: black" v-else
               description="Выберете тип, параметры по горизонтали и вертикали"/>
    </div>
  </div>

</template>

<script>
import {DownOutlined, UpOutlined} from "@ant-design/icons-vue";
import {Line, Bar,Doughnut} from 'vue-chartjs';
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  ArcElement
} from 'chart.js'

ChartJS.register(Title, Tooltip, Legend, PointElement,
    BarElement, LineElement, CategoryScale, LinearScale, ArcElement)


export default {
  components: {
    Line, Bar, Doughnut,
    DownOutlined, UpOutlined
  },
  props: ['stages', 'item'],
  name: "ChartSoloParam",
  computed: {
    questionsInSoloParamCharts() {
      return this.$store.getters.questionsInSoloParamCharts
    }
  },
  data() {
    return {
      form: null,
      visible: false,
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false
      },
    }
  },
  methods: {
    changeFrom(val) {
      this.$store.commit('changeSoloParamStatistic', {val, index: this.item.index, type: 'horizontalParam'});
      this.loadQuestions();
    },
    loadQuestions() {
      this.$store.dispatch('fetchFromQuestions', this.item.horizontalParam).then(data => {
        this.form = data;
        this.$store.commit('addSoloParamChartsQuestions', data.questions);
      });
    },
  },
  beforeMount() {
    if (this.item.horizontalParam) {
      this.loadQuestions();
    }
  }
}
</script>

<style scoped>

.card-container {
  display: flex;
  flex-direction: column;
  padding: 35px;

}

.card-header {
  display: flex;
  justify-content: space-between;
}


.charts-block {
  height: 260px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.param-el {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin-bottom: 5px;
}

.solo-param-data {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}

.solo-param-answer {
  text-align: center;
}
</style>