<template>
  <div class="answer">
    <div class="answer-caption">
      <p class="answer__title">Написать письмо</p>
      <button class="answer__close" @click="$emit('closePostMessage')">
        <close-outlined style="font-size: 18px;"/>
      </button>
    </div>

    <!--    <input type="text" class="answer__input" v-model="post.email" placeholder="Кому">-->
    <a-select showSearch @search="searchUsers" @change="pickUser" placeholder="Кому" v-if="usersList"
              class="answer__input"
              v-model:value="clientName">
      <a-select-option v-for="item in usersList" :key="item.id" :value="item.name">{{ item.name }}</a-select-option>
    </a-select>
    <input type="text" class="answer__input" v-model="post.title" placeholder="Тема письма">
    <textarea class="answer__text" v-model="post.body" placeholder="Напишите текст"></textarea>
    <a-button class="answer__send" type="primary" size="large" shape="round" @click="sendMessage">Отправить</a-button>
  </div>
</template>

<script>
import {
  CloseOutlined
} from "@ant-design/icons-vue";


export default {
  name: "AnswerElement",
  components: {CloseOutlined},
  computed: {
    usersList() {
      return this.$store.getters.usersList;
    }
  },
  data() {
    return {
      post: {
        title: '',
        clientId: null,
        body: '',
      },
      clientName: null,
      name: false
    }
  },
  methods: {
    sendMessage() {
      this.$store.dispatch('sendPostMessage', this.post).then((status) => {
        if (status === 200) {
          this.$store.dispatch('fetchPostMessages', {page: 1, limit: 20});
          this.$emit('closePostMessage');
        }
      })
    },
    fetchUserList() {
      this.$store.dispatch('fetchUsersList', {name: this.name, city: this.city, sex: this.sex});
    },
    pickUser() {
      let index = this.usersList.map(item => item.name).indexOf(this.clientName);
      if (index !== -1) {
        this.post.clientId = this.usersList[index].id;
      }
    },
    searchUsers(val) {
      this.name = val;
      this.fetchUserList()
    }
  },
  beforeMount() {
    this.fetchUserList();
  }
}
</script>

