<template>
  <div class="container" v-if="!currentSlot">
    <div class="title-container">
      <a-typography-title :level="5">Даты интервью:</a-typography-title>
      <a-range-picker :locale="locale" v-model:value="interviewDates"/>
    </div>


    <div class="current-slot-container">
      <a-typography-title :level="5">Слоты:</a-typography-title>
      <div class="" style="display: flex; justify-content: flex-end">
        <a-button v-if="interviewDates.length===2" @click="$store.commit('addInterviewSlot')">Добавить</a-button>
      </div>
      <a-table v-if="interview" :data-source="interview.slots" :columns="columns">
        <template #bodyCell="{record,column,index}">
          <div class="" v-if="column.key==='day'">
            <a-date-picker :locale="locale" :disabled-date="disabledDate"
                           @change="$store.commit('setSlotsDay',{index,val:$event})"
                           :value="record.day">
            </a-date-picker>
          </div>
          <div class="" v-if="column.key==='slots'">
            <a-button @click="currentSlot={index,data:record}">Посмотреть</a-button>
          </div>
        </template>
      </a-table>
    </div>


  </div>
  <div class="container" v-if="currentSlot">
    <div class="current-slot-container">
      <a-typography-title :level="5">{{
          new Date(currentSlot.data.day).toLocaleDateString('ru-RU', dateOptions)
        }}
      </a-typography-title>
      <div class="" style="display: flex; justify-content: space-between">
        <a-button @click="currentSlot=null">Назад</a-button>
        <a-button @click="$store.commit('addInterviewTimes',currentSlot.index)">Добавить</a-button>
      </div>
      <a-table :data-source="currentSlot.data.times" :columns="slotsColumns">
        <template #bodyCell="{column,record,index}">
          <div class="" v-if="column.key==='startTime'||column.key==='endTime'">
            <a-time-picker :locale="locale"
                           format="HH:mm"
                           @change="$store.commit('setTimesParams',{dayIndex:currentSlot.index,timeIndex:index,val:$event,key:column.key})"
                           :value="record[column.key]">
            </a-time-picker>
          </div>
          <div class="" v-if="column.key==='slotsCount'">
            <a-input-number
                @change="$store.commit('setTimesParams',{dayIndex:currentSlot.index,timeIndex:index,val:$event,key:column.key})"
                :min="1" :value="record.slotsCount"></a-input-number>
          </div>
        </template>
      </a-table>
    </div>
  </div>

</template>

<script>
import locale from "ant-design-vue/es/date-picker/locale/ru_RU"
import dayjs from "dayjs";
import 'dayjs/locale/ru';

export default {
  name: "InterviewEditor",
  computed: {
    interview() {
      return this.$store.getters.interview;
    }
  },
  watch: {
    interviewDates: function (val) {
      if (val.length === 2) {
        this.$store.commit('setInterviewDates', val);
      }
    },
    interview(val) {
      if (val && this.firstLoad) {
        this.interviewDates = this.id === 'new' ? [] : [val.startDate ? dayjs(val.startDate) : null, val.endDate ? dayjs(val.endDate) : null];
      }
    }
  },
  props: ['id'],
  data() {
    return {
      locale,
      dayjs,
      columns: [
        {
          title: 'Дата',
          dataIndex: 'day',
          key: 'day'
        },
        {
          title: 'Cлоты',
          key: 'slots'
        },
      ],
      slotsColumns: [
        {
          title: 'Время начала',
          dataIndex: 'startTime',
          key: 'startTime'
        },
        {
          title: 'Время конца',
          dataIndex: 'endTime',
          key: 'endTime'
        },
        {
          title: 'Количество мест',
          dataIndex: 'slotsCount',
          key: 'slotsCount'
        },
      ],
      dateOptions: {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
      },


      interviewDates: [],
      currentSlot: null,
      firstLoad: true
    }
  },
  methods: {
    disabledDate(val) {
      return !(val && val >= this.interviewDates[0].day(this.interviewDates[0].get('day') - 1) && val <= this.interviewDates[1])
    },
    saveInterview() {
      this.$store.dispatch('saveInterview', {id: this.id, role: 'admin'});
    }
  },
  beforeMount() {
    this.dayjs.locale('ru');
    this.$store.dispatch('fetchInterviewById', this.id);
  },
  mounted() {
    document.addEventListener('saveInterview', this.saveInterview);
  },
  beforeUnmount() {
    document.removeEventListener('saveInterview', this.saveInterview);
  },
  beforeRouteLeave() {
    document.removeEventListener('saveInterview', this.saveInterview);
  }
}
</script>

<style scoped>
.current-slot-container {
  margin-top: 25px;
  display: flex;
  flex-direction: column;
  gap: 7px;
}


.container {
  padding: 25px;
}
</style>