<script>
import { mapGetters } from "vuex";
import { textSymbolLimit } from "@/lib/textSymbolLimit";
import EventCard from "@/components/events/EventCard";
import StatisticWindow from "@/components/events/StatisticWindow";
import Cookies from "js-cookie";

export default {
  components: { EventCard, StatisticWindow },
  computed: {
    ...mapGetters(["allEvents", "currentEvents", "role"]),
  },
  data() {
    return {
      textSymbolLimit,
      viewStatistic: false,
      name: "",
      startDate: "",
      endDate: "",
      status: "all",
    };
  },

  watch: {
    status: function () {
      this.fetchAllEvents();
    },
  },
  name: "EventsPage",
  methods: {
    fetchAllEvents() {
      this.$store.dispatch("fetchAllEvents", {
        name: this.name,
        type: this.status,
      });
    },
  },
  beforeMount() {
    const searchName = Cookies.get("search_name");
    this.name = searchName != null ? searchName : "";
    Cookies.remove("search_name");
    scroll(0, 0);
    this.fetchAllEvents();
  },
  beforeUnmount() {
    this.$store.commit("clearEventsList");
  },
};
</script>

