<template>
  <div class="container">
    <div class="info" v-if="!isMobile||(isMobile &&  !writing&&!currentMessage)">
      <div class="surch-wrapper">
        <a-space direction="vertical" class="surch-field" v-if="false">
          <a-input-search
              size="large"
              placeholder="Поиск письма"
              v-model:value="name"
              @search="searchEvents"
              enter-button
              shape="round"
          >
          </a-input-search>
        </a-space>
      </div>
      <div class="messages">
        <SeparateMessage @selectMessage="(currentMessage=$event,writing=false)" v-for="item in postMessages"
                         :key="item.id"
                         :message="item">
        </SeparateMessage>
      </div>
    </div>
    <div class="filling">
      <button class="back mobile" @click="(writing=false,currentMessage=null)">
        <arrow-left-outlined style="font-size: 20px;"/>
        Назад
      </button>
      <AnswerElement v-if="!writing&&currentMessage" :message="currentMessage">
      </AnswerElement>
      <LetterElement @closePostMessage="(writing=false,currentMessage=null)" v-if="writing&&!currentMessage">
      </LetterElement>
    </div>
  </div>
</template>

<script>
import SeparateMessage from "@/components/post/SeparateMessage.vue"
import LetterElement from "@/components/post/AnswerElement.vue"
import AnswerElement from "@/components/post/LetterElement.vue"
import {
  ArrowLeftOutlined
} from "@ant-design/icons-vue";

export default {
  name: "PostPage",
  components: {SeparateMessage, LetterElement, AnswerElement, ArrowLeftOutlined},
  computed: {
    postMessages() {
      return this.$store.getters.postMessages;
    }
  },
  data() {
    return {
      page: 1,
      limit: 20,
      writing: false,
      currentMessage: null,
      isMobile: false
    }
  },
  methods: {
    fetchPostMessages() {
      this.$store.dispatch('fetchPostMessages', {page: this.page, limit: this.limit});
    },
    writingMessage() {
      this.currentMessage = null;
      this.writing = true;
    }

  },

  beforeMount() {
    this.fetchPostMessages();
  },
  mounted() {
    document.addEventListener('writingMessage', this.writingMessage);
    if (window.innerWidth <= 700) {
      this.isMobile = true;
    }
  },
  beforeUnmount() {
    document.removeEventListener('writingMessage', this.writingMessage);
  },
  beforeRouteLeave() {
    document.removeEventListener('writingMessage', this.writingMessage);
  }
}
</script>

