}

.messages {
  max-width: 100%;
  width: 100%;
  height: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.filling {
  height: 100%;
  padding: 20px;
  max-width: 100%;
  width: 100%;
}

@media (max-width: 700px) {
  .info {
    max-width: 100%;
  }

  .filling {
    height: calc(100vh - 184px);
