<template>
  <a-modal
    v-if="canEdit"
    cancel-text="Отменить"
    v-model:visible="viewEditColumnName"
    title="Изменить название"
    @ok="changeAntTableColumns"
  >
    <a-input v-if="currentColumn" v-model:value="currentColumn.title"></a-input>
  </a-modal>
  <a-table
    class="table-filling"
    :data-source="antTableData"
    :columns="antTableColumns"
    :scroll="{ x: '50%', y: '50%' }"
    :pagination="false"
  >
    <template v-if="canEdit" #headerCell="{ column }">
      <div class="header-container">
        <p>{{ column?.title }}</p>
        <edit-outlined @click="pickCurrentColumn(column)"></edit-outlined>
        <delete-outlined @click="deleteColumn(column)"></delete-outlined>
      </div>
    </template>

    <template #bodyCell="{ column, record, text }">
      <div class="editable-cell">
        <delete-outlined
          style="cursor: pointer"
          @click="deleteRow(record)"
          v-if="canEdit && !Number(column?.dataIndex?.replace('x', ''))"
        ></delete-outlined>
        <div
          v-if="
            currentCell?.y_coord === record.y_coord &&
            currentCell?.x_coord === Number(column?.dataIndex?.replace('x', ''))
          "
          class="editable-cell-input-wrapper"
        >
          <a-input
            :id="currentCell?.id"
            v-model:value="currentCell.body"
            @pressEnter="(event) => event?.target?.blur()"
            @blur="changeAntTableData"
          ></a-input>
        </div>

        <div v-else class="editable-cell-text-wrapper">
          <edit-outlined
            class="editable-cell-icon"
            @click="pickCurrentCell(record, column)"
          />
          <p class="editable-cell-text">
            {{ text || "\n" }}
          </p>
        </div>
      </div>
    </template>
  </a-table>
</template>

<script>
import { computed, onBeforeUnmount, ref, watch } from "vue";
import { useStore } from "vuex";
import { appConfig } from "@/lib/appConfig";
import { EditOutlined, DeleteOutlined } from "@ant-design/icons-vue";

export default {
  props: ["hackathonId", "canEdit"],
  name: "HackathonTable",
  components: {
    EditOutlined,
    DeleteOutlined,
  },
  setup(props) {
    const store = useStore();

    const hackathon = computed(() => store.getters.hackathon);

    const currentColumn = ref(null);

    const currentCell = ref(null);

    const viewEditColumnName = ref(false);

    const socket = ref(null);
    if (props.hackathonId !== "new") {
      socket.value = new WebSocket(appConfig.socket);
      socket.value.onopen = () =>
        (socket.value.onmessage = (msg) => {
          try {
            const data = JSON.parse(msg.data);
            if (data?.action === "HACKATON_UPDATE_MESSAGE") {
              if (data?.msg?.hackatonId !== props.hackathonId) return;
              store.commit("updateTableCell", data?.msg?.cells[0]);
            }
          } catch (e) {
            console.log(e);
          }
        });
    }

    // const tableColumns = computed(() => {
    //   return hackathon?.value?.table ? hackathon.value.table?.filter(item => !item?.y_coord).toSorted((a, b) => a?.x_coord - b?.x_coord).map(item => {
    //     return {
    //       name: item?.body,
    //       prop: `x${item?.x_coord}`,
    //       id: item?.id,
    //       size: 300,
    //       columnTemplate: (createElement, column) => {
    //         const columnElements = [createElement('span', {}, column.name)];
    //         if (props.canEdit) {
    //           columnElements.push(createElement('button', {
    //             onclick: () => {
    //               currentColumn.value = column;
    //               viewEditColumnName.value = true;
    //             },
    //           }, '(Ред.)'))
    //         }
    //         return columnElements;
    //       },
    //     }
    //   }) : [];
    // });

    const antTableColumns = computed(() => {
      return hackathon?.value?.table
        ? hackathon.value.table
            ?.filter((item) => !item.delete && !item?.y_coord)
            .sort((a, b) => a?.x_coord - b?.x_coord)
            .map((item) => {
              return {
                title: item?.body,
                dataIndex: `x${item?.x_coord}`,
                id: item?.id,
                width: "200px",
              };
            })
        : [];
    });

    // const tableData = computed(() => {
    //   return hackathon?.value?.table ? [...new Map(hackathon.value.table.filter(item => item?.y_coord).toSorted((a, b) => a?.y_coord - b?.y_coord).map((item, index, array) => {
    //     const currentYCells = array.filter(cell => cell?.y_coord === item?.y_coord);
    //     const newObj = {
    //       y_coord: item?.y_coord,
    //     }
    //     for (let i = 0; i < currentYCells.length; i++) {
    //       newObj[`x${currentYCells[i]?.x_coord}`] = currentYCells[i]?.body;
    //       newObj[`x${currentYCells[i]?.x_coord}Id`] = currentYCells[i]?.id;
    //     }
    //     return newObj
    //   }).map(item => [item?.y_coord, item])).values()] : [];
    // });

    const antTableData = computed(() => {
      return hackathon?.value?.table
        ? [
            ...new Map(
              hackathon.value.table
                .filter((item) => !item.delete && item?.y_coord)
                .sort((a, b) => a?.y_coord - b?.y_coord)
                .map((item, index, array) => {
                  const currentYCells = array.filter(
                    (cell) => cell?.y_coord === item?.y_coord
                  );
                  const newObj = {
                    y_coord: item?.y_coord,
                  };
                  for (let i = 0; i < currentYCells.length; i++) {
                    newObj[`x${currentYCells[i]?.x_coord}`] =
                      currentYCells[i]?.body;
                    newObj[`x${currentYCells[i]?.x_coord}Id`] =
                      currentYCells[i]?.id;
                  }
                  return newObj;
                })
                .map((item) => [item?.y_coord, item])
            ).values(),
          ]
        : [];
    });

    // const changeTableData = (event) => {
    //
    //   const cell = {
    //     id: event?.detail?.model?.[`${event?.detail?.prop}Id`],
    //     x_coord: Number(event?.detail?.prop?.replace('x', '')),
    //     y_coord: event?.detail?.rowIndex + 1,
    //     body: event?.detail?.val
    //   }
    //
    //   if (socket.value) {
    //     store.dispatch('updateTableCell', {socket: socket.value, cell, hackatonId: props.hackathonId});
    //   } else {
    //     store.commit('updateTableCell', cell);
    //   }
    // }

    const changeAntTableData = () => {
      const cell = {
        id: currentCell.value.id,
        x_coord: currentCell.value.x_coord,
        y_coord: currentCell.value.y_coord,
        body: currentCell.value?.body,
      };
      if (socket.value) {
        store.dispatch("updateTableCell", {
          socket: socket.value,
          cell,
          hackatonId: props.hackathonId,
        });
      } else {
        store.commit("updateTableCell", cell);
      }
      currentCell.value = null;
    };

    // const changeTableColumns = () => {
    //   const cell = {
    //     id: currentColumn.value?.id,
    //     y_coord: 0,
    //     x_coord: Number(currentColumn?.value.prop.replace('x', '')),
    //     body: currentColumn.value?.name
    //   }
    //
    //   if (socket.value) {
    //     store.dispatch('updateTableCell', {socket: socket.value, cell, hackatonId: props.hackathonId});
    //   } else {
    //     store.commit('updateTableCell', cell);
    //   }
    //
    //   viewEditColumnName.value = false;
    // }

    const changeAntTableColumns = () => {
      const cell = {
        id: currentColumn.value?.id,
        y_coord: 0,
        x_coord: Number(currentColumn.value?.dataIndex?.replace("x", "")),
        body: currentColumn.value?.title,
      };

      if (socket.value) {
        store.dispatch("updateTableCell", {
          socket: socket.value,
          cell,
          hackatonId: props.hackathonId,
        });
      } else {
        store.commit("updateTableCell", cell);
      }

      viewEditColumnName.value = false;
    };

    const pickCurrentCell = (record, column) => {
      currentCell.value = {
        id: record?.[`${column?.dataIndex}Id`],
        columnId: column.id,
        body: record?.[column?.dataIndex],
        y_coord: record?.y_coord,
        x_coord: Number(column?.dataIndex?.replace("x", "")),
      };
      setTimeout(
        () => document.getElementById(currentCell.value?.id)?.focus(),
        100
      );
    };

    const pickCurrentColumn = (column) => {
      currentColumn.value = column;
      viewEditColumnName.value = true;
    };

    const deleteColumn = (column) => {
      store.commit("deleteColumn", column);
    };

    const deleteRow = (record) => {
      store.commit("deleteRow", record);
    };

    onBeforeUnmount(() => socket.value?.close());

    watch(
      () => viewEditColumnName.value,
      (val) => {
        if (!val) currentColumn.value = null;
      }
    );

    return {
      // tableColumns,
      // tableData,

      antTableData,
      antTableColumns,

      deleteColumn,
      deleteRow,

      currentColumn,
      viewEditColumnName,
      currentCell,

      // changeTableData,
      // changeTableColumns,
      changeAntTableData,
      changeAntTableColumns,
      pickCurrentCell,
      pickCurrentColumn,
    };
  },
};
</script>

<style scoped>
.table {
  max-height: 1000px;
  min-height: 500px;
}
.editable-cell {
  display: flex;
  align-items: center;
  gap: 15px;
}
.table-filling :deep(.ant-table-cell .anticon) {
  display: inline-block !important;
  position: relative;
  margin: 0;
  opacity: 0;
}
.table-filling :deep(.ant-table-cell) {
  background: #fafafa;
}
.table-filling :deep(.editable-cell) {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.table-filling :deep(.ant-table-cell:hover .editable-cell) {
  padding: 16px 16px;
  width: auto;
  position: absolute;
  left: 0;
  top: 0;
  background: #fafafa;
  z-index: 2;
  border: 1px solid #f0f0f0;
}
.table-filling :deep(.ant-table-cell .anticon-delete) {
  opacity: 1;
}
.header-container :deep(.ant-table-cell:hover .anticon) {
  display: inline-block !important;
  opacity: 1;
}
.table-filling :deep(.ant-table-cell:hover .anticon) {
  display: inline-block !important;
  opacity: 1;
}
.header-container p {
  margin: 0;
}
.editable-cell-text-wrapper {
  display: flex;
  align-items: center;
  gap: 20px;
}
.editable-cell-text {
  margin: 0;
  max-width: 100%;
  width: 100%;
  overflow: auto;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.editable-cell {
  position: relative;
}
.editable-cell-text-wrapper {
  padding-right: 24px;
}

.editable-cell-text-wrapper {
  min-height: 32px;
}

.editable-cell-icon,
.editable-cell-icon-check {
  position: absolute;
  right: 0;
  width: 20px;
  cursor: pointer;
}

.editable-cell-icon {
  margin-top: 4px;
  display: none;
}

.editable-cell-icon-check {
  line-height: 28px;
}

.editable-cell-icon:hover,
.editable-cell-icon-check:hover {
  color: #108ee9;
}

.editable-add-btn {
  margin-bottom: 8px;
}

.editable-cell:hover .editable-cell-icon {
  display: inline-block;
}

.table-filling {
  margin-bottom: 100px;
}
</style>
