import {appConfig} from "@/lib/appConfig";
import {defaultHttpResHandler} from "@/store/helpers/defaultHttpResHandler";
import {makePostReq, makeGetReq} from "@/store/helpers/request";


export const fetchHackathonsList = async (token) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/hackaton`, {token}))

export const fetchHackathonById = async (token, id) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/hackaton/${id}`, {token}))

export const fetchHackathonsResults = async (token) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/hackaton_pass`, {token}))

export const saveHackathonResult = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(`${appConfig.domain}/hackaton_pass`, data, {
    data: true,
    token
}))


export const saveHackathon = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(`${appConfig.domain}/hackaton`, data, {
    token,
    data: true
}))






