<template>
  <div class="container">
    <div class="users-info">
      <a-tag color="success">
        <template #icon>
          <check-circle-outlined/>
        </template>
        Проверенно: {{ verified }}
      </a-tag>
      <a-tag color="processing">
        <template #icon>
          <clock-circle-outlined/>
        </template>
        Осталось проверить: {{ unverified }}
      </a-tag>

    </div>
    <a-table :columns="columns" :data-source="usersList" class="tabel">
      <template #bodyCell="{ column, record }">
        <template v-if="column.key === 'avatarUrl'">
          <a-avatar :src="record.avatarUrl" size="large"></a-avatar>
        </template>
        <template v-if="column.key === 'sex'">
          {{ record.sex ? (record.sex === 'man' ? 'Мужской' : 'Женский') : 'Не определенно' }}
        </template>
        <template v-if="column.key === 'city'">
          {{ record.city ? record.city : 'Не определенно' }}
        </template>
        <template v-if="column.key === 'actions'">
          <a-button @click="goToAssessment(record)">
            <check-circle-outlined
                style="font-size: 13px;"/>
            {{ record.finalScore != null ? 'Посмотреть' : 'Оценить' }}
          </a-button>
        </template>
      </template>

      <template #customFilterDropdown="{ column, confirm }">
        <div style="padding: 8px" v-if="column.dataIndex === 'name'">
          <a-input placeholder="Найти по имени" v-model:value="name"
                   style="width: 188px; margin-bottom: 8px; display: block" @pressEnter="fetchUserList(confirm)"/>
          <a-button shape="round" type="primary" size="small" style="width: 90px; margin-right: 8px"
                    @click="fetchUserList(confirm)">
            Поиск
          </a-button>
          <a-button shape="round" size="small" style="width: 90px" @click="dropValue('name', confirm)">
            Сбросить
          </a-button>
        </div>
        <div style="padding: 8px;display: flex;flex-direction: column;gap: 9px; align-items: center" v-else>
          <a-select style="width: 150px" v-if="column.dataIndex === 'sex'" v-model:value="sex"
                    placeholder="Фильтр по полу">
            <a-select-option value="man">
              Мужской
            </a-select-option>
            <a-select-option value="girl">
              Женский
            </a-select-option>

          </a-select>

          <a-select v-else show-search style="width:100%" v-model:value="city" placeholder="Фильтр по городу">
            <a-select-option v-for="item in cities" :key="item.name + item.subject" :value="item.name">
              {{ item.name }}
            </a-select-option>

          </a-select>
          <div class="filter-buttons">
            <a-button shape="round" type="primary" size="small" style="width: 90px; margin-right: 8px"
                      @click="fetchUserList(confirm)">
              Поиск
            </a-button>
            <a-button shape="round" size="small" style="width: 90px" @click="dropValue(column.dataIndex, confirm)">
              Сбросить
            </a-button>
          </div>

        </div>
      </template>


      <template #customFilterIcon="{ filtered, column }">
        <search-outlined v-if="column.dataIndex === 'name'" :style="{ color: filtered ? '#108ee9' : undefined }"/>
        <filter-outlined v-else :style="{ color: filtered ? '#108ee9' : undefined }"/>
      </template>


    </a-table>

  </div>
</template>

<script>
import {FilterOutlined, SearchOutlined, ClockCircleOutlined, CheckCircleOutlined} from "@ant-design/icons-vue";
import Cookies from "js-cookie";

export default {
  name: "RaterCurrentEventPage",
  props: ['stageId', 'stageType', 'testId'],
  components: {
    SearchOutlined,
    FilterOutlined,
    ClockCircleOutlined, CheckCircleOutlined
  },
  data() {
    return {
      name: null,
      city: null,
      sex: null,
      usersList: null,
      verified: 0,
      unverified: 0,
      columns: [
        {
          title: '',
          dataIndex: 'avatarUrl',
          key: 'avatarUrl',
        },
        {
          title: 'Имя',
          dataIndex: 'name',
          key: 'name',
          customFilterDropdown: true,
        },
        {
          title: 'Город',
          dataIndex: 'city',
          customFilterDropdown: true,
          key: 'city',
        },
        {
          title: 'Пол',
          dataIndex: 'sex',
          customFilterDropdown: true,
          key: 'sex',
        },
        {
          title: 'Номер телефона',
          dataIndex: 'phone',
          key: 'phone',
        },
        {
          title: 'Оценка',
          dataIndex: 'score',
          key: 'score',
        },
        {
          title: 'Действия',
          key: 'actions'
        }
      ],

    }
  },
  methods: {
    dropValue(field, confirm) {
      this[field] = null;
      this.fetchUserList(confirm);
    },
    fetchUserList(confirm) {
      if (confirm) {
        confirm();
      }
      this.$store.dispatch('fetchUsersByStageId', {
        stageId: this.stageId,
        name: this.name,
        city: this.city,
        sex: this.sex
      }).then((data) => {
        this.usersList = data[0].testing.testingPassing;
        this.verified = this.usersList.filter(item => item.finalScore != null).length;
        this.unverified = this.usersList.filter(item => item.finalScore === null).length;
        for (let i = 0; i < this.usersList.length; i++) {
          this.usersList[i]['testingPassingId'] = this.usersList[i].id;
          for (let key in this.usersList[i].eventPassing.client) {
            this.usersList[i][key] = this.usersList[i].eventPassing.client[key];
          }
          this.usersList[i]['testId'] = this.testId;
          this.usersList[i]['score'] = this.usersList[i].finalScore || this.usersList[i].finalScore === 0 ? this.usersList[i].finalScore : 'Не оценено';
        }
        this.viewUsersList = true;
      });
    },
    goToAssessment(record) {
      this.$store.commit('setTestingPassingId', record.testingPassingId);
      Cookies.set('testingPassingId', record.testingPassingId);
      if (this.stageType === 'test') {
        this.$router.push({
          name: 'testingEstimation',
          params: {
            testingId: record.testId,
            eventId: record.eventPassing.eventId,
            role: 'rater',
            passingId: record.eventPassing.id,
            score: record.finalScore != null ? record.finalScore : 'null'
          }
        })
      } else if (this.stageType === 'form') {
        this.$router.push({
          name: 'form',
          params: {
            formId: record.testId,
            eventId: record.eventPassing.eventId,
            role: 'rater',
            passingId: record.eventPassing.id
          }
        })
      } else if (this.stageType === 'exercise') {
        this.$router.push({
          name: 'exercise',
          params: {
            exerciseId: record.testId,
            eventId: record.eventPassing.eventId,
            role: 'rater',
            passingId: record.eventPassing.id
          }
        })
      }
    },

  },
  beforeMount() {
    this.fetchUserList();
  }
}
</script>

<style scoped>
.users-info {
  display: flex;
  gap: 10px;
  margin: 10px;
}
</style>