<script>
import EventQuestion from "@/components/events/testing/EventQuestion";
import {mapGetters} from "vuex";
// import {ArrowLeftOutlined} from "@ant-design/icons-vue";
import {errorHandler} from "@/lib/errorHandler";
import Cookies from "js-cookie";
import {convertTimeInMinuteAndSeconds} from "@/lib/convertTime";


export default {
  components: {
    EventQuestion,
    //  ArrowLeftOutlined
  },
  name: "TestingPage",
  props: ['testingId', 'eventId', 'role', 'passingId', 'score'],
  beforeMount() {
    scroll(0, 0);
    if (this.role === 'rater') {
      this.viewPreview = false;
    }
    let passingTestingId = Cookies.get('testingPassingId');
    if (this.role === 'rater') {
      if (passingTestingId && passingTestingId !== 'null') {
        this.$store.commit('setTestingPassingId', passingTestingId);
      } else {
        this.$router.go(-1);
      }
    }
    this.$store.dispatch('fetchEventsTesting', {
      testingId: this.testingId,
      eventId: this.eventId,
      role: this.role,
      passingId: this.passingId,
      isPassTesting: true
    }).then((access) => {
      if (!access) return;
      this.allTime = this.currentStage.limitSeconds;
    });

  },
  data() {
    return {
      dateOptions: {
        month: 'long', day: 'numeric'
      },
      currentQuestion: 0,
      errText: 'Незаполненные данные :\n',

      viewPreview: true,
      timer: null,
      allTime: null,
      convertTimeInMinuteAndSeconds,
      disabledButton: false
    }
  },
  mounted() {
    if (this.role !== 'rater')
      window.onblur = () => {
        if (!this.viewPreview) {
          this.finishTesting();
        }
      }
  },
  computed: {
    ...mapGetters(['currentStage', 'passingTest', 'currentBlank', 'passingBlank'])
  },
  methods: {
    finishTesting() {
      this.$store.commit('finishTesting');
      this.savePassing(true);
    },
    startTimer() {
      this.timer = setInterval(() => {
        if (this.currentStage.limitSeconds === 0) {
          clearInterval(this.timer);
          this.finishTesting();
          return;
        }
        this.$store.commit('decrementTimer');
      }, 1000)
    },
    incrementQuestion() {
      scroll(0, 0);
      if (this.viewPreview) {
        this.viewPreview = false;
        this.startTimer();
        return;
      }
      this.currentQuestion++
    },
    checkPassing() {
      let approve = true;
      for (let i = 0; i < this.passingTest.testingPassing.answers.length; i++) {
        let question = this.passingTest.testingPassing.answers[i];
        let type = this.currentStage.questions[this.currentStage.questions.map(item => item.id).indexOf(question.questionId)].type;
        if (((type === 'single' || type === 'select') && !question.answers) || (type === 'multi' && !question.answers.length) || ((type === 'text' || type === 'open') && !question.openAnswer.trim())) {
          this.errText += `Вопрос ${i + 1}: нет ответа\n`
          approve = false;
        }
        if (type === 'conformity' && question.matchingAnswers.length !== this.currentStage.questions[this.currentStage.questions.map(item => item.id).indexOf(question.questionId)].answers.length) {
          this.errText += `Вопрос ${i + 1}: сопоставление не заполнено\n`
          approve = false;
        }
      }
      return approve;

    },
    savePassing(finish) {
      if (!this.checkPassing()) {
        errorHandler({
          name: 'passing', status: 300, meta: {
            body: this.errText
          }
        });
        this.errText = 'Незаполненные данные :\n'
        return;
      }
      this.disabledButton = true;
      this.$store.dispatch('savePassingTesting', {role: this.role, finish, eventId: this.eventId})
    },
  },
  beforeRouteLeave() {
    clearInterval(this.timer);
    window.onblur = null;
    this.$store.commit('clearPassing');
    Cookies.remove('testingPassingId');
  }
}
</script>

